<template>
  <div class="user">
      <el-row class="search">
        <el-col :span="24">
            <el-form :inline="true" :model="searchForm" ref="searchForm" label-width="100px">
                <el-form-item label="用户名" prop="user">
                    <el-input v-model="searchForm.user" placeholder="请输入用户名"></el-input>
                </el-form-item>
                <el-form-item label="姓名" prop="name">
                    <el-input v-model="searchForm.name" placeholder="请输入姓名"></el-input>
                </el-form-item>
                <el-form-item label="手机号码" prop="phoneNumber">
                    <el-input v-model="searchForm.phoneNumber" placeholder="请输入手机号码"></el-input>
                </el-form-item>
                <el-form-item label="状态" prop="status">
                    <el-select v-model="searchForm.status" placeholder="状态">
                        <el-option label="全部" value="all"></el-option>
                        <el-option label="已启用" value="1"></el-option>
                        <el-option label="已禁用" value="2"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="帐号类型" prop="userType">
                    <el-select v-model="searchForm.userType" placeholder="帐号类型">
                        <el-option label="全部" value="all"></el-option>
                        <el-option label="sso" value="sso"></el-option>
                        <el-option label="wmpass" value="wmpass"></el-option>
                    </el-select>
                </el-form-item>
            </el-form>
            <div class="search-btns">
                <el-button type="primary" @click="search">查询</el-button>
                <el-button @click="reset">重置</el-button>
            </div>
        </el-col>
      </el-row>
      <div class="user-manage">
          <el-button
                  type="primary"
                  @click="createUser"
                  icon="el-icon-plus">
              绑定用户
          </el-button>
          <el-table
                  :data="tableData"
                  style="width: 100%">
              <el-table-column
                      prop="user_id"
                      width="80"
                      label="NO.">
              </el-table-column>
              <el-table-column
                      prop="user_name"
                      width="90"
                      label="用户名">
              </el-table-column>
              <el-table-column
                      prop="type"
                      width="90"
                      label="帐号类型">
              </el-table-column>
              <el-table-column
                      width="80"
                      prop="real_name"
                      label="姓名">
              </el-table-column>
              <el-table-column
                      prop="phone"
                      width="120"
                      label="手机号">
              </el-table-column>
              <el-table-column
                      prop="email"
                      label="邮箱">
              </el-table-column>
              <el-table-column
                      width="80"
                      label="状态">
                  <template slot-scope="scope">
                      {{scope.row.enabled == '1' ? '已启用' : '已禁用'}}
                  </template>
              </el-table-column>
              <el-table-column
                  prop="time"
                  label="创建时间">
              </el-table-column>
              <el-table-column
                      label="操作">
                  <template slot-scope="scope">
                      <el-button
                              @click.native.prevent="linkToEdit(scope.row.user_id)"
                              type="text"
                              size="small">
                          关联角色
                      </el-button>
                      <el-button
                              v-if="isWms"
                              @click.native.prevent="editPermission(scope.row.user_id)"
                              type="text"
                              size="small">
                          数据权限
                      </el-button>
                      <el-button
                              @click.native.prevent="changeStatus(scope.row.user_id, scope.row.enabled)"
                              type="text"
                              size="small">
                          {{scope.row.enabled == '1' ? '禁用' : '启用'}}
                      </el-button>
                  </template>
              </el-table-column>
          </el-table>
          <el-pagination
                  @current-change="changePage"
                  :current-page.sync="page"
                  :page-size="20"
                  layout="total, prev, pager, next"
                  :total="totalCount"
                  prev-text="上一页"
                  next-text="下一页">
          </el-pagination>
      </div>
  </div>
</template>

<script>
import { getUserList, updateUser } from '../api/api'

export default {
  data () {
    return {
      // 列表页table所有数据
      tableData: [],
      // 当前翻页数
      page: 1,
      // 总页数
      totalPage: 1,
      // table总条数
      totalCount: 1,
      // 搜索表单的数据
      searchForm: {
        user: '',
        name: '',
        phoneNumber: '',
        status: 'all',
        userType: 'all'
      },
      // 彩云系统还是沧海系统
      isWms: window.location.href.indexOf('wms') >= 0
    }
  },
  methods: {
    // 修改用户状态
    changeStatus (id, status) {
      // 判断当前是启用还是禁用
      const conformText = status == '1' ? '禁用' : '启用'
      const enableStatus = status == '1' ? 2 : 1
      // 弹窗提示
      this.$confirm('确定' + conformText + '用户?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        // 修改用户状态
        const postData = {
          user_id: id,
          enable: enableStatus
        }
        updateUser(postData).then(response => {
          const res = response.data
          if (res.error_no === 0) {
            this.$message.success(conformText + '成功!')
            this.resetParams()
            this.getTableData(this.page)
          } else {
            this.$message.error(res.error_msg)
          }
        })
        .catch(error => {
          this.$message.error('服务器傲娇了，请刷新重试')
        })
      })
    },
    // 获取table数据
    getTableData (page) {
      const getParams = {
        page_num: page,
        user_name: this.searchForm.user, // 用户名
        real_name: this.searchForm.name, // 姓名
        phone: this.searchForm.phoneNumber, // 手机号码
        enable: this.searchForm.status === 'all' ? '' : parseInt(this.searchForm.status), // 是否禁用
        type:  this.searchForm.userType === 'all' ? '' : this.searchForm.userType// 用户类型
      }
      getUserList(getParams).then(response => {
        const res = response.data
        if (res.error_no === 0) {
          res.result.users = res.result.users.map(item => {
            item.time = this.handleTimestamp(item.create_time)
            return item
          })
          this.tableData = res.result.users
          this.totalCount = parseInt(res.result.total_count, 10)
          this.totalPage = Math.ceil(this.totalCount / 20)
        } else {
          this.$message.error(res.error_msg)
        }
      })
      .catch(error => {
        this.$message.error('服务器傲娇了，请刷新重试')
      })
    },
    handleTimestamp (timestamp) {
      timestamp = parseInt(timestamp) * 1000
      let date = new Date(timestamp)
      let result = ''
      result += date.getFullYear() + '-'
      result += ((date.getMonth() + 1) > 9 ? date.getMonth() + 1 : '0' + (date.getMonth() + 1)) + '-'
      result += (date.getDate() > 9 ? date.getDate() : '0' + date.getDate()) + ' '
      result += (date.getHours() > 9 ? date.getHours() : '0' + date.getHours()) + ':'
      result += (date.getMinutes() > 9 ? date.getMinutes() : '0' + date.getMinutes()) + ':'
      result += date.getSeconds() > 9 ? date.getSeconds() : '0' + date.getSeconds()
      return result
    },
    // 翻页事件
    changePage (val) {
      this.getTableData(val)
    },
    // 跳转新增用户
    createUser () {
      this.$router.push({ path: '/userCreate'})
    },
    linkToEdit (id) {
      this.$router.push({path: '/userEdit', query: {user_id: id}})
    },
    // 编辑数据权限
    editPermission (id) {
      this.$router.push({
        path: '/dataPermission',
        query: {user_id: id}})
    },
    // 搜索
    search () {
      this.page = 1
      this.getTableData(this.page)
    },
    resetParams () {
      this.$refs['searchForm'].resetFields()
    },
    // 重置
    reset () {
      this.resetParams()
      this.getTableData(1)
    }
  },
  mounted () {
    // 获取初始数据
    this.getTableData(this.page)
  }
}
</script>

<style>
    .search {
        margin-bottom: 20px;
    }
    .search-btns {
        margin-left: 100px;
    }
    .list-wrap .user-management {
        background: none;
        padding: 0;
    }
    .search, .user-manage {
        background: #fff;
        padding: 20px;
    }
</style>


