<template>
  <section>
      <el-menu 
      text-color="#333"
      :default-active="activeIndex" 
      active-text-color="#39F"
      class="el-menu-demo" mode="horizontal">
      <el-menu-item index="1"><router-link :to="{ path: '/createBasicInfo',query:{create: vendorId, name: this.$route.query.name}}" >供货商基础信息</router-link></el-menu-item>
      <el-menu-item index="2"><router-link :to="{ path: '/createSupplyAccount',query:{create: vendorId, name: this.$route.query.name}}" v-if="!!vendorId">供货商后台帐号</router-link></el-menu-item>
      <el-menu-item index="3"><router-link :to="{ path: '/createPond',query:{create: vendorId, name: this.$route.query.name}}" v-if="!!vendorId">商品池</router-link></el-menu-item>
    </el-menu>

  <el-form ref="tab" label-width="119px" :rules="rules" :label-position="labelPosition" :model="tab" class="demo-ruleForm">
<span class="supplyName">{{this.$route.query.name}}</span>

  <el-form-item label="供货商登录名"  class="tab2" prop="name">
    <el-input v-model="tab.name" class="i380" placeholder="6～20位字母或字母数字组合" :disabled="writable"></el-input>
  </el-form-item>

  <el-form-item label="绑定手机号"  class="tab2" prop="tel">
    <el-input v-model="tab.tel" class="i380" placeholder="请输入绑定手机号" :disabled="writable"></el-input>
  </el-form-item>

  <el-form-item>
    <el-button type="primary" @click="submitForm('tab')" :disabled="btn.updatevendoradmin">保存</el-button>
  </el-form-item>

  </el-form>
  
  <el-dialog
    title="提示"
    :visible.sync="dialogVisible"
    width="30%">
    <span>创建成功，已将登录网址、登录名、初始密码发送短信到供货商的该绑定手机号</span>
    <span slot="footer" class="dialog-footer">
      <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
    </span>
  </el-dialog>

  </section>

</template>
<script>
import qs from 'qs'
import { updateVendorAdmin, getCityWarehouse } from '../../api/api'
import util from '../../common/js/util'
export default {
  data () {
    return {
      btn: {
        updatevendoradmin: !util.jdugeShow('vendor-main-updatevendoradmin')
      },
      activeName: '1',
      activeIndex: '2',
      vendor_id: '',
      labelPosition: 'left',
      dialogVisible: false,
      writable: false,
      vendorId: this.$route.query.create,
      tab: {
        name: '',
        tel: '',
        supplyName: this.$route.query.name
      },
      rules: {
        name: [
          { required: true, validator: 'regexp', pattern: /^[a-zA-Z0-9]{6,20}$/, message: '请输入6～20位字母或字母数字组合', trigger: 'change,blur' }
        ],
        tel: [
          { required: true, validator: 'regexp', pattern: /^1\d{10}$/, message: '请输入正确的手机号', trigger: 'change,blur' }
        ]
      }
    }
  },
  created () {
    this.getVendorById()
  },
  methods: {
    getVendorById () {
      let params = {
        vendor_id: this.vendorId
      }
      getCityWarehouse(params).then((res) => {
        let result = res.data.result
        if (result.vendor_login_account_id) {
          this.tab.name = result.vendor_login_account_name
          this.tab.tel = result.vendor_login_account_mobile
          this.writable = true
          this.btn.updatevendoradmin = true
        }
      })
    },
    handleClick (tab, event) {
        // console.log(tab, event)；
    },
    updateVendorAdmin () {
      let params = {
        vendor_id: this.vendorId,
        vendor_login_account: this.tab.name,
        vendor_login_mobile: this.tab.tel
      }

      updateVendorAdmin(qs.stringify(params)).then((res) => {
        if (res.data.error_no === 0) {
          this.dialogVisible = true
          this.writable = true
        } else {
          this.$message({message: res.data.error_msg, type: 'error'})
        }
      })
    },

    submitForm (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.updateVendorAdmin()
        } else {
          this.$message({message: '请填写正确信息', type: 'error'})
          return false
        }
      })
    }
  }
}
</script>

<style scoped lang="scss">
section{
    background-color: #fff;
    padding-left: 20px;
    .el-menu-item{
      padding: 0;
      margin: 0 20px;
      &:nth-child(1){
        margin-left: 0;
      }
      a{
        font-size: 14px;
        text-decoration: none;
        font-weight: 500;
        width: 100%;
        height: 100%;
        display: block;
      }
    }
      .i380{
      width: 380px;
      font-size: 14px;
    }

    .supplyName{
      color: #2D2F33;
      font-size: 18px;
      padding-left: 10px;
      margin-top: 25px;
      display: block;
      margin-bottom: 56px;
    }
    .el-form{
      overflow: hidden;
    }

}
</style>



