import Util from '@/common/js/util'
import Purchase from './views/notice/purchase'
import Supplier from './views/notice/supplier'
import Home from './views/Home.vue'
// 商品管理
import ProductsManage from './views/buyBasic/productsManage'
import CreateProduct from './views/buyBasic/createProduct'
// 维护配货信息
import UpdateSupplyInfo from './views/buyBasic/updateSupplyInfo/updateSupplyInfo'
// 供货商管理
import SupplyManage from './views/buyBasic/supplyManage'
// 报价单管理
import QuotationList from './views/buyBasic/quotationManager/quotationList/quotationList.vue'
import QuotationCreate from './views/buyBasic/quotationManager/quotationCreate/quotationCreate.vue'
// 商品生效报价单
import ProductsWorkPrice from './views/buyBasic/productsWorkPrice'
// 商品条码映射
import SkuUpcMapList from './views/buyBasic/skuUpcMap/skuUpcMapList/skuUpcMapList.vue'

// 采购进货
// 采购单管理
import PurchaseList from './views/purchaseManager/purchase/purchaseList/purchaseList.vue'
import CreatePurchase from './views/purchaseManager/purchase/purchaseCreate/purchaseCreate.vue'
import PurchaseInfo from './views/purchaseManager/purchase/purchaseInfo/purchaseInfo.vue'
// 供货商确认页面
import ConfirmPage from './views/purchaseManager/purchase/confirmPage/confirmPage.vue'
// 到货单管理
import ArrivalNoteList from './views/purchaseManager/arrivalNote/arrivalNoteList/arrivalNoteList.vue'
import ArrivalNoteInfo from './views/purchaseManager/arrivalNote/arrivalNoteInfo/arrivalNoteInfo.vue'
// 供货商管理
import CreateBasicInfo from './views/createSupply/createBasicInfo'
import CreateSupplyAccount from './views/createSupply/createSupplyAccount'
import CreatePond from './views/createSupply/createPond'
// 日志
import Log from './views/log/log'
// 导出任务列表
import ExportList from './components/exportList/exportList.vue'

// 权限管理模块
// 权限管理tab列表页
import PMSListWrap from 'nr-pms/src/views/listWrap'
// 角色新建&编辑页
import RoleEdit from 'nr-pms/src/views/roleEdit'
// 用户新建&编辑页
import UserEdit from 'nr-pms/src/views/userEdit'
// 开发者工具
import DeveloperTools from 'nr-pms/src/views/developerTools/tabs'
import DataPermission from 'nr-pms/src/views/dataPermission'
import CreatePermission from 'nr-pms/src/views/developerTools/createPermission'

let routes = [{
  path: '/',
  component: Home,
  name: '首页',
  iconCls: 'el-icon-clouds-home',
  meta: { requiresAuth: false },
  children: [{
    path: '/',
    name: '公告',
    component: Util.isSupplier() ? Supplier : Purchase
  },
  {
    path: '/exportlist',
    name: '导出任务列表',
    hidden: Util.jdugeMenu('exportcenter'),
    component: ExportList
  }]
},
{
  path: '/',
  component: Home,
  name: '采购基础信息',
  menu_id: '4506366140413432',
  iconCls: 'el-icon-clouds-basemsg', // 图标class
  hidden: Util.jdugeMenu('getskulist') && Util.jdugeMenu('getvendorlist') && Util.jdugeMenu('quotationlist') && Util.jdugeMenu('productsPrice'),
  children: [{
    path: '/getskulist',
    menu_name: 'getskulist',
    hidden: Util.jdugeMenu('getskulist'),
    component: {
      template: '<router-view></router-view>'
    },
    name: '商品管理',
    children: [{
      path: '',
      component: ProductsManage,
      name: ''
    },
    {
      path: '/createproduct',
      component: CreateProduct,
      name: '新建商品',
      hidden: true
    },
    {
      path: '/updateSupplyInfo',
      component: UpdateSupplyInfo,
      name: '维护配货信息',
      hidden: true
    }
    ]
  },
  {
    path: '/getvendorlist',
    menu_name: 'getvendorlist',
    hidden: Util.jdugeMenu('getvendorlist'),
    component: {
      template: '<router-view></router-view>'
    },
    name: '供货商管理',
    children: [{
      path: '',
      component: SupplyManage,
      name: ''
    },
    {
      path: '/CreateBasicInfo',
      component: CreateBasicInfo,
      name: '新建（编辑）供货商',
      hidden: true
    },
    {
      path: '/createSupplyAccount',
      component: CreateSupplyAccount,
      name: '供货商后台账号',
      hidden: true
    },
    {
      path: '/createPond',
      component: CreatePond,
      name: '商品池',
      hidden: true
    },
    {
      path: '/log',
      component: Log,
      name: '日志',
      hidden: true
    }
    ]
  },
  {
    path: '/quotationlist',
    component: QuotationList,
    name: '报价单管理',
    menu_name: 'quotationlist',
    hidden: Util.jdugeMenu('quotationlist')
  },
  {
    path: '/createquotation',
    component: QuotationCreate,
    name: '新建报价单',
    hidden: true
  },
  {
    path: '/productsprice',
    component: ProductsWorkPrice,
    name: '商品生效报价',
    menu_name: 'productsPrice',
    hidden: Util.jdugeMenu('productsPrice')
  },
  {
    path: '/skuupcmap',
    menu_name: 'skuupcmap',
    hidden: Util.jdugeMenu('skuupcmap'),
    component: SkuUpcMapList,
    name: '商品条形码映射'
  }
  ]
},
{
  path: '/',
  component: Home,
  name: '采购进货',
  iconCls: 'el-icon-clouds-purchase', // 图标class
  menu_id: '4506366140529789',
  hidden: Util.jdugeMenu('purchaselist') && Util.jdugeMenu('arrivalnotelist'),
  children: [{
    path: '/purchaselist',
    component: {
      template: '<router-view></router-view>'
    },
    name: '采购单管理',
    menu_name: 'purchaselist',
    hidden: Util.jdugeMenu('purchaselist'),
    children: [{
      path: '',
      component: PurchaseList,
      name: ''
    },
    {
      path: '/createpurchase',
      component: CreatePurchase,
      name: '新建采购单',
      hidden: true
    },
    {
      path: '/purchaseinfo',
      component: PurchaseInfo,
      name: '查看采购单',
      hidden: true
    },
    {
      path: '/confirmpage',
      component: ConfirmPage,
      name: '供货商确认',
      menu_name: 'confirmpage',
      hidden: Util.jdugeMenu('confirmpage')
    }
    ]
  },
  {
    path: '/arrivalnotelist',
    component: {
      template: '<router-view></router-view>'
    },
    name: '到货单管理',
    menu_name: 'arrivalnotelist',
    hidden: Util.jdugeMenu('arrivalnotelist'),
    children: [{
      path: '/',
      component: ArrivalNoteList,
      name: ''
    },
    {
      path: '/exportlist',
      component: ExportList,
      name: '导出任务列表'
    },
    {
      path: '/arrivalnoteinfo',
      component: ArrivalNoteInfo,
      name: '到货单查看',
      menu_name: 'arrivalnoteinfo',
      hidden: Util.jdugeMenu('arrivalnoteinfo')
    }
    ]
  }
  ]
},
{
  path: '/',
  component: Home,
  name: '系统设置',
  menu_id: '4506366140640887',
  iconCls: 'el-icon-clouds-setting', // 图标class
  hidden: Util.jdugeMenu('set'),
  children: [{
    path: '/privilegeManage',
    component: {
      template: '<router-view></router-view>'
    },
    name: '权限管理',
    menu_name: 'set',
    hidden: Util.jdugeMenu('set'),
    children: [{
      path: '',
      component: PMSListWrap,
      hidden: true
    },
    {
      path: '/userCreate',
      component: UserEdit,
      hidden: true,
      name: '新建用户'
    },
    {
      path: '/userEdit',
      component: UserEdit,
      hidden: true,
      name: '编辑用户'
    },
    {
      path: '/roleCreate',
      component: RoleEdit,
      hidden: true,
      name: '新建角色'
    },
    {
      path: '/roleEdit',
      component: RoleEdit,
      hidden: true,
      name: '编辑角色'
    },
    {
      path: '/dataPermission',
      component: DataPermission,
      hidden: true,
      name: '数据权限'
    }
    ]
  }, {
    path: '/developerTools',
    component: {
      template: '<router-view></router-view>'
    },
    name: '开发者工具',
    menu_name: 'developerTools',
    hidden: Util.jdugeMenu('auth-dev-tools'),
    children: [{
      path: '/',
      component: DeveloperTools,
      hidden: true
    }, {
      path: '/editPermission',
      component: CreatePermission,
      hidden: true,
      name: '编辑权限'
    }, {
      path: '/createPermission',
      component: CreatePermission,
      hidden: true,
      name: '新建权限'
    }]
  }]
},
{
  path: '*',
  hidden: true,
  redirect: { path: '/' }
}
]

export default routes



