// 接口
import {
  createQuotation,
  getvalidvendorsku,
  searchVendor,
  getquotationaudit,
  getquotationbyid,
  updatequotationfirstaudit,
  updatequotationsecondaudit,
  updatequotationaudit,
  updatequotation
} from '../../../../api/api'
import util from '../../../../common/js/util'
// todo: 合并公用文件，from index
import upc_unit_text from '../../../../common/js/commonData'
export default {
  data () {
    return {
      btn: {
        updatequotation: util.jdugeShow('vendor-quotation-updatequotation'),
        updatequotationfirstaud: util.jdugeShow('vendor-quotation-updatequotationfirstaud'), // 一审通过
        updatequotationsecondau: util.jdugeShow('vendor-quotation-updatequotationsecondau')  // 二审通过
      },
      quotation_id: this.$route.query.quotation_id,
      quotation_status: 10,
      // 整个页面需要提交的内容都录入表单内，包括列表部分
      formData: {
        // 存一个原供应商名称，在不变更供应商时，用来回退数据
        old_vendor_name: '',
        vendor_name: '',
        vendor_id: '',
        quotation_start_time: '',
        quotation_end_time: '',
        skuText: '',
        upcText: '',
        quotationTabel: []
      },
      rules: {
        vendor_name: [
          { required: true, message: '请输入供应商名称', trigger: 'blur' }
        ],
        quotation_start_time: [
          { required: true, message: '请输入开始时间', trigger: 'blur' }
        ],
        quotation_end_time: [
          { required: true, message: '请输入结束时间', trigger: 'blur' }
        ]
      },
      amountErr: [],
      priceErr: [],
      amountErrType: {},
      priceErrType: {},
      // 驳回原因
      rejectText: '',
      rejectErr: false,
      // 显示状态控制
      popShow: false,
      dialogVisible: false,
      count: 0,
      upc_unit_text: upc_unit_text,
      mousePosX: '',
      mousePosY: '',
      showText: '',
      showPop: false
    }
  },
  created () {
    if (this.quotation_id) {
      this.getQuotationInfo()
    }
  },
  methods: {
    unitPriceTaxLabel (creatElement) {
      return creatElement('span', {}, [
        creatElement('span', {
          style: {
            color: 'red'
          }
        }, '*'), creatElement('span', '默认含税单价')
      ])
    },
    specUnitLabel (creatElement) {
      return creatElement('span', {}, [
        creatElement('span', {
          style: {
            color: 'red'
          }
        }, '*'), creatElement('span', '最小起订包装数')
      ])
    },
    specPriceLabel (creatElement) {
      return creatElement('span', {
        class: 'real-price-label',
        style: {
          position: 'relative'
        }
      }, [
        creatElement('span', '规格报价'),
        creatElement('i', {
          class: 'el-icon-question',
          on: {
            mouseover: event => {
              this.showPop = true
              let e = event || window.event
              this.mousePosX = e.clientX + 5
              this.mousePosY = e.clientY + 5
              this.showText = '规格报价=默认含税报价*规格'
            },
            mouseout: () => {
              this.showPop = false
            }
          }
        })
      ])
    },
    querySupplier (queryString, callback) {
      let params = {}
      params.vendor_name = queryString
      searchVendor(params).then((res) => {
        let venderArr = []
        let result = (res.data.result && res.data.result) || []
        for (let i = 0, len = result.length; i < len; i++) {
          venderArr[i] = {}
          venderArr[i].value = result[i].vendor_name
          venderArr[i].vendor_id = result[i].vendor_id
        }
        callback(venderArr)
      })
    },
    selectSupplier (item) {
      if (this.formData.quotationTabel.length !== 0) {
        this.$confirm('更换供应商后，报价单将被清空，确认此操作吗?', '', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.formData.vendor_id = item.vendor_id
          this.formData.quotationTabel = []
        }).catch(() => {
          this.formData.vendor_name = this.formData.old_vendor_name
        })
      } else {
        this.formData.vendor_id = item.vendor_id
        this.formData.old_vendor_name = this.formData.vendor_name
      }
    },
    jumpToSupplier () {
      this.$router.push({ path: '/CreateBasicInfo' })
    },
    addQuotation () {
      let params = {}
      if (this.valiAddQuotation()) {
        if (this.formData.upcText) {
          params.upc_id = this.formData.upcText
        } else {
          params.sku_id = this.formData.skuText
        }
        params.vendor_id = this.formData.vendor_id
        getvalidvendorsku(params).then((res) => {
          if (res.data.error_no === 0) {
            let result = res.data.result
            result.quotation_sku_price = ''
            result.quotation_sku_min_order = '1'
            result.total_quotation = ''
            this.formData.quotationTabel.push(res.data.result)
            this.formData.skuText = ''
            this.formData.upcText = ''
            this.count ++
          } else {
            this.$message({ message: res.data.error_msg || '加入报价有误，请重试', type: 'warning', center: true })
          }
        })
      }
    },
    valiAddQuotation () {
      // 加入对sku_id和upc文本框输入的非空|非并校验
      if (!this.formData.vendor_id) {
        this.$message({ message: '请填写供应商名称', type: 'warning', center: true })
        return
      }
      if (!this.formData.upcText && !this.formData.skuText) {
        this.$message({ message: '请填入商品编码或者商品条形码', type: 'warning', center: true })
        return
      }
      if (this.formData.upcText && this.formData.skuText) {
        this.$message({ message: '只允许输入其中一项', type: 'warning', center: true })
        return
      }
      if (this.formData.quotationTabel.length > 0) {
        let quotationSkuArr = []
        let quotationUpcArr = []
        this.formData.quotationTabel.forEach((val, index, arr) => {
          quotationSkuArr.push(parseInt(arr[index].sku_id))
          quotationUpcArr.push(parseInt(arr[index].display_upc_id))
        })
        if (quotationSkuArr.indexOf(parseInt(this.formData.skuText)) !== -1 || quotationUpcArr.indexOf(parseInt(this.formData.upcText)) !== -1) {
          this.$message.warning('该商品已存在在报价单中')
          return
        }
      }
      return true
    },
    computedQuotation (row) {
      if (row.quotation_sku_price) {
        row.total_quotation = util.Mul(row.quotation_sku_price, row.upc_unit_num)
      }
    },
    deleteRow (index, rows) {
      this.$confirm('此操作将移除该商品, 是否继续?', '', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        rows.splice(index, 1)
        this.count --
      }).catch(() => {
      })
    },
    getTimeFormat (t) {
      let time = new Date(t).getTime()
      return time
    },
    saveQuotation () {
      let params = this.getSaveParams()
      if (this.validate()) {
        if (!this.quotation_id) {
          createQuotation(params).then((res) => {
            if (res.data.error_no === 0) {
              this.$message.success('保存成功')
              this.$router.push({ path: '/quotationlist' })
            } else {
              this.$message.error(res.data.error_msg || '保存失败，请稍后重试')
            }
          })
        } else {
          updatequotation(params).then((res) => {
            if (res.data.error_no === 0) {
              this.$message.success('保存成功')
              this.$router.push({ path: '/quotationlist' })
            } else {
              this.$message.error(res.data.error_msg || '保存失败，请稍后重试')
            }
          })
        }
      }
    },
    validate () {
      let errNum = 0
      let regNum = new RegExp(/(^[1-9]([0-9]*)$)/)
      let regMoney = new RegExp(/(^[1-9]([0-9]+)?(\.[0-9]{1,2})?$)|(^(0){1}$)|(^[0-9]\.[0-9]([0-9])?$)/)
      if (!this.formData.vendor_id) {
        this.$message.error('请填写供应商名称')
      }
      this.formData.quotationTabel.forEach((value, index, arr) => {
        if (!value.quotation_sku_price) {
          this.priceErr.push(index)
          this.priceErrType[index] = 'none'
          errNum++
        } else {
          if (!regMoney.test(value.quotation_sku_price)) {
            this.priceErr.push(index)
            this.priceErrType[index] = 'notMoney'
            errNum++
          }
        }
        if (!value.quotation_sku_min_order) {
          this.amountErr.push(index)
          this.amountErrType[index] = 'none'
          errNum++
        } else {
          if (!regNum.test(value.quotation_sku_min_order)) {
            this.amountErr.push(index)
            this.amountErrType[index] = 'notInt'
            errNum++
          }
        }
      })
      if (errNum > 0) {
        return
      }
      return true
    },
    getSaveParams () {
      let skus = []
      this.formData.quotationTabel.forEach(function (value, index, array) {
        skus[index] = {
          'quotation_sku_id': array[index].sku_id,
          // quotation_sku_price单位为分
          'quotation_sku_price': util.Mul(array[index].quotation_sku_price, 100),
          'quotation_sku_min_order': parseInt(array[index].quotation_sku_min_order)
        }
      })
      let params = {
        vendor_id: this.formData.vendor_id,
        quotation_start_time: this.formatDate(new Date(this.formData.quotation_start_time)),
        quotation_end_time: this.formatDate(new Date(this.formData.quotation_end_time)),
        skus: JSON.stringify(skus)
      }
      if (this.quotation_id) {
        params.quotation_id = this.quotation_id
      }
      return params
    },
    doReject () {
      if (!this.rejectText) {
        this.rejectErr = true
      } else {
        let params = {}
        params.quotation_id = this.quotation_id
        params.workflow_pass = 2
        params.workflow_comment = this.rejectText
        if (this.quotation_status === 20) {
          updatequotationfirstaudit(params).then((res) => {
            if (res.data.error_no === 0) {
              this.$router.push({ path: '/quotationlist' })
            } else {
              this.$message.error(res.data.error_msg || '审批失败，请稍后重试')
            }
          })
        } else {
          updatequotationsecondaudit(params).then((res) => {
            if (res.data.error_no === 0) {
              this.$router.push({ path: '/quotationlist' })
            } else {
              this.$message.error(res.data.error_msg || '审批失败，请稍后重试')
            }
          })
        }
      }
    },
    passQuotation () {
      this.dialogVisible = false
      let params = {}
      params.quotation_id = this.quotation_id
      params.workflow_pass = 1
      params.workflow_comment = 'agree'
      if (this.quotation_status === 20) {
        updatequotationfirstaudit(params).then((res) => {
          if (res.data.error_no === 0) {
            this.$router.push({ path: '/quotationlist' })
          } else {
            this.$message.error(res.data.error_msg || '审批失败，请稍后重试')
          }
        })
      } else if (this.quotation_status === 30) {
        updatequotationsecondaudit(params).then((res) => {
          if (res.data.error_no === 0) {
            this.$router.push({ path: '/quotationlist' })
          } else {
            this.$message.error(res.data.error_msg || '审批失败，请稍后重试')
          }
        })
      }
    },
    getQuotationResult () {
      let params = {}
      params.quotation_id = this.quotation_id
      getquotationaudit(params).then((res) => {

      })
    },
    formatDate (now) {
      let year = now.getFullYear()
      let month = now.getMonth() + 1
      let date = now.getDate()
      let hour = now.getHours()
      let minute = now.getMinutes()
      let second = now.getSeconds()
      return year + '-' + month + '-' + date + '   ' + hour + ':' + minute + ':' + second
    },
    getQuotationInfo () {
      let params = {}
      params.quotation_id = this.quotation_id
      getquotationbyid(params).then((res) => {
        this.formData.vendor_name = res.data.result && res.data.result.vendor_name
        this.formData.old_vendor_name = this.formData.vendor_name
        this.formData.vendor_id = res.data.result && res.data.result.vendor_id
        this.formData.quotation_start_time = res.data.result && res.data.result.quotation_start_time * 1000
        this.formData.quotation_end_time = res.data.result && res.data.result.quotation_end_time * 1000
        this.formData.quotationTabel = this.formatQuotationTable((res.data.result && res.data.result.skus) || [])
        this.quotation_status = res.data.result && res.data.result.quotation_status
        this.count = res.data.result && res.data.result.skus && res.data.result.skus.length
      })
    },
    formatQuotationTable (skus) {
      skus.forEach((val, index, arr) => {
        arr[index].quotation_sku_price = (arr[index].quotation_sku_price / 100).toFixed(2)
        this.computedQuotation(arr[index])
      })
      return skus
    }
  }
}



