// 接口
import {
  getQuotationlist,
  getQuotationStatus,
  postquotationaudit,
  deletequotation,
  destroyquotation,
  searchVendor
} from '../../../../api/api'
import util from '../../../../common/js/util'
export default {
  data () {
    return {
      btn: {
        createquotation: util.jdugeShow('vendor-quotation-createquotation'),
        getquotationbyid: util.jdugeShow('vendor-quotation-getquotationbyid'), // 查看
        postquotationaudit: util.jdugeShow('vendor-quotation-postquotationaudit'), // 提交审核
        deletequotation: util.jdugeShow('vendor-quotation-deletequotation'), // 删除
        destroyquotation: util.jdugeShow('vendor-quotation-destroyquotation'), // 作废
        getquotationlog: util.jdugeShow('vendor-quotation-getquotationlog') // 日志
      },
      // 上方tab栏
      statusTab: [],
      currentStatus: '',
      params: '', // todo: 这里和采购单相似，需要重新处理
      // 采购单查询条件
      filter: {
        quotation_id: '',
        vendor_name: '',
        quotation_creator: '',
        start_time: '',
        end_time: ''
      },
      // 采购单列表展示
      list: {
        quotationTabel: [],
        currentPage: 1,
        currentPageSize: 20,
        total: 0,
        // todo: status能否抽调出来
        status: {
          '0': '全部',
          '10': '新建待提交',
          '20': '审核中',
          '30': '一审通过',
          '40': '二审通过',
          '50': '审核驳回',
          '60': '已作废'
        }
      },
      // 提示框状态
      dialog: {
        dialogVisible: false,
        optionText: '',
        optionType: '',
        optionId: ''
      }
    }
  },
  created () {
    this.getTabStatus()
    this.getQuotationList()
  },
  methods: {
    /**
     * [getTabStatus 获取tab状态数据，并对statusTab数据结构进行修改]
     */
    getTabStatus () {
      getQuotationStatus().then((res) => {
        if (res.data.error_no === 0) {
          this.statusTab = res.data.result && res.data.result.list
          // statusTab中增加‘全部’状态，用作页面展示
          this.statusTab.unshift({'quotation_status': '', 'quotation_status_text': '全部', 'qotation_status_count': res.data.result.total})
        }
      })
    },
    /**
     * [querySupplier sug查询供货商名称]
     * @param  {[String]} queryString [必填，查询字符]
     * @param  {Function} callback    [请求返回后获得数据后的回调函数]
     */
    querySupplier (queryString, callback) {
      let params = {}
      params.vendor_name = queryString
      // 每次输入后的sug查询
      searchVendor(params).then((res) => {
        let venderArr = []
        let result = (res.data.result && res.data.result) || []
        for (let i = 0, len = result.length; i < len; i++) {
          venderArr[i] = {}
          venderArr[i].value = result[i].vendor_name
          venderArr[i].vendor_id = result[i].vendor_id
        }
        // 参考elementui源码，这里callback的fetch会将数据填充到输入框下拉提示框中
        callback(venderArr)
      })
    },
    /**
     * [getQuotationList 获取报价单列表数据]
     * @param  {[Object]} param [非必填，请求参数]
     */
    getQuotationList (param) {
      let params = {}
      // todo: 这里的params能否不用传参
      // 此处若调用函数时有参数存在，则直接使用参数作为接口请求参数
      if (!param) {
        if (this.currentStatus) {
          params.quotation_status = this.currentStatus
        }
        params.page_size = this.list.currentPageSize
      } else {
        params = param
      }
      // 这里记录下当前请求的参数，以便更改部分参数时不影响其余参数
      // todo: 感觉这里并不合理，请求参数可否当次获取
      this.params = params
      getQuotationlist(params).then((res) => {
        if (res.data.error_no === 0) {
          this.list.quotationTabel = (res.data.result && res.data.result.list) || []
          this.list.total = res.data.result && res.data.result.total
        } else {
          this.$message.error(res.data.error_msg || '获取数据失败，请稍后重试')
        }
      })
    },
    /**
     * [tabChange tab更改时触发事件]
     * @param  {[Object]} tab   [当前的tab对象]
     * @param  {[Object]} event [当前操作事件对象]
     */
    tabChange (tab, event) {
      this.currentStatus = tab.name
      // 切换tab时，对当前params重置
      this.params = {}
      this.params.quotation_status = tab.name
      this.params.page_size = this.list.currentPageSize
      this.filter.quotation_id = ''
      this.filter.vendor_name = ''
      this.filter.quotation_creator = ''
      this.filter.start_time = ''
      this.filter.end_time = ''
      this.getQuotationList(this.params)
    },
    /**
     * [search 查询函数]
     */
    search () {
      // todo: 参数处理方式，关联之前的todo，不需要单独获取参数
      let params = {}
      if (this.filter.quotation_id) {
        params.quotation_id = this.filter.quotation_id
      }
      if (this.filter.vendor_name) {
        params.vendor_name = this.filter.vendor_name
      }
      if (this.filter.quotation_creator) {
        params.quotation_creator = this.filter.quotation_creator
      }
      if (this.filter.start_time && this.filter.end_time) {
        params.start_time = parseInt(util.getTimeStamp(this.filter.start_time))
        params.end_time = parseInt(util.getTimeStamp(this.filter.end_time))
      } else if (this.filter.start_time && !this.filter.end_time) {
        this.$message.error('请选择结束时间')
      } else if (!this.filter.start_time && this.filter.end_time) {
        this.$message.error('请选择开始时间')
      } else {}
      if (this.currentStatus) {
        params.quotation_status = this.currentStatus
      }
      params.page_size = this.list.currentPageSize
      this.getQuotationList(params)
    },
    /**
     * [doOption 列表操作的统一处理]
     */
    doOption () {
      let params = {}
      params.quotation_id = this.dialog.optionId
      this.dialog.dialogVisible = false
      if (this.dialog.optionType === 'commit') {
        postquotationaudit(params).then((res) => {
          if (res.data && res.data.error_no === 0) {
            this.getQuotationList()
          } else {
            this.$message.error(res.data.error_msg || '提交失败，请稍候重试')
          }
        })
      } else if (this.dialog.optionType === 'del') {
        deletequotation(params).then((res) => {
          if (res.data && res.data.error_no === 0) {
            this.getQuotationList()
          } else {
            this.$message.error(res.data.error_msg || '删除失败，请稍候重试')
          }
        })
      } else {
        destroyquotation(params).then((res) => {
          if (res.data && res.data.error_no === 0) {
            this.getQuotationList()
          } else {
            this.$message.error(res.data.error_msg || '作废操作失败，请稍候重试')
          }
        })
      }
    },
    commitQuotation (quotationId) {
      this.dialog.dialogVisible = true
      this.dialog.optionText = '是否提交该报价单'
      this.dialog.optionType = 'commit'
      this.dialog.optionId = quotationId
    },
    delQuotation (quotationId) {
      this.dialog.dialogVisible = true
      this.dialog.optionText = '是否删除该报价单'
      this.dialog.optionType = 'del'
      this.dialog.optionId = quotationId
    },
    destroyQuotation (quotationId) {
      this.dialog.dialogVisible = true
      this.dialog.optionText = '是否作废该报价单'
      this.dialog.optionType = 'destroy'
      this.dialog.optionId = quotationId
    },
    handleCurrentChange (val) {
      this.list.currentPage = val
      // todo: 防止数据污染，深拷贝params
      this.params.page_num = val
      this.getQuotationList(this.params)
    },
    handleSizeChange (val) {
      this.list.currentPageSize = val
      // todo: 防止数据污染，深拷贝params
      this.params.page_size = val
      this.getQuotationList(this.params)
    }
  }
}



