<template>
  <section>
    <el-form ref="form" :model="form" :rules="rules" label-width="120px" class="createproduct">
      <el-form-item label="规格信息" prop="standard">
        <div class="tipText">商品自带条形码不能重复，最小规格对应的商品自带条形码不能为空。默认采购规格有且只能有1个</div>
        <table>
          <thead>
            <td>商品条形码</td>
            <td>包装单位</td>
            <td>规格</td>
            <td>默认采购规格</td>
            <td></td>
          </thead>
          <tr
          v-for="(item, index) in form.standard"
          :key="index">
            <td>
              <el-input v-model="item.upc_id" placeholder="长度为8～13个数字或字母" class="barcard" @blur="getupcfromcommodity(index)" ></el-input>
            </td>
            <td>
              <el-select v-model="item.upc_unit" placeholder="请选择" class="standard">
                <el-option v-for="(commonDataItem, commonDataIndex) in commonData" :key= "commonDataIndex" :label="commonDataItem" :value="commonDataIndex"></el-option>
              </el-select>
            </td>
            <td>
              1* <el-input v-model="item.upc_unit_num"  class="num"></el-input>
            </td>
            <td>
              <el-radio v-model="form.radio" :label="index" @change.native="getCurrentRow(index)" border>默认采购规格</el-radio>
            </td>
            <td>
              <i class="el-icon-delete" v-if="index>0" @click="delCurrentRow(index)"></i>
            </td>
          </tr>
        </table>
      </el-form-item>
      <el-button type="primary" :disabled="!(form.standard.length<40)" class="add-standard" @click="addStandard">添加规格</el-button>
      <el-form-item label="商品名称" prop="name">
        <el-input v-model="form.name" placeholder="长度最多为50个字符" class="barcard"></el-input>
      </el-form-item>
       <el-form-item label-width="0">
        <el-form-item label="后台类目" prop="value1" class="category">
          <el-select v-model="form.value1" placeholder="一级目录" @change='handleSelect' id="select1">
            <el-option
              v-for="item in options1"
              :key="item.cat_id"
              :label="item.cat_name"
              :value="item.cat_id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item prop="value2" class="category">
          <el-select v-model="form.value2" placeholder="二级目录" @change='handleSelectTwo' id="select2">
            <el-option
              v-for="item in options2"
              :key="item.cat_id"
              :label="item.cat_name"
              :value="item.cat_id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item prop="value3" class="category">
          <el-select v-model="form.value3" placeholder="三级目录" id="select3">
            <el-option
              v-for="item in options3"
              :key="item.cat_id"
              :label="item.cat_name"
              :value="item.cat_id">
            </el-option>
          </el-select>
        </el-form-item>
      </el-form-item>
      <el-form-item label="税率" prop="tax_rate">
        <el-radio-group v-model="form.tax_rate">
          <el-radio label="1">17%</el-radio>
          <el-radio label="2">11%</el-radio>
          <el-radio label="3">6%</el-radio>
          <el-radio label="4">0%</el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="图片" prop="fileUrl">
        <p style="margin-top: 0px;">最多上传6张，格式为PNG、JPG、JPEG，单张小于8M，建议尺寸为1:1。<em style="color:red; font-style:normal;">第一张图片默认为主图。</em></p>
        <el-upload
          action="/vendor/upload/uploadimg"
          :headers='uploadHeaders'
          accept='image/png, image/jpeg, image/jpg'
          name='image'
          :limit='6'
          :file-list="fileList"
          list-type="picture-card"
          :before-upload="handlePictureCardBefore"
          :on-preview="handlePictureCardPreview"
          :on-success="handleSuccess"
          :on-remove="handleRemove"
          :on-exceed="handleExact">
          <i class="el-icon-plus"></i>
          <el-input v-model="form.fileUrl" class="inputWidth" type="hidden"></el-input>
        </el-upload>
        <el-dialog :visible.sync="dialogVisible" size="tiny">
          <img width="100%" :src="dialogImageUrl" alt="">
        </el-dialog>
      </el-form-item>
      <el-form-item prop="skuSafeStockNum" label="安全库存" required>
          <el-input v-model="form.skuSafeStockNum" placeholder="请填写正整数" style="width: 200px;"></el-input><span>&nbsp;&nbsp;{{commonData[form.skuSafeStockUnit]}}</span>
      </el-form-item>
      <el-form-item prop="skuFromCountry" label="国产/进口" required>
        <el-select v-model="form.skuFromCountry" style="width: 200px;" clearable placeholder="请选择">
          <el-option label="国产" value="1"></el-option>
          <el-option label="进口" value="2"></el-option>
        </el-select>
      </el-form-item>
      <el-row>
        <el-col :span="12" style="max-width: 350px;">
          <el-form-item prop="skuEffectType" label="效期类型" required>
            <el-select v-model="form.skuEffectType" style="width: 200px;" clearable placeholder="请选择效期类型">
              <el-option label="生产日期型" value="1"></el-option>
              <el-option label="到效期型" value="2"></el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col v-if="form.skuEffectType + '' === '1'" :span="12">
          <el-form-item prop="skuEffectDay" label="保质期天数"  required>
            <el-input v-model="form.skuEffectDay" placeholder="请填写正整数" style="width: 200px;"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-form-item prop="skuEffectAlarmDay" label="效期预警天数" required>
          <el-input v-model="form.skuEffectAlarmDay" placeholder="请填写正整数" style="width: 200px;"></el-input>
      </el-form-item>
      <el-row>
        <el-col :span="12" style="max-width: 350px;">
          <el-form-item prop="skuNet" label="净含量" required>
            <el-input v-model="form.skuNet" @change="changeSkuNetUnit" placeholder="请填写正整数或小数" style="width: 200px;"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item prop="skuNetUnit" label-width="0">
            <el-select v-model="form.skuNetUnit" style="width: 200px;" @change="changeSkuNetUnit" clearable placeholder="选择净含量单位">
              <el-option label="g" value="2"></el-option>
              <el-option label="kg" value="1"></el-option>
              <el-option label="ml" value="3"></el-option>
              <el-option label="L" value="4"></el-option>
            </el-select>
          </el-form-item>
        </el-col>
      </el-row>
      <el-form-item label="重量(g)" prop="skuWeight" required>
          <el-input v-model="form.skuWeight" :disabled="form.skuNetUnit==='1' || form.skuNetUnit==='2'" placeholder="请填写正整数或小数" style="width: 200px;"></el-input>
      </el-form-item>
      <el-form-item prop="skuBusinessForm" label="支持的业态" required>
          <el-checkbox-group v-model="form.skuBusinessForm">
            <el-checkbox :label="1">货架</el-checkbox>
            <el-checkbox :label="3">门店</el-checkbox>
          </el-checkbox-group>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="checkFormRule" :disabled="btn.updatesku">保存并下一步</el-button>
      </el-form-item>
    </el-form>
  </section>
</template>

<script>
import qs from 'qs'
import util from '../../common/js/util'
import commonData from '../../common/js/commonData'
import { getUpcFromCommodity, createSku, updateSku, getSkuInfo, getUpcCategory } from '../../api/api'
export default {
  data () {
    let checkStandar = (rule, value, callback) => {
      let hasValue = true
      let upcIdIsTrue = true
      let upcUnitNumIsTrue = true
      let upcIdNoRepeat = true
      let upcIdList = []
      let upcUnitNumIsMultiple = true
      let minUpcUnitNum = value[0].upc_unit_num
      let upcUnitNumList = []
      let minUpcUnitNumHasUpcId = true
      let minUpcUnitNumList = []
      let upcUnitNumObj = new Map()
      let upcUnitIsMultiple = true
      let minUpcUnitText = value[0].upc_unit
      for (let item of value) {
        item.upc_id && upcIdList.push(item.upc_id)
        upcUnitNumList.push(item.upc_unit_num)
        if (upcUnitNumObj.has(item.upc_unit_num + '')) {
          if (upcUnitNumObj.get(item.upc_unit_num + '') !== item.upc_unit) {
            upcUnitIsMultiple = false
          }
        } else {
          upcUnitNumObj.set(item.upc_unit_num + '', item.upc_unit)
        }
        if (!(item.upc_unit && item.upc_unit_num)) hasValue = false
        if (item.upc_id && !/^[A-Za-z0-9]{8,13}$/.test(item.upc_id)) upcIdIsTrue = false
        if (!/^[0-9]*$/.test(item.upc_unit_num) || item.upc_unit_num === 0 + '') upcUnitNumIsTrue = false
        if (item.upc_unit_num * 1 < minUpcUnitNum * 1) {
          minUpcUnitNum = item.upc_unit_num
          minUpcUnitText = item.upc_unit
        }
      }
      upcUnitNumList.map((item, index) => {
        if (item * 1 === minUpcUnitNum * 1) minUpcUnitNumList.push(index)
        if (item % minUpcUnitNum !== 0) upcUnitNumIsMultiple = false
      })
      minUpcUnitNumList.map((item) => {
        if (!value[item].upc_id) minUpcUnitNumHasUpcId = false
      })
      if (upcIdList.length !== [...new Set(upcIdList)].length) upcIdNoRepeat = false
      this.form.skuSafeStockUnit = minUpcUnitText
      if (!hasValue) {
        callback(new Error('请选择商品包装单位并输入规格'))
      } else if (!upcIdIsTrue) {
        callback(new Error('商品条形码必须为8-13位的数字或字母'))
      } else if (!upcUnitNumIsTrue) {
        callback(new Error('商品个数必须为正整数'))
      } else if (!upcIdNoRepeat) {
        callback(new Error('商品条形码不允许重复'))
      } else if (!upcUnitNumIsMultiple) {
        callback(new Error('商品最小规格必须是其它规格的约数'))
      } else if (!minUpcUnitNumHasUpcId) {
        callback(new Error('商品最小规格的条形码必须不为空'))
      } else if (!upcUnitIsMultiple) {
        callback(new Error('相同规格的商品对应包装单位必须相同'))
      }
      callback()
    }
    let checkSkuEffectDay = (rule, value, callback) => {
      if (!/^[0-9]*[1-9][0-9]*$/.test(value) && this.form.skuEffectType === '1') {
        callback(new Error('请输入正整数'))
      } else {
        callback()
      }
    }
    let checkSkuBusinessForm = (rule, value, callback) => {
      if (value.length) {
        callback()
      } else {
        callback(new Error('请选择支持的业态'))
      }
    }
    let checkSkuNet = (rule, value, callback) => {
      if (/^([0-9]+)+([.]{1}[0-9]{1})?$/.test(value) && value * 1 > 0) {
        callback()
      } else {
        callback(new Error())
      }
    }
    return {
      btn: {
        updatesku: !util.jdugeShow('vendor-sku-updatesku')
      },
      getupcfromcommoditySuccess: false,
      uploadHeaders: {
        'Access-Control-Allow-Origin': '*',
        'Access-Control-Allow-Methods': 'POST,GET,OPTIONS,DELETE',
        'Access-Control-Allow-Headers': 'application/json; charset=UTF-8'
      },
      commonData: commonData,
      fileList: [],
      options1: [],
      options2: [],
      options3: [],
      form: {
        radio: 0,
        standard: [
          {
            upc_id: '',
            upc_unit: '',
            upc_unit_num: ''
          }
        ],
        name: '',
        value1: '',
        value2: '',
        value3: '',
        tax_rate: '1',
        fileUrl: '',
        fileArr: [],
        skuSafeStockNum: '',
        skuSafeStockUnit: '',
        skuFromCountry: '',
        skuEffectType: '',
        skuEffectDay: '',
        skuEffectAlarmDay: '',
        skuNet: '',
        skuNetUnit: '',
        skuWeight: '',
        skuBusinessForm: []
      },
      dialogImageUrl: '',
      dialogVisible: false,
      edit_sku_id: '',
      rules: {
        standard: [
          { required: true, type: 'number', validator: checkStandar, trigger: 'blur change' }
        ],
        name: [
          { required: true, message: '请输入商品名称', trigger: 'blur' },
          { pattern: /^.{0,50}$/, message: '商品名称限制最多50个字符' }
        ],
        tax_rate: [
          { required: true }
        ],
        value1: [
          { required: true, message: '请选择一级类目', trigger: 'change' }
        ],
        value2: [
          { required: true, message: '请选择二级类目', trigger: 'change' }
        ],
        value3: [
          { required: true, message: '请选择三级类目', trigger: 'change' }
        ],
        fileUrl: [
          { required: true, message: '请上传商品图片', trigger: 'change' }
        ],
        skuSafeStockNum: [
          { required: true, message: '请输入安全库存', trigger: 'blur change' },
          { pattern: /^[0-9]*[1-9][0-9]*$/, message: '请输入正整数' }
        ],
        skuFromCountry: [
          { required: true, message: '请选择国产/进口', trigger: 'change' }
        ],
        skuEffectType: [
          { required: true, message: '请选择效期类型', trigger: 'change' }
        ],
        skuEffectDay: [
          { required: true,
            validator: checkSkuEffectDay,
            trigger: 'blur change' }
        ],
        skuEffectAlarmDay: [
          { required: true, message: '请输入效期预警天数', trigger: 'blur change' },
          { pattern: /^[0-9]*[1-9][0-9]*$/, message: '请输入正整数' }
        ],
        skuNet: [
          { required: true, message: '请填写净含量', trigger: 'blur change' },
          { validator: checkSkuNet, message: '请输入正整数或一位小数' }
        ],
        skuNetUnit: [
          { required: true, message: '请选择净含量类型', trigger: 'change' }
        ],
        skuWeight: [
          { required: true, message: '请填写重量', trigger: 'blur change' },
          { validator: checkSkuNet, message: '请输入正整数或一位小数' }
        ],
        skuBusinessForm: [
          { required: true,
            validator: checkSkuBusinessForm,
            message: '请选择支持的业态',
            trigger: 'change' }
        ]
      }
    }
  },
  created () {
    this.edit_sku_id = this.$route.query.sku_id
    if (this.edit_sku_id) {
      this.getMsg(this.edit_sku_id)
    } else {
      this.getUpcCategory(1)
    }
  },
  methods: {
    changeSkuNetUnit () {
      if (this.form.skuNetUnit === '2') {
        this.form.skuWeight = this.form.skuNet * 1 + ''
      }
      if (this.form.skuNetUnit === '1') {
        this.form.skuWeight = this.form.skuNet * 1000 + ''
      }
    },
    handlePictureCardBefore (file) {
      const isLt8M = file.size / 1024 / 1024 < 8
      if (!isLt8M) {
        this.$message({message: '上传图片大小不能超过 8MB', type: 'error'})
      }
      return isLt8M
    },
    getMsg (sku_id) {
      let params = {
        sku_id
      }
      getSkuInfo(params).then((res) => {
        let result = res.data.result
        // console.log(result)
        this.form.name = result.sku_name
        this.form.value1 = result.sku_category_1
        this.form.value2 = result.sku_category_2
        this.form.value3 = result.sku_category_3
        this.form.tax_rate = String(result.sku_tax_rate)
        this.form.standard = result.upcs
        this.fileList = result.sku_image || []
        this.form.fileUrl = result.sku_image ? result.sku_image[0].url : ''
        this.form.fileArr = result.sku_image || []
        this.form.skuSafeStockNum = result.sku_safe_stock_num || ''
        this.form.skuFromCountry = result.sku_from_country || ''
        this.form.skuEffectType = result.sku_effect_type || ''
        this.form.skuEffectDay = result.sku_effect_day || ''
        this.form.skuEffectAlarmDay = result.sku_effect_alarm_day || ''
        this.form.skuNet = result.sku_net || ''
        this.form.skuWeight = result.sku_weight || ''
        this.form.skuNetUnit = result.sku_net_unit || ''
        this.form.skuBusinessForm = result.sku_business_form || []
        this.form.skuWeight = result.sku_weight || ''
        this.form.initialSkuBusinessForm = result.sku_business_form || []
        this.form.standard.forEach((v, i) => {
          if (v.is_default === 1) {
            this.form.radio = i
          }
        })
        this.getUpcCategory(1)
        this.getUpcCategory(2, this.form.value1)
        this.getUpcCategory(3, this.form.value2)
      })
    },
    handleSelectThree (category) {
      // console.log(category)
      // this.form.value1 = category.value1
      // this.form.value2 = category.value2
      // this.form.value3 = category.value3
    },
    delCurrentRow (index) {
      if (this.form.radio === index) this.form.radio = 0
      this.form.standard.splice(index, 1)
      // console.log(this.form.standard)
    },
    getupcfromcommodity (index) {
      this.edit_sku_id = this.$route.query.sku_id
      if (this.getupcfromcommoditySuccess) {
        return
      }
      if (this.edit_sku_id) {
        return
      }
      let upc_id = this.form.standard[index].upc_id
      let params = {
        upc_id
      }
      getUpcFromCommodity(params).then((res) => {
        let result = res.data.result
        if (result.name) {
          this.form.name = result.name
          this.form.value1 = result.cat1_id
          this.form.value2 = result.cat2_id
          this.form.value3 = result.cat3_id
          this.form.tax_rate = '1'
          this.fileList = result.image || []
          this.form.fileArr = result.image || []
          this.form.fileUrl = result.image ? result.image[0].url : ''
          this.getUpcCategory(2, this.form.value1)
          this.getUpcCategory(3, this.form.value2)
          this.getupcfromcommoditySuccess = true
        }
        // console.log(result)
      })
    },
    getCurrentRow (index) {
      // console.log(this.form.standard)
      // console.log(index)
    },
    /**
     * 检查Form规则
     */
    checkFormRule () {
      this.$refs['form'].validate((valid) => {
        if (!valid) {
          return
        }
        if (this.edit_sku_id) {
          let deleteSkuBusinessForm = false
          this.form.initialSkuBusinessForm.map((item, index, list) => {
            if (Array.from(this.form.skuBusinessForm).indexOf(item) === -1) {
              deleteSkuBusinessForm = true
            }
          })
          if (deleteSkuBusinessForm) {
            this.$confirm('取消勾选业态后，该业态下的配货信息将被清空', '提示', {
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              type: 'warning'
            }).then(() => {
              this.onSave()
            }).catch(() => {
            })
          } else {
            this.onSave()
          }
        } else {
          this.onSave()
        }
      })
    },
    /**
     * 提交Form表单
     */
    onSave () {
      this.form.standard.forEach((v, index) => {
        this.form.standard[index].is_default = false
      })
      this.form.standard[this.form.radio].is_default = true
      let categoryOneText = document.getElementById('select1').value
      let categoryTwoText = document.getElementById('select2').value
      let categoryThrText = document.getElementById('select3').value
      let skuCategoryText = `${categoryOneText},${categoryTwoText},${categoryThrText}`
      let params = {
        sku_id: this.edit_sku_id || '',
        sku_category_text: skuCategoryText,
        sku_name: this.form.name,
        sku_category_1: this.form.value1,
        sku_category_2: this.form.value2,
        sku_category_3: this.form.value3,
        sku_tax_rate: this.form.tax_rate,
        upcs: JSON.stringify(this.form.standard),
        sku_safe_stock_num: Number(this.form.skuSafeStockNum),
        sku_from_country: Number(this.form.skuFromCountry),
        sku_effect_type: Number(this.form.skuEffectType),
        sku_effect_alarm_day: Number(this.form.skuEffectAlarmDay),
        sku_net: this.form.skuNet,
        sku_net_unit: Number(this.form.skuNetUnit),
        sku_business_form: JSON.stringify(this.form.skuBusinessForm)
      }
      if (params.sku_net_unit !== 1 && params.sku_net_unit !== 2) {
        params.sku_weight = this.form.skuWeight
      }
      if (params.sku_effect_type === 1) {
        params.sku_effect_day = this.form.skuEffectDay
      }
      if (this.edit_sku_id) {
        params.sku_image = JSON.stringify(this.form.fileArr)
        updateSku(qs.stringify(params)).then((res) => {
          if (res.data.error_no === 0) {
            this.$message({message: '保存成功', type: 'success'})
            this.$router.push({path: `/updateSupplyInfo?sku_id=${this.edit_sku_id}`})
          } else {
            this.$message({message: res.data.error_msg, type: 'error'})
          }
        })
      } else {
        params.sku_image = JSON.stringify(this.form.fileArr)
        createSku(qs.stringify(params)).then((res) => {
          if (res.data.error_no === 0) {
            this.$message({message: '保存成功', type: 'success'})
            this.$router.push({path: `/updateSupplyInfo?sku_id=${res.data.result.sku_id}`})
          } else {
            this.$message({message: res.data.error_msg, type: 'error'})
          }
        })
      }
    },
    handleExact () {
      this.$message({
        message: '最多只能传6张',
        type: 'warning'
      })
    },
    handleSuccess (response, file, fileList) {
      let addPics = this.mangePics(fileList)
      this.form.fileArr = addPics
      this.form.fileUrl = addPics[0].url
    },
    handleRemove (file, fileList) {
      let delPics = this.mangePics(fileList)
      this.form.fileArr = delPics
      this.form.fileUrl = delPics[0] ? delPics[0].url : ''
      // console.log(this.form.fileArr)
    },
    mangePics (fileList) {
      let _imgArr = []
      fileList.forEach((v, index) => {
        let imgUrl = {}
        if (fileList[index].response) {
          imgUrl.url = fileList[index].response.result
        } else {
          imgUrl.url = fileList[index].url
        }
        index === 0 ? imgUrl.is_master = true : imgUrl.is_master = false
        _imgArr.push(imgUrl)
      })
      return _imgArr
    },
    handlePictureCardPreview (file) {
      this.dialogImageUrl = file.url
      this.dialogVisible = true
    },
    addStandard () {
      this.form.standard.push({
        upc_id: '',
        upc_unit: '',
        upc_unit_num: ''
      })
    },
    // get category
    getUpcCategory (deepth, parentId) {
      let params = {
        depth: deepth,
        parent_id: parentId || ''
      }
      getUpcCategory(params).then((res) => {
        let _options = `options${deepth}`
        if (res.data.result) {
          this[_options] = res.data.result
        }
      })
    },
    handleSelect (val) {
      this.form.value2 = ''
      this.form.value3 = ''
      this.getUpcCategory(2, val)
    },
    handleSelectTwo (val) {
      this.form.value3 = ''
      this.getUpcCategory(3, val)
    }
  }
}
</script>

<style scoped lang="scss">
  .num{width: 54px;}
  .standard{width: 94px;}
  .barcard{width: 380px;}
  .add-standard{margin: 10px 0 30px 80px;}
  table thead td,
  table tr td{padding-right: 30px;}
  .el-icon-delete{cursor: pointer;}
  .category{display: inline-block;}
  .tipText{color: #5a5e66;}
</style>



