<template>
  <!--接口列表-->
  <div class="role-manage">
    <el-button
            type="primary"
            @click.native.prevent="showDialog('create')"
            icon="el-icon-plus">
      新建接口
    </el-button>
    <el-table
            :data="tableData"
            style="width: 100%">
      <el-table-column
              prop="operation_id"
              label="NO.">
      </el-table-column>
      <el-table-column
              prop="operation_name"
              label="接口名称">
      </el-table-column>
      <el-table-column
              prop="router"
              label="接口路由">
      </el-table-column>
      <el-table-column
              prop="check_param"
              label="接口参数">
      </el-table-column>
      <el-table-column
              prop="content"
              label="描述">
      </el-table-column>
      <el-table-column
              label="操作">
        <template slot-scope="scope">
          <el-button
                  @click.native.prevent="showDialog('edit', scope.row)"
                  type="text"
                  size="small">
            编辑
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
            @size-change="handleSizeChange"
            @current-change="changePage"
            :current-page.sync="page"
            :page-sizes="[20, 50, 100, 200]"
            :page-size="pageSize"
            layout="total, sizes, prev, pager, next"
            prev-text="上一页"
            next-text="下一页"
            :total="totalCount">
    </el-pagination>
    <el-dialog
            :title="dialogTitle"
            :visible.sync="show"
            width="30%">
      <el-form
              :model="formParams"
              :rules="rules"
              ref="operation"
              label-width="100px">
        <el-form-item
                class="is-required"
                prop="operationName"
                label="接口名称">
          <el-input v-model="formParams.operationName"></el-input>
        </el-form-item>
        <el-form-item
                class="is-required"
                prop="operationRouter"
                label="接口路由">
          <el-input v-model="formParams.operationRouter"></el-input>
        </el-form-item>
        <el-form-item label="接口参数">
          <el-input v-model="formParams.operationParam"></el-input>
        </el-form-item>
        <el-form-item label="描述">
          <el-input v-model="formParams.operationContent"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="show = false">取 消</el-button>
        <el-button
                v-show="dialogTitle === '新建接口'"
                type="primary"
                @click="save('create')">
          确 定
        </el-button>
        <el-button
                v-show="dialogTitle === '编辑接口'"
                type="primary"
                @click="save('edit')">
          确 定
        </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
  import { getInterfaceList, createOperation, updateOperation } from '../../api/api'

  export default {
    data () {
      return {
        // table所有数据
        tableData: [],
        // 当前翻页数
        page: 1,
        // 总页数
        totalPage: 3,
        // table总条数
        totalCount: 1,
        // 每页显示多少条
        pageSize: 20,
        show: false,
        dialogTitle: '',
        formParams: {
          operationId: '',
          // 接口名称
          operationName: '',
          // 接口路由
          operationRouter: '',
          // 接口参数
          operationParam: '',
          // 描述
          operationContent: '',
        },
        rules: {
          operationName: [{
            required: true, message: '接口名称不能为空'
          }],
          operationRouter: [{
            required: true, message: '接口路由不能为空'
          }]
        }
      }
    },
    mounted () {
      this.getOperationList(1, 20)
    },
    methods: {
      /**
       * 获取接口列表
       * @param {Number} page 页码
       */
      getOperationList (page, pageSize) {
        this.page = page
        this.pageSize = pageSize
        let params = {
          page_size: pageSize,
          page_num: page
        }
        getInterfaceList(params).then(res => {
          if (res.data.error_no === 0) {
            this.tableData = res.data.result.operations
            this.totalCount = parseInt(res.data.result.total_count)
          } else {
            this.$message.error(res.data.error_msg)
          }
        })
          .catch(error => {
            this.$message.error('服务器傲娇了，请刷新重试')
          })
      },
      changePage (val) {
        this.getOperationList(val, this.pageSize)
      },
      handleSizeChange (val) {
        this.getOperationList(1, val)
      },
      /**
       * 新建、编辑接口
       * @param {String} operation 操作（编辑 or 删除）
       * @param {Object} data
       */
      showDialog (operation, data = {}) {
        this.show = true
        if (operation ===  'create') {
          this.$nextTick(() => {
            this.$refs['operation'].resetFields()
            this.formParams.operationName = ''
            this.formParams.operationRouter = ''
            this.formParams.operationParam = ''
            this.formParams.operationContent = ''
            this.formParams.operationId = ''
          })
          this.dialogTitle = '新建接口'
        } else {
          this.formParams.operationName = data.operation_name || ''
          this.formParams.operationRouter = data.router || ''
          this.formParams.operationParam = data.check_param || ''
          this.formParams.operationContent = data.content || ''
          this.formParams.operationId = data.operation_id || ''
          this.dialogTitle = '编辑接口'
        }
      },
      /**
       * 保存
       * @param {String} operation 操作（编辑 or 删除）
       */
      save (operation) {
        this.formParams.operationName = this.formParams.operationName.trim()
        this.formParams.operationRouter = this.formParams.operationRouter.trim()
        this.$refs['operation'].validate((valid) => {
          if (valid) {
            let params = {
              operation_name: this.formParams.operationName,
              content: this.formParams.operationContent,
              router: this.formParams.operationRouter,
              check_param: this.formParams.operationParam
            }
            if (operation === 'create') {
              createOperation(params).then(res => {
                if (res.data.error_no === 0) {
                  this.$message.success('保存成功')
                  this.getOperationList(1, this.pageSize)
                  this.show = false
                } else {
                  this.$message.error(res.data.error_msg)
                }
              })
                .catch(() => {
                  this.$message.error('服务器傲娇了，请刷新重试')
                })
            } else {
              params.operation_id = this.formParams.operationId
              updateOperation(params).then(res => {
                if (res.data.error_no === 0) {
                  this.$message.success('保存成功')
                  this.show = false
                  this.getOperationList(this.page, this.pageSize)
                } else {
                  this.$message.error(res.data.error_msg)
                }
              })
                .catch(() => {
                  this.$message.error('服务器傲娇了，请刷新重试')
                })
            }
          }
        })
      }
    }
  }
</script>



