<template>
  <div class="role-manage">
    <el-button
      type="primary"
      @click="createUser"
      icon="el-icon-plus">
      新增角色
    </el-button>
    <el-table
      :data="tableData"
      style="width: 100%">
      <el-table-column
        prop="role_id"
        label="NO.">
      </el-table-column>
      <el-table-column
        prop="role_name"
        label="角色名称">
      </el-table-column>
      <el-table-column
        prop="creator"
        label="创建人">
      </el-table-column>
      <el-table-column
        label="创建时间">
        <template slot-scope="scope">
          {{formatDate(scope.row.create_time)}}
        </template>
      </el-table-column>
      <el-table-column
        label="状态">
        <template slot-scope="scope">
          {{scope.row.enabled == '1' ? '已启用' : '已禁用'}}
        </template>
      </el-table-column>
      <el-table-column
        label="操作">
        <template slot-scope="scope">
          <el-button
            @click.native.prevent="linkToEdit(scope.row.role_id)"
            type="text"
            size="small">
            关联权限
          </el-button>
          <el-button
            @click.native.prevent="changeStatus(scope.row.role_id, scope.row.enabled)"
            type="text"
            size="small">
            {{scope.row.enabled == '1' ? '禁用' : '启用'}}
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      @current-change="changePage"
      :current-page.sync="page"
      :page-size="20"
      layout="total, prev, pager, next"
      :total="totalCount"
      prev-text="上一页"
      next-text="下一页">
    </el-pagination>
  </div>
</template>

<script>
import { getRoleList, updateRole } from '../api/api'
// 时间戳转字符串
const timeStampToDate = (timeStamp) => {
  timeStamp = parseInt(timeStamp)
  let date = new Date(timeStamp)
  let result = ''
  result += date.getFullYear() + '-'
  result += ((date.getMonth() + 1) > 9 ? date.getMonth() + 1 : '0' + (date.getMonth() + 1)) + '-'
  result += (date.getDate() > 9 ? date.getDate() : '0' + date.getDate()) + ' '
  result += (date.getHours() > 9 ? date.getHours() : '0' + date.getHours()) + ':'
  result += (date.getMinutes() > 9 ? date.getMinutes() : '0' + date.getMinutes()) + ':'
  result += date.getSeconds() > 9 ? date.getSeconds() : '0' + date.getSeconds()
  return result
}

export default {
  data () {
    return {
      // table所有数据
      tableData: [],
      // 当前翻页数
      page: 1,
      // 总页数
      totalPage: 3,
      // table总条数
      totalCount: 1
    }
  },
  methods: {
    // 修改角色状态
    changeStatus (id, status) {
      // 判断当前是启用还是禁用
      const conformText = status == '1' ? '禁用' : '启用'
      const enableStatus = status == '1' ? 2 : 1
      // 弹窗提示
      this.$confirm('确定' + conformText + '角色?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        // 修改用户状态
        const postData = {
          role_id: id,
          enable: enableStatus
        }
        updateRole(postData).then(response => {
          const res = response.data
          if (res.error_no === 0) {
            this.$message.success(conformText + '成功!')
            this.getTableData(this.page)
          } else {
            this.$message.error(res.error_msg)
          }
        })
        .catch(error => {
          this.$message.error('服务器傲娇了，请刷新重试')
        })
      })
    },
    // 表格中展示的状态文案
    formatDate (timeStamp) {
      return timeStampToDate(timeStamp * 1000)
    },
    // 获取table数据
    getTableData (page) {
      const getParams = {
        page_num: page
      }
      getRoleList(getParams).then(response => {
        let res = response.data
        if (res.error_no === 0) {
          this.tableData = res.result.roles
          this.totalCount = parseInt(res.result.total_count, 10)
          this.totalPage = Math.ceil(this.totalCount / 20)
        } else {
          this.$message.error(res.error_msg)
        }
      })
      .catch(error => {
        this.$message.error('服务器傲娇了，请刷新重试')
      })
    },
    // 翻页事件
    changePage (val) {
      this.getTableData(val)
    },
    createUser () {
      this.$router.push({path: '/roleCreate'})
    },
    linkToEdit (id) {
      this.$router.push({path: '/roleEdit', query: {role_id: id}})
    }
  },
  mounted () {
    // 获取初始数据
    this.getTableData(this.page)
  }
}
</script>



