<template>
  <div class="user-edit">
    <el-form
            ref="form"
            :model="formData"
            label-width="100px"
            label-position="left">
      <el-form-item
              label="用户类型">
        <el-select
                :disabled="!!userId || !isWms"
                @change="changeUserType"
                v-model="formData.userType">
          <el-option
                  label="sso"
                  value="sso">
          </el-option>
          <el-option
                  label="wmpass"
                  value="wmpass">
          </el-option>
        </el-select>
      </el-form-item>
      <!--内部用户-->
      <div v-show="formData.userType === 'sso'">
        <el-form-item label="用户邮箱">
          <el-autocomplete
                  :disabled="!!userId"
                  v-model="formData.email"
                  :fetch-suggestions='queryUserMail'
                  :trigger-on-focus='false'
                  placeholder="请输入用户邮箱"
                  @select="selectEmail"
                  valueKey="value"
                  value="info">
          </el-autocomplete>
        </el-form-item>
        <el-form-item
                v-show="formData.name != ''"
                label="姓名">
          <el-input
                  :disabled="true"
                  v-model="formData.name">
          </el-input>
        </el-form-item>
        <el-form-item
                v-show="formData.phone != ''"
                label="手机号">
          <el-input
                  :disabled="true"
                  v-model="formData.phone">
          </el-input>
        </el-form-item>
      </div>
      <!--外部用户-->
      <div v-show="formData.userType === 'wmpass'">
        <el-form-item label="手机号">
          <el-input
                  :disabled="!!userId"
                  v-model="formData.phone">
          </el-input>
        </el-form-item>
        <el-form-item
                v-show="formData.name != ''"
                label="姓名">
          <el-input
                  :disabled="true"
                  v-model="formData.name">
          </el-input>
        </el-form-item>
        <el-form-item
                v-show="formData.email != ''"
                label="邮箱">
          <el-input
                  :disabled="true"
                  v-model="formData.email">
          </el-input>
        </el-form-item>
      </div>
      <template v-if="userId">
        <el-form-item label="已关联角色">
          <div class="expandingArea">
            <span class="hidden-text">{{userRole.join(',')}}</span>
            <el-input
                    :disabled="true"
                    v-model="userRole.join(',')"
                    type="textarea">
            </el-input>
          </div>
        </el-form-item>
      </template>
      <el-form-item label="可关联角色">
        <el-checkbox-group v-model="formData.role_ids">
          <el-checkbox
                  v-for="item in roleData"
                  :label="item.role_id"
                  name="type"
                  :key="item.role_id"
                  v-if="item.enabled == 1">
            {{item.role_name}}
          </el-checkbox>
        </el-checkbox-group>
      </el-form-item>
      <el-form-item>
        <el-button
                type="primary"
                @click="onSubmit">
          保存
        </el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  import { getRoleList, getUserRoles, modifyUserRoles, createUser, searchUserMail } from '../api/api'
  export default {
    data () {
      return {
        // 编辑时的用户id
        userId: this.$route.query.user_id ? this.$route.query.user_id : '',
        // 提交的post data
        formData: {
          email: '',
          role_ids: [],
          user_id: this.userId,
          // 用户名
          name: '',
          // 手机号码
          phone: '',
          // 用户类型
          userType: 'sso' // 'SSO' 内部用户  'WMPASS'外部用户
        },
        // 所有角色数据
        roleData: [],
        // 当前用户的角色
        userRole: [],
        // 彩云系统还是沧海系统
        isWms: window.location.href.indexOf('wms') >= 0
      }
    },
    methods: {
      // 查找用户邮箱
      queryUserMail (queryString, callback) {
        let params = {}
        params.wd = queryString
        searchUserMail(params).then((res) => {
          let venderArr = []
          let result = (Array.isArray(res.data.result.list) && res.data.result.list) || []
          result.forEach((val, index, arr) => {
            venderArr[index] = {}
            // venderArr[index].value = (val.email + '').replace('@ele.me','')
            venderArr[index].value = val.email
            venderArr[index].info = JSON.stringify({name: val.name, mobile: val.mobile})
          })
          callback(venderArr)
        })
      },
      // 获取所有角色数据
      getRoleData () {
        const getParams = {
          // 后端没有全量接口，暂时设个巨大值
          page_size: 100000
        }
        getRoleList(getParams).then(response => {
          const res = response.data
          if (res.error_no === 0) {
            this.roleData = res.result.roles
          } else {
            this.$message.error(res.error_msg)
          }
        })
          .catch(error => {
            this.$message.error('服务器傲娇了，请刷新重试')
          })
      },
      // 获取当前用户绑定的角色
      getUserRole () {
        const getParams = {
          user_id: this.userId
        }
        getUserRoles(getParams).then(response => {
          const res = response.data
          if (res.error_no === 0) {
            // 判断是否有用户信息
            if (res.result.user_info) {
              this.formData.email = res.result.user_info.email.replace('@ele.me', '')
              this.formData.userType = res.result.user_info.type
              this.formData.phone = res.result.user_info.phone
              this.formData.name = res.result.user_info.real_name
              let rolesRes = res.result.user_roles.granted
              for (let i = 0; i < rolesRes.length; i++) {
                this.formData.role_ids.push(rolesRes[i].role_id)
                this.userRole.push(rolesRes[i].role_name)
              }
            }
          } else {
            this.$message.error(res.error_msg)
          }
        })
          .catch(error => {
            this.$message.error('服务器傲娇了，请刷新重试')
          })
      },
      // 选中邮箱时候回调
      selectEmail (data) {
        data =JSON.parse(data.info)
        this.formData.name = data.name
        this.formData.phone = data.mobile
      },
      changeUserType () {
        this.formData.name = ''
        this.formData.phone = ''
        this.formData.email = ''
      },
      // 提交post
      onSubmit () {
        // 判断是新建还是编辑
        if (this.userId) {
          const postData = {
            user_id: this.userId,
            role_ids: this.formData.role_ids.join(',')
          }
          modifyUserRoles(postData).then(response => {
            const res = response.data
            if (res.error_no === 0) {
              this.$message.success('修改成功!')
              this.$router.push({ path: '/privilegeManage'})
            } else {
              this.$message.error(res.error_msg)
            }
          })
            .catch(error => {
              this.$message.error('服务器傲娇了，请刷新重试')
            })
        } else {
          const postData = {
            email: this.formData.email,
            role_ids: this.formData.role_ids.join(','),
            phone: this.formData.phone,
            type: this.formData.userType
          }
          createUser(postData).then(response => {
            const res = response.data
            if (res.error_no === 0) {
              this.$message.success('新建成功!')
              this.$router.push({ path: '/privilegeManage'})
            } else {
              this.$message.error(res.error_msg)
            }
          })
            .catch(error => {
              this.$message.error('服务器傲娇了，请刷新重试')
            })
        }
      }
    },
    mounted () {
      this.getRoleData()
      // 判断是新建还是编辑
      if (this.userId) {
        this.getUserRole()
      }
    }
  }
</script>

<style lang="scss">
  .user-edit{

    .el-form{
      background-color: #fff;
      padding: 20px;

      .el-form-item__content{
        width: auto;

        .el-input{
          width: 310px;
          margin-right: 15px;
        }

        .expandingArea{
          position: relative;
        }

        .el-checkbox{
          margin: 0 30px 0 0;
        }

        .el-textarea{
          width: 385px;
          position: absolute;
          top: 0;
          left: 0;
          height: 100%;

          textarea{
            height: 100%;
            min-height: 40px!important;
          }
        }

        .hidden-text{
          display: block;
          visibility: hidden;
          width: 385px;
          padding: 6px 16px;
          line-height: 1.5;
        }
      }
    }
  }
</style>




