<template>
  <!--权限集合-->
  <div class="role-manage">
    <el-button
            type="primary"
            @click="showDialog('create')"
            icon="el-icon-plus">
      <span>新建权限集合</span>
    </el-button>
    <el-table
            :data="tableData"
            style="width: 100%">
      <el-table-column
              prop="set_id"
              label="NO.">
      </el-table-column>
      <el-table-column
              prop="set_name"
              label="权限集合名称">
      </el-table-column>
      <el-table-column
              prop="content"
              label="描述">
      </el-table-column>
      <el-table-column
              label="操作">
        <template slot-scope="scope">
          <el-button
                  @click.native.prevent="showDialog('edit', scope.row)"
                  type="text"
                  size="small">
            编辑
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
            @size-change="handleSizeChange"
            @current-change="changePage"
            :current-page.sync="page"
            :page-sizes="[20, 50, 100, 200]"
            :page-size="pageSize"
            layout="total, sizes, prev, pager, next"
            prev-text="上一页"
            next-text="下一页"
            :total="totalCount">
    </el-pagination>
    <el-dialog
            :title="dialogTitle"
            :visible.sync="show"
            width="30%">
      <el-form
              ref="createPermissionSet"
              label-width="110px"
              :model="formParams"
              :rules="rules">
        <el-form-item
                class="is-required"
                label="权限集合名称"
                prop="permissionSetName">
          <el-input
                  v-model="formParams.permissionSetName">
          </el-input>
        </el-form-item>
        <el-form-item label="描述">
          <el-input
                  v-model="formParams.permissionSetContent">
          </el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button
                @click="show = false">
          取 消
        </el-button>
        <el-button
                v-show="dialogTitle === '新建权限集合'"
                type="primary"
                @click="save('create')">
          确 定
        </el-button>
        <el-button
                v-show="dialogTitle === '编辑权限集合'"
                type="primary"
                @click="save('edit')">
          确 定
        </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
  import { getPermissionSetList, createPermissionSet, updatePermissionSet } from '../../api/api'

  export default {
    data () {
      return {
        // table所有数据
        tableData: [],
        // 当前翻页数
        page: 1,
        // 总页数
        totalPage: 3,
        // table总条数
        totalCount: 1,
        // 每页显示条数
        pageSize: 20,
        show: false,
        // 操作（新建 or 编辑）
        operation: '',
        // 弹出框标题
        dialogTitle: '',
        formParams: {
          // 权限集合名称
          permissionSetName: '',
          // 描述信息
          permissionSetContent: '',
          // 权限集合id
          permissionSetId: '',
        },
        rules: {
          permissionSetName: [{
            required: true, message: '权限集合名称不能为空'
          }]
        }
      }
    },
    mounted () {
      this.getPermissionSetList(1, 20)
    },
    methods: {
      /**
       * 获取权限列表
       * @param {Object} page 当前页
       * @param {Object} page 当前页
       */
      getPermissionSetList (page, pageSize) {
        this.page = page
        this.pageSize = pageSize
        let params = {
          set_name: '',
          page_size: pageSize,
          page_num: this.page
        }
        getPermissionSetList(params).then(res => {
          if (res.data.error_no === 0) {
            this.tableData = res.data.result.permission_sets
            this.totalCount = parseInt(res.data.result.total_count)
          } else {
            this.$message.error(res.data.error_msg)
          }
        })
          .catch(error => {
            this.$message.error('服务器傲娇了，请刷新重试')
          })
      },
      changePage (val) {
        this.getPermissionSetList(val, this.pageSize)
      },
      handleSizeChange (val) {
        this.getPermissionSetList(1, val)
      },
      /**
       * 新建、编辑权限集合
       * @param {String} operation 操作（编辑 or 删除）
       * @param {Object} data
       */
      showDialog (operation, data = {}) {
        this.show = true
        if (operation ===  'create') {
          this.$nextTick(() => {
            this.$refs['createPermissionSet'].resetFields()
            this.formParams.permissionSetName = ''
            this.formParams.permissionSetContent = ''
            this.formParams.permissionSetId = ''
          })
          this.dialogTitle = '新建权限集合'
        } else {
          this.formParams.permissionSetName = data.set_name || ''
          this.formParams.permissionSetContent = data.content || ''
          this.formParams.permissionSetId = data.set_id || ''
          this.dialogTitle = '编辑权限集合'
        }
      },
      /**
       * 保存
       * @param {String} operation 操作（编辑 or 删除）
       */
      save (operation) {
        this.formParams.permissionSetName = this.formParams.permissionSetName.trim()
        this.$refs['createPermissionSet'].validate((valid) => {
          if (valid) {
            let params = {
              set_name: this.formParams.permissionSetName.trim(),
              content: this.formParams.permissionSetContent.trim()
            }
            if (operation === 'create') {
              createPermissionSet(params).then(res => {
                if (res.data.error_no === 0) {
                  this.$message.success('保存成功')
                  this.getPermissionSetList(1, this.pageSize)
                  this.show = false
                } else {
                  this.$message.error(res.data.error_msg)
                }
              })
                .catch(error => {
                  this.$message.error('服务器傲娇了，请刷新重试')
                })
            } else {
              params.set_id = this.formParams.permissionSetId.trim()
              updatePermissionSet(params).then(res => {
                if (res.data.error_no === 0) {
                  this.$message.success('保存成功')
                  this.getPermissionSetList(this.page, this.pageSize)
                  this.show = false
                } else {
                  this.$message.error(res.data.error_msg)
                }
              })
                .catch((error) => {
                  this.$message.error(res.data.error_msg)
                })
            }
          } else {
            return false
          }
        })
      }
    }
  }
</script>



