<template>
  <div class="role-manage">
    <el-button
            type="primary"
            @click="createPermission"
            icon="el-icon-plus">
      新建权限
    </el-button>
    <el-table
            :data="tableData"
            style="width: 100%">
      <el-table-column
              prop="permission_id"
              label="NO.">
      </el-table-column>
      <el-table-column
              prop="permission_name"
              label="权限名称">
      </el-table-column>
      <el-table-column
              prop="set_name"
              label="权限集合">
      </el-table-column>
      <el-table-column
              label="操作">
        <template slot-scope="scope">
          <el-button
                  @click.native.prevent="editPermission(scope.row.permission_id)"
                  type="text"
                  size="small">
            编辑
          </el-button>
          <el-button
                  @click.native.prevent="changeStatus(scope.row)"
                  type="text"
                  size="small">
            {{scope.row.enabled == '1' ? '禁用' : '启用'}}
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
            @size-change="handleSizeChange"
            @current-change="changePage"
            :current-page.sync="page"
            :page-sizes="[20, 50, 100, 200]"
            :page-size="pageSize"
            layout="total, sizes, prev, pager, next"
            prev-text="上一页"
            next-text="下一页"
            :total="totalCount">
    </el-pagination>
  </div>
</template>

<script>
  import { getPermissionList, updatePermission } from '../../api/api'

  export default {
    data () {
      return {
        // table所有数据
        tableData: [],
        // 当前翻页数
        page: 1,
        // 总页数
        totalPage: 3,
        // table总条数
        totalCount: 1,
        // 每页显示条数
        pageSize: 20
      }
    },
    mounted () {
      this.getPermissionList(this.page, this.pageSize)
    },
    methods: {
      /**
       * 新建权限
       */
      createPermission () {
        this.$router.push({path: '/createPermission'})
      },
      /**
       * 获取权限列表
       * @param {Number} page 页码
       * @param {Number} pageSize 每页显示条数
       */
      getPermissionList (page, pageSize) {
        this.page = page
        this.pageSize = pageSize
        let params = {
          permission_name: '',
          page_size: pageSize,
          page_num: page
        }
        getPermissionList(params).then(res => {
          if (res.data.error_no === 0) {
            this.tableData = res.data.result.permissions
            this.totalCount = parseInt(res.data.result.total_count)
          } else {
            this.$message.error(res.data.error_msg)
          }
        })
        .catch(error => {
          this.$message.error('服务器傲娇了，请刷新重试')
        })
      },
      changePage (val) {
        this.getPermissionList(val, this.pageSize)
      },
      handleSizeChange (val) {
        this.getPermissionList(1, val)
      },
      /**
       * 编辑权限
       * @param {String} id 权限id
       */
      editPermission (id) {
        this.$router.push({path: '/editPermission', query: {permission_id: id}})
      },
      /**
       * 禁用启用状态切换
       * @param {Object} data
       */
      changeStatus (data) {
        const conformText = data.enabled == '1' ? '禁用' : '启用'
        const enableStatus = data.enabled == '1' ? 2 : 1
        // 弹窗提示
        this.$confirm('确定' + conformText + '权限?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          // 修改权限状态
          const postData = {
            permission_id: data.permission_id,
            enable: enableStatus
          }
          updatePermission(postData).then(response => {
            const res = response.data
            if (res.error_no === 0) {
              this.$message.success(conformText + '成功!')
              this.getPermissionList(this.page, this.pageSize)
            } else {
              this.$message.error(res.error_msg)
            }
          })
            .catch(error => {
              this.$message.error('服务器傲娇了，请刷新重试')
            })
        })
          .catch(() => {
          })
      }
    }
  }
</script>



