<template>
  <!--菜单列表-->
  <div class="role-manage">
    <el-button
            type="primary"
            @click.native="showDialog('create')"
            icon="el-icon-plus">
      新建菜单
    </el-button>
    <el-table
            :data="tableData"
            style="width: 100%">
      <el-table-column
              prop="menu_id"
              label="NO.">
      </el-table-column>
      <el-table-column
              prop="menu_name"
              label="菜单名称">
      </el-table-column>
      <el-table-column
              prop="url"
              label="菜单编码">
      </el-table-column>
      <el-table-column
              prop="content"
              label="描述">
      </el-table-column>
      <el-table-column
              label="操作">
        <template slot-scope="scope">
          <el-button
                  @click.native.prevent="showDialog('edit', scope.row)"
                  type="text"
                  size="small">
            编辑
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
            @size-change="handleSizeChange"
            @current-change="changePage"
            :current-page.sync="page"
            :page-sizes="[20, 50, 100, 200]"
            :page-size="pageSize"
            layout="total, sizes, prev, pager, next"
            prev-text="上一页"
            next-text="下一页"
            :total="totalCount">
    </el-pagination>
    <el-dialog
            :title="dialogTitle"
            :visible.sync="show"
            width="30%">
      <el-form
              ref="menu"
              :model="formParams"
              :rules="rules"
              label-width="100px">
        <el-form-item
                prop="menuName"
                class="is-required"
                label="菜单名称">
          <el-input v-model="formParams.menuName"></el-input>
        </el-form-item>
        <el-form-item
                class="is-required"
                prop="menuCode"
                label="菜单编码">
          <el-input v-model="formParams.menuCode"></el-input>
        </el-form-item>
        <el-form-item label="描述">
          <el-input v-model="formParams.menuContent"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="show = false">取 消</el-button>
        <el-button
                v-show="dialogTitle === '新建菜单'"
                type="primary"
                @click="save('create')">
          确 定
        </el-button>
        <el-button
                v-show="dialogTitle === '编辑菜单'"
                type="primary"
                @click="save('edit')">
          确 定
        </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
  import { getMenuList, createMenu, updateMenu } from '../../api/api'
  export default {
    data () {
      return {
        // table所有数据
        tableData: [],
        // 当前翻页数
        page: 1,
        // 总页数
        totalPage: 3,
        // table总条数
        totalCount: 1,
        // 每页显示的条数
        pageSize: 20,
        dialogTitle: '',
        show: false,
        formParams: {
          menuId: '',
          // 菜单名称
          menuName: '',
          // 菜单编码
          menuCode: '',
          // 描述
          menuContent: ''
        },
        rules: {
          menuName: [{
            required: true, message: '菜单名称不能为空'
          }],
          menuCode: [{
            required: true, message: '菜单编码不能为空'
          }]
        }
      }
    },
    mounted () {
      this.getMenuList(1, 20)
    },
    methods: {
      /**
       * 获取菜单列表
       * @param {Number} page 页码
       * @param {Number} pageSize 每页显示多少条
       */
      getMenuList (page, pageSize) {
        let params = {
          page_size: pageSize,
          page_num: page
        }
        this.page = page
        this.pageSize = pageSize
        getMenuList(params).then(res => {
          if (res.data.error_no === 0) {
            this.tableData = res.data.result.menus
            this.totalCount = parseInt(res.data.result.total_count)
          } else {
            this.$message.error(res.data.error_msg)
          }
        })
          .catch(error => {
            this.$message.error('服务器傲娇了，请刷新重试')
          })
      },
      changePage (val) {
        this.getMenuList(val, this.pageSize)
      },
      handleSizeChange (val) {
        this.getMenuList(1, val)
      },
      /**
       * 新建、编辑菜单
       * @param {String} operation 操作（编辑 or 删除）
       * @param {Object} data
       */
      showDialog (operation, data = {}) {
        this.show = true
        if (operation ===  'create') {
          this.$nextTick(() => {
            this.$refs['menu'].resetFields()
            this.formParams.menuName = ''
            this.formParams.menuCode = ''
            this.formParams.menuContent = ''
            this.formParams.menuId = ''
          })
          this.dialogTitle = '新建菜单'
        } else {
          this.formParams.menuName = data.menu_name || ''
          this.formParams.menuCode = data.url || ''
          this.formParams.menuContent = data.content || ''
          this.formParams.menuId = data.menu_id || ''
          this.dialogTitle = '编辑菜单'
        }
      },
      /**
       * 保存
       * @param {String} operation 操作（编辑 or 删除）
       */
      save (operation) {
        this.formParams.menuName = this.formParams.menuName.trim()
        this.formParams.menuCode = this.formParams.menuCode.trim()
        this.$refs['menu'].validate((valid) => {
          if (valid) {
            let params = {
              menu_name: this.formParams.menuName,
              content: this.formParams.menuContent,
              url: this.formParams.menuCode
            }
            if (operation === 'create') {
              createMenu(params).then(res => {
                if (res.data.error_no === 0) {
                  this.$message.success('保存成功')
                  this.getMenuList(1, this.pageSize)
                  this.show = false
                } else {
                  this.$message.error(res.data.error_msg)
                }
              })
                .catch(error => {
                  this.$message.error('服务器傲娇了，请刷新重试')
                })
            } else {
              params.menu_id = this.formParams.menuId
              updateMenu(params).then(res => {
                if (res.data.error_no === 0) {
                  this.$message.success('保存成功')
                  this.show = false
                  this.getMenuList(this.page, this.pageSize)
                } else {
                  this.$message.error(res.data.error_msg)
                }
              })
                .catch((error) => {
                  this.$message.error('服务器傲娇了，请刷新重试')
                })
            }
          }
        })
      }
    }
  }
</script>



