<template>
  <section>
    <el-form :inline='true' :model='filter' class='options label-align-left'>
      <el-form-item label-width='95px' label='城市' class="search-label">
        <el-select v-model="filter.region" clearable placeholder="请选择" label-width='179px'>
          <el-option
            v-for="item in cityList"
            :key="item.city_id"
            :label="item.city_name"
            :value="item.city_id">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label-width='95px' label='供货商ID' class="search-label">
        <el-input v-model='filter.supplyId' placeholder='请输入内容'></el-input>
      </el-form-item>
      <el-form-item label-width='95px' label='供货商名称' class="search-label">
        <el-input v-model='filter.supplyName' placeholder='从首字开始匹配'></el-input>
      </el-form-item>
      <el-form-item label-width='95px' label='采购员' class="search-label">
        <el-input v-model='filter.name' placeholder='请输入中文全名'></el-input>
      </el-form-item>
      <el-form-item class='btn' style="margin-left: 135px;">
        <el-button type="primary" @click="this.getList">查询</el-button>
      </el-form-item> 
    </el-form> 

<!-- 供货商显示 -->
    <template>
      <section class="table-area">
      <el-button type="primary" class='createNew' @click='createSupply' v-if="btn.createorupdatevendor">
      <i class="el-icon-plus"></i> 新建供货商
    </el-button>
      <el-table
        :data="tableData"
        v-loading="loading"
        style="width: 100%">
        <el-table-column
          prop="vendor_id"
          label="供货商ID">
        </el-table-column>
        <el-table-column
          prop="vendor_name"
          label="供货商名称">
        </el-table-column>
        <el-table-column
          prop="city_list"
          label="城市">
        </el-table-column>
        <el-table-column
          prop="vendor_buyer_uname"
          label="采购员">
        </el-table-column>
        <el-table-column
          prop="update_time"
          label="更新时间">
        </el-table-column>
        <el-table-column
          prop="vendor_update_uname"
          label="更新人">
        </el-table-column>
        <el-table-column
          label="操作"
          prop="vendor_id">
          <template slot-scope="scope">
            <router-link :to="{ path: '/createBasicInfo',query: { create: scope.row.vendor_id, name: scope.row.vendor_name}}" class="look" v-if="btn.getvendorbyid">查看</router-link>
            <router-link :to="{ path: '/log',query: { vendor_id: scope.row.vendor_id, source: 'vendor'}}" class="rizhi" v-if="btn.getvendorlog">日志</router-link>
          </template>
        </el-table-column>
      </el-table>
    
    <div class="page">
      <el-pagination
        @current-change="changePage"
        @size-change="changeSize"
        :current-page="pageData.page"
        :page-size="pageData.count"
        :page-sizes="[10, 20, 50, 100]"
        layout="total, sizes, prev, pager, next, jumper"
        :total="pageData.total">
      </el-pagination>
    </div>
    </section>
</template>
</section>
</template>
<script>
import { getSupply, getcitylist } from '@/api/api'
import util from '@/common/js/util'
export default {
  data () {
    return {
      btn: {
        createorupdatevendor: util.jdugeShow('vendor-main-createorupdatevendor'),
        getvendorbyid: util.jdugeShow('vendor-main-getvendorbyid'),
        getvendorlog: util.jdugeShow('vendor-main-getvendorlog')
      },
      filter: {
        supplyId: '',
        supplyName: '',
        region: '',
        name: ''
      },
      tableData: [],
      table2Show: false,
      flagSear: false,
      logShow: false,
      loading: false,
      pageData: {
        page: 1,
        total: 0,
        count: 20,
        page_num: 0
      },
      cityList: []
    }
  },
  created () {
    this.getList()
    this.getcitylist()
  },
  methods: {
        // 获取列表
    getList (flagSear = false) {
      let params = ''
      if (flagSear) {
        this.flagSear = true
        if (this.filter.supplyId) {
          params = {
            vendor_id: this.filter.supplyId,
            page_size: this.pageData.count,
            page_num: this.pageData.page
          }
        } else {
          params = {
            vendor_name: this.filter.supplyName,
            city_id: this.filter.region,
            vendor_buyer_uname: this.filter.name,
            page_size: this.pageData.count,
            page_num: this.pageData.page
            // 需要后期修改，去掉分页的必选项
          }
        }
      } else {
        this.flagSear = false
        params = {
          page_num: this.pageData.page,
          page_size: this.pageData.count
          // 需要后期修改，去掉分页的必选项
        }
      }
      // console.log(params);
      this.loading = true
      getSupply(params).then((res) => {
        if (res.data.error_no === 0 && res.data.result.vendor_list && res.data.result.vendor_list.length !== 0) {
          res.data.result.vendor_list.forEach((item) => {
            item.city_list = item.city_list === undefined ? '' : item.city_list.toString().replace(/,/g, '、')
            item.update_time = util.formatTime(item.update_time)
          })
          this.tableData = res.data.result.vendor_list
          this.pageData.total = res.data.result.total
          this.loading = false
        } else {
          this.tableData = []
          this.loading = false
        }
      })
    },
    // 新建供货商跳转
    createSupply () {
      this.$router.push({ path: '/createBasicInfo' })
    },
    changePage (val) {
      // console.log('chang')
      this.pageData.page = val
      this.getList()
    },
    changeSize (val) {
      this.pageData.count = val
      this.getList()
    },
    getcitylist () {
      let params = {
        page_size: this.pageData.count,
        page_num: this.pageData.page
      }
      this.loading = true
      getcitylist(params).then((res) => {
        if (res.data.error_no === 0 && res.data.result.city_list && res.data.result.city_list.length !== 0) {
          this.cityList = res.data.result.city_list
          this.loading = false
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
  section{
    padding: 20px;
    // padding-top: 10px;
    padding-bottom: 0;
    .options{
      padding-top: 20px;
      .btn{
        display: block;
        margin-left: 10%;
      }
    }
    .search-label{
      padding-left:40px;
      text-align: left;
    }
    .createNew{
      margin-bottom: 20px;
    }
    .el-pagination{
      text-align: right;
    }
    .el-form{
      background-color: #fff;
      overflow: hidden;
      margin-bottom: 20px;
    }
    .look{
      text-decoration: none;
      color: #409EFF;
      font-weight: 500;
      font-size: 12px;
    }
    .rizhi{
      text-decoration: none;
      color: #409EFF;
      font-weight: 500;
      font-size: 12px;
      margin-left: 10px;
    }
    .table-area{
      overflow: hidden;
      background-color: #fff;
    }
  }
</style>



