<template>
  <!--新建权限和编辑权限的页面-->
  <div class="create-permission">
    <el-form
            :model="formParams"
            :rules="rules"
            ref="form">
      <el-form-item
              prop="permissionName"
              label="权限名称"
              class="permission-name is-required">
        <el-input v-model="formParams.permissionName"></el-input>
      </el-form-item>
      <el-form-item
              prop="permissionSet"
              label="权限集合"
              class="permission-set is-required">
        <el-select v-model="formParams.permissionSet">
          <el-option
                  v-for="item in permissionSetList"
                  :key="item.set_id"
                  :label="item.set_name"
                  :value="item.set_id">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item
              class="resource"
              label="数据字段">
        <el-row v-if="permissionResource.fields.selected && permissionResource.fields.selected.length > 0">
          <div class="text">已关联</div>
          <el-checkbox-group
                  v-model="dataField.selected">
            <el-checkbox
                    v-for="item in permissionResource.fields.selected || []"
                    :label="item.data_id"
                    :key="item.data_id">
              {{item.data_name}}
            </el-checkbox>
          </el-checkbox-group>
        </el-row>
        <el-row>
          <div class="text">可关联</div>
          <el-checkbox-group
                  v-model="dataField.unselected">
            <el-checkbox
                    v-for="item in permissionResource.fields.unselected || []"
                    :label="item.data_id"
                    :key="item.data_id">
              {{item.data_name}}
            </el-checkbox>
          </el-checkbox-group>
        </el-row>
      </el-form-item>
      <el-form-item
              class="resource"
              label="页面元素">
        <el-row v-if="permissionResource.pageItems.selected && permissionResource.pageItems.selected.length > 0">
          <div class="text">已关联</div>
          <el-checkbox-group
                  v-model="pageItem.selected">
            <el-checkbox
                    v-for="item in permissionResource.pageItems.selected || []"
                    :label="item.page_id"
                    :key="item.page_id">
              {{item.page_name}}
            </el-checkbox>
          </el-checkbox-group>
        </el-row>
        <el-row>
          <div class="text">可关联</div>
          <el-checkbox-group
                  v-model="pageItem.unselected">
            <el-checkbox
                    v-for="item in permissionResource.pageItems.unselected || []"
                    :label="item.page_id"
                    :key="item.page_id">
              {{item.page_name}}
            </el-checkbox>
          </el-checkbox-group>
        </el-row>
      </el-form-item>
      <el-form-item
              class="resource"
              label="接口">
        <el-row v-if="permissionResource.operations.selected && permissionResource.operations.selected.length > 0">
          <div class="text">已关联</div>
          <el-checkbox-group
                  v-model="operation.selected">
            <el-checkbox
                    v-for="item in permissionResource.operations.selected || []"
                    :label="item.operation_id"
                    :key="item.operation_id">
              {{item.operation_name}}
            </el-checkbox>
          </el-checkbox-group>
        </el-row>
        <el-row>
          <div class="text">可关联</div>
          <el-checkbox-group
                  v-model="operation.unselected">
            <el-checkbox
                    v-for="item in permissionResource.operations.unselected || []"
                    :label="item.operation_id"
                    :key="item.operation_id">
              {{item.operation_name}}
            </el-checkbox>
          </el-checkbox-group>
        </el-row>
      </el-form-item>
      <el-form-item
              class="resource"
              label="菜单">
        <el-row v-if="permissionResource.menus.selected && permissionResource.menus.selected.length > 0">
          <div class="text">已关联</div>
          <el-checkbox-group
                  v-model="menu.selected">
            <el-checkbox
                    v-for="item in permissionResource.menus.selected || []"
                    :label="item.menu_id"
                    :key="item.menu_id">
              {{item.menu_name}}
            </el-checkbox>
          </el-checkbox-group>
        </el-row>
        <el-row>
          <div class="text">可关联</div>
          <el-checkbox-group
                  v-model="menu.unselected">
            <el-checkbox
                    v-for="item in permissionResource.menus.unselected || []"
                    :label="item.menu_id"
                    :key="item.menu_id">
              {{item.menu_name}}
            </el-checkbox>
          </el-checkbox-group>
        </el-row>
      </el-form-item>
      <el-form-item>
      </el-form-item>
    </el-form>
    <div style="text-align: center">
      <el-button type="primary"
                 v-if="!permissionId"
                 @click="save('create')">
        保存
      </el-button>
      <el-button type="primary"
                 v-if="permissionId"
                 @click="save('edit')">
        保存
      </el-button>
      <el-button
              @click="linkToList">
        取消
      </el-button>
    </div>
  </div>
</template>
<script>
  import { getPermissionResource, getPermissionSetList, createPermission, editPermission } from '../../api/api'
  export default {
    data () {
      return {
        // 编辑时的权限id
        permissionId: this.$route.query.permission_id ? this.$route.query.permission_id : '',
        permissionResource: {
          fields: {},
          menus: {},
          operations: {},
          pageItems: {}
        },
        permissionSetList: [],
        formParams: {
          // 权限名称
          permissionName: '',
          // 权限集合
          permissionSet: ''
        },
        rules: {
          permissionName: [{
            required: true, message: '权限名称不能为空', trigger: 'blur'
          }],
          permissionSet: [{
            required: true, message: '请选择权限集合', trigger: 'change'
          }]
        },
        // 数据字段
        dataField: {
          // 已关联
          selected: [],
          // 可关联
          unselected: []
        },
        // 页面元素
        pageItem: {
          selected: [],
          unselected: []
        },
        // 接口
        operation: {
          selected: [],
          unselected: []
        },
        // 菜单
        menu: {
          selected: [],
          unselected: []
        }
      }
    },
    mounted () {
      this.getPermissionResource('')
      this.getPermissionSetList()
    },
    methods: {
      /**
       * 获取权限资源关联
       * @param {String} permissionId 权限id
       */
      getPermissionResource (permissionId) {
        let params = {
          permission_id: this.permissionId
        }
        getPermissionResource(params).then(res => {
          if (res && res.data && res.data.error_no === 0) {
            let permission_resource = res.data.result.permission_resource
            this.permissionResource.fields = permission_resource.fields
            this.permissionResource.menus = permission_resource.menus
            this.permissionResource.operations = permission_resource.operations
            this.permissionResource.pageItems = permission_resource.page_items
            // 编辑权限的时候回显默认的数据
            if (this.permissionId) {
              permission_resource.fields.selected.forEach(item => {
                this.dataField.selected.push(item.data_id)
              })
              permission_resource.menus.selected.forEach(item => {
                this.menu.selected.push(item.menu_id)
              })
              permission_resource.operations.selected.forEach(item => {
                this.operation.selected.push(item.operation_id)
              })
              permission_resource.page_items.selected.forEach(item => {
                this.pageItem.selected.push(item.page_id)
              })
              if (permission_resource.permission.length > 0) {
                this.formParams.permissionName = permission_resource.permission[0].permission_name
                this.formParams.permissionSet = permission_resource.permission[0].set_id
              }
            }
          } else {
            this.$message.error(res.data.error_msg)
          }
        })
          .catch(error => {
            this.$message.error('服务器傲娇了，请刷新重试')
          })
      },
      /**
       * 获取权限列表
       */
      getPermissionSetList () {
        let params = {
          set_name: '',
          page_size: 20,
          page_num: 1
        }
        getPermissionSetList(params).then(response => {
          let res = response.data
          if (res && res.result && res.error_no === 0) {
            this.permissionSetList = res.result.permission_sets
          } else {
            this.$message.error(res.error_msg)
          }
        })
        .catch(error => {
          this.$message.error('服务器傲娇了，请刷新重试')
        })
      },
      /**
       * 保存
       * @param {String} operation 操作（编辑 or 删除）
       */
      save (operation) {
        this.$refs['form'].validate((valid) => {
          if (valid) {
            let params = {
              permission_name: this.formParams.permissionName,
              set_id: this.formParams.permissionSet,
              operation_ids: this.operation.unselected.concat(this.operation.selected).join(','),
              menu_ids: this.menu.unselected.concat(this.menu.selected).join(',')
            }
            // 新建权限
            if (operation === 'create') {
              params.page_ids = this.pageItem.unselected.concat(this.pageItem.selected).join(',')
              params.data_ids = this.dataField.unselected.concat(this.dataField.selected).join(',')
              createPermission(params).then(res => {
                if (res.data.error_no === 0) {
                  this.$message.success('保存成功')
                  this.$router.push({ path: '/developerTools'})
                } else {
                  this.$message.error(res.data.error_msg)
                }
              })
                .catch(error => {
                  this.$message.error('服务器傲娇了，请刷新重试')
                })
            } else {
              params.data_field_ids = this.dataField.unselected.concat(this.dataField.selected).join(',')
              params.page_item_ids = this.pageItem.unselected.concat(this.pageItem.selected).join(',')
              params.permission_id = this.permissionId
              editPermission(params).then(res => {
                if (res.data.error_no === 0) {
                  this.$message.success('保存成功')
                  this.$router.push({ path: '/developerTools'})
                } else {
                  this.$message.error(res.data.error_msg)
                }
              })
                .catch(error => {
                  this.$message.error('服务器傲娇了，请刷新重试')
                })
            }
          }
        })
      },
      linkToList () {
        this.$router.push({ path: '/developerTools'})
      }
    }
  }
</script>
<style lang="scss">
  .create-permission {
    .permission-name {
      margin-right: 20px;
    }
    .permission-name, .permission-set {
      .el-form-item__content {
        margin-left: 78px;
      }
    }

    .resource {
      margin-bottom: 0;
      .el-form-item__label {
        float: none;
      }
      .el-form-item__content {
        width: auto;
      }
    }

    .el-form-item__label {
      font-weight: bold;
    }
  }
</style>
<style lang="scss" scoped>
  .create-permission {
    background: #fff;
    padding: 20px 0;

    .permission-name {
      padding-left: 20px;
    }

    .permission-name,
    .permission-set {
      display: inline-block;
    }

    .resource {
      padding-left: 20px;
      border-bottom: 1px solid #ccc;
      .el-row {
        margin-left: 2em;
        display: flex;
        div.text {
          color: #5a5e66;
          width: 45px;
          margin-right: 20px;
        }
      }
      .el-checkbox-group {
        flex: 1;
        .el-checkbox {
          margin-left: 0;
          margin-right: 30px;
        }
      }
    }
  }

</style>




