import {
  getPurchaseList,
  searchVendor,
  wfauditcommite,
  deletePurchaseOrder,
  cancelPurchaseOrder,
  getpurchaseorderstatistics,
  createRepostTask
} from '../../../../api/api'
import util from '../../../../common/js/util'
import citywarehouse from '@/components/citywarehouse/citywarehouse.vue'

/**
 * 交换一审、 二审的位置
 * @param {rd传过来的tabs名称列表，所有权限类型都是固定的一个列表} arr
 */
const swapTab = (arr) => {
  if (Array.isArray(arr) && arr[3] && arr[4]) {
    const temp = arr[3]
    arr[3] = arr[4]
    arr[4] = temp
  }
}

export default {
  data () {
    return {
      btn: {
        createpurchaseorder: util.jdugeShow('order-purchase-createpurchaseorder'),
        updatepurchaseorder: util.jdugeShow('order-purchase-updatepurchaseorder'), // 编辑
        wfauditcommite: util.jdugeShow('order-purchase-wfauditcommite'), // 提交审核
        deletepurchaseorder: util.jdugeShow('order-purchase-deletepurchaseorder'), // 删除
        confirmpurchaseorder: util.jdugeShow('order-purchase-confirmpurchaseorder'), // 供货商确定
        cancelpurchaseorder: util.jdugeShow('order-purchase-cancelpurchaseorder'), // 作废
        getpurchaseorderinfo: util.jdugeShow('order-purchase-getpurchaseorderinfo'), // 查看详情
        getloglistbyid: util.jdugeShow('order-purchase-getloglistbyid'),  // 日志
        orderexport: util.jdugeShow('purchase-order-export'), // 导出
        exportread: util.jdugeShow('export-read') // 导出查看
      },
      isSupplier: util.isSupplier(),
      // todo:全部不传当前状态
      currentStatus: 0,
      statusTab: [],
      params: {},
      // 采购单查询条件
      filter: {
        po_id: '',
        arrival_id: '',
        inbound_id: '',
        vendor_name: '',
        po_contact_name: '',
        create_time_begin: '',
        create_time_end: '',
        city_id: ''
        // warehouse_id: ''
      },
      // 采购单列表展示
      list: {
        purchaseTabel: [],
        currentPage: 1,
        currentPageSize: 20,
        total: 0
      },
      dialog: {
        dialogVisible: false,
        dialogText: '',
        id: '',
        type: ''
      },
      // todo: 合并到common库
      po_status: {
        10: '新建待提交',
        20: '审核中',
        21: '待一审',
        22: '待二审',
        30: '二审通过',
        31: '一审通过',
        32: '二审通过',
        40: '审核驳回',
        41: '一审驳回',
        42: '二审驳回',
        50: '供货商确认',
        60: '已入库',
        90: '已作废'
      }
    }
  },
  created () {
    this.getTabStatus()
    this.getPurchaseList()
  },
  methods: {
    handleCitywarehouse (val) {
      this.filter.city_id = val
    },
    getTabStatus () {
      getpurchaseorderstatistics().then((res) => {
        if (res && res.data && res.data.error_no === 0) {
          let list = res.data.result && res.data.result.list
          let tempArr = []
          for (let i in list) {
            if (list[i].po_status !== 41 && list[i].po_status !== 42) {
              tempArr.push(list[i])
            }
          }
          tempArr.unshift({'po_status': 0, 'po_status_show': '全部', 'count': res.data.result.total})
          swapTab(tempArr) // 让tabs中 一审tab的位置在二审tab前面
          this.statusTab = tempArr
        }
      })
    },
    getPurchaseList (param) {
      let params = {}
      if (!param) {
        if (this.currentStatus) {
          params.po_status = this.currentStatus
          if (this.currentStatus === '0') {
            params.po_status = ''
          }
        }
        params.page_size = this.list.currentPageSize
      } else {
        params = param
      }
      if (this.filter.city_id) {
        params.city_id = this.filter.city_id
      }
      this.params = params
      getPurchaseList(params).then((res) => {
        if (res.data.error_no === 0) {
          this.list.purchaseTabel = res.data.result && res.data.result.detail
          this.list.total = res.data.result && res.data.result.total
          this.gettableTime()
        } else {
          this.$message.error(res.data.error_msg || '查询出错，试试刷新页面')
        }
      })
    },
    gettableTime () {
      this.list.purchaseTabel.forEach((val, index, array) => {
        val.create_time = util.formatTime(val.create_time)
        val.total_price_tax = val.total_price_tax / 100
      })
    },
    handleSelectCity () {
      this.getWarehouselist()
    },
    tabChange (tab, event) {
      this.currentStatus = tab.name
      this.params = {}
      this.params.po_status = tab.name
      this.params.page_size = this.list.currentPageSize
      this.filter.po_id = ''
      this.filter.arrival_id = ''
      this.filter.inbound_id = ''
      this.filter.vendor_name = ''
      this.filter.po_contact_name = ''
      this.filter.create_time_begin = ''
      this.filter.create_time_end = ''
      this.getPurchaseList(this.params)
    },
    querySupplier (queryString, callback) {
      let params = {}
      params.vendor_name = queryString
      searchVendor(params).then((res) => {
        let vendorArr = []
        let result = res.data.result ? res.data.result : []
        for (let i = 0, len = result.length; i < len; i++) {
          vendorArr[i] = {}
          vendorArr[i].value = result[i].vendor_name
          vendorArr[i].vendor_id = result[i].vendor_id
        }
        callback(vendorArr)
      })
    },
    search () {
      let params = {}
      if (this.filter.po_id) {
        params.po_id = this.filter.po_id
      }
      if (this.filter.inbound_id) {
        params.inbound_id = this.filter.inbound_id
      }
      if (this.filter.arrival_id) {
        params.arrival_id = this.filter.arrival_id
      }
      if (this.filter.vendor_name) {
        params.vendor_name = this.filter.vendor_name
      }
      if (this.filter.po_contact_name) {
        params.po_contact_name = this.filter.po_contact_name
      }
      if (this.filter.create_time_begin && this.filter.create_time_end) {
        params.create_time_begin = parseInt(util.getTimeStamp(this.filter.create_time_begin))
        params.create_time_end = parseInt(util.getTimeStamp(this.filter.create_time_end))
      }
      // todo: 数据到底怎么传，在考虑
      if (this.currentStatus) {
        params.po_status = this.currentStatus
        if (this.currentStatus === '0') {
          params.po_status = ''
        }
      }
      params.page_size = this.list.currentPageSize
      this.getPurchaseList(params)
    },
    doOption () {
      let params = {}
      params.po_id = this.dialog.id
      this.dialog.dialogVisible = false
      if (this.dialog.type === 'commit') {
        wfauditcommite(params).then((res) => {
          if (res.data.error_no === 0) {
            this.getPurchaseList(this.params)
          } else {
            this.$message.error(res.data.error_msg || '请求数据出错，试试刷新页面')
          }
        })
      } else if (this.dialog.type === 'del') {
        deletePurchaseOrder(params).then((res) => {
          if (res.data.error_no === 0) {
            this.getPurchaseList(this.params)
          } else {
            this.$message.error(res.data.error_msg || '请求数据出错，试试刷新页面')
          }
        })
      } else {
        cancelPurchaseOrder(params).then((res) => {
          if (res.data.error_no === 0) {
            this.getPurchaseList(this.params)
          } else {
            this.$message.error(res.data.error_msg || '请求数据出错，试试刷新页面')
          }
        })
      }
    },
    commitPurchase (id) {
      this.dialog.dialogVisible = true
      this.dialog.dialogText = '是否提交该采购单'
      this.dialog.type = 'commit'
      this.dialog.id = id
    },
    delPurchase (id) {
      this.dialog.dialogVisible = true
      this.dialog.dialogText = '是否删除该采购单'
      this.dialog.type = 'del'
      this.dialog.id = id
    },
    destroyPurchase (id) {
      this.dialog.dialogVisible = true
      this.dialog.dialogText = '是否作废该采购单'
      this.dialog.type = 'destroy'
      this.dialog.id = id
    },
    handleCurrentChange (val) {
      this.list.currentPage = val
      // todo: 防止数据污染，深拷贝params
      this.params.page_num = val
      this.getPurchaseList(this.params)
    },
    handleSizeChange (val) {
      this.list.currentPageSize = val
      // todo: 防止数据污染，深拷贝params
      this.params.page_size = val
      this.getPurchaseList(this.params)
    },
    exportResult () {
      this.$confirm('是否要导出当前所有查询结果?', '', {
        confirmButtonText: '确定',
        cancelButtonText: '取消'
      }).then(() => {
        let params = {}
        params.task_type = 12
        params.app_id = 2
        params.query_parameters = this.params
        for (let key in params.query_parameters) {
          if (!params.query_parameters[key]) {
            delete params.query_parameters[key]
          }
        }
        createRepostTask(params).then((res) => {
          if (res && res.data && res.data.error_no === 0) {
            this.$message.success('任务已加入导出任务管理列表，请前往查看')
          } else {
            this.$message.error(res.data.error_msg || '导出失败，请稍候重试')
          }
        })
      }).catch(() => {})
    }
  },
  components: {
    'city-warehouse': citywarehouse
  }
}



