            <el-radio :label="3">二级经销商</el-radio>
            <el-radio :label="4">三级经销商</el-radio>
            <el-radio :label="5">其他</el-radio>
          </el-radio-group>
      </el-form-item>
      <el-form-item label="公司性质" label-width='123px' class="is-required">
        <el-radio-group v-model="form.radio3" class="w475">
