          <el-radio :label="5">其他</el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="增值纳税人身份" label-width='123px' class="is-required">
        <el-radio-group v-model="form.radio4" class="w475">
          <el-radio :label="1">一般纳税人</el-radio>
          <el-radio :label="2">小规模纳税人</el-radio>
        </el-radio-group>
      </el-form-item>
        <h2 class="classify-1">联系方式</h2>
      <el-form-item label="姓名" label-width='123px' prop="name2">
        <el-input v-model="form.name2" placeholder="请输入内容" style="width: 380px;"></el-input>
      </el-form-item>
      <el-form-item label="地址" label-width='123px' prop='desc2'>
        <el-input v-model="form.desc2" placeholder="请输入内容" class="i380"></el-input>
      </el-form-item>
      <el-form-item label="电话" label-width='123px' prop='tel2'>
        <el-input v-model="form.tel2" placeholder='仅支持"数字"、"/"、"-"' class="i380"></el-input>
      </el-form-item>
      <el-form-item label="邮箱" label-width='123px' prop="email2">
        <el-input v-model="form.email2" placeholder="请输入邮箱" class="i380"></el-input>
      </el-form-item>
        <!-- 结算信息 -->
        <h2 class="classify-1">结算信息</h2>
      <el-form-item label="结算周期" label-width='123px' class="is-required">
        <el-radio-group v-model="form.calculation" class="w475">
          <el-radio :label="1">月结</el-radio>
          <el-radio :label="2">日结</el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="账期" label-width='123px' prop="deadline">
        <el-input v-model="form.deadline" placeholder="请输入整数" style="width: 120px;"></el-input>
        <span class="quantity"> 天</span>
      </el-form-item>
      <el-form-item label="结算人" label-width='123px' prop="name3">
        <el-input v-model="form.name3" placeholder="请输入姓名" class="i380"></el-input>
      </el-form-item>
      <el-form-item label="结算人电话" label-width='123px' prop="tel3">
        <el-input v-model="form.tel3" placeholder='仅支持"数字"、"/"、"-"' class="i380"></el-input>
      </el-form-item>
      <el-form-item label="结算人邮箱" label-width='123px' prop="email3">
        <el-input v-model="form.email3" placeholder="请输入邮箱" class="i380"></el-input>
      </el-form-item>
        <el-form-item label="银行名称" label-width='123px' prop="bank">
          <el-select v-model="form.bank" clearable placeholder="请选择银行">
              <el-option
                v-for="item in form.bankList"
                :key="item.bank_name"
                :label="item.bank_name"
                :value="item.bank_id">
              </el-option>
          </el-select>
      </el-form-item>
      <el-form-item label="开户行" label-width='123px' prop="bankName">
        <el-input v-model="form.bankName" placeholder="请输入开户行名称" class="i380"></el-input>
      </el-form-item>
      <el-form-item label="银行联行号" label-width='116px' class="padding-left-5" prop="num">
        <el-input v-model="form.num" placeholder="请输入银行联行号" class="i380"></el-input>
      </el-form-item>
      <el-form-item label="开户名" label-width='123px' prop="openName">
        <el-input v-model="form.openName" placeholder="请输入开户名" class="i380"></el-input>
      </el-form-item>
      <el-form-item label="银行卡账号" label-width='123px' prop="account">
        <el-input v-model="form.account" placeholder="请输入银行卡账号" class="i380"></el-input>
      </el-form-item>
        <!-- 采购信息 -->
        <h2 class="classify-1">采购信息</h2>
      <el-form-item label="采购员邮箱" label-width='123px' prop="email4" class="w475 is-required">
        <el-autocomplete
          class="inline-input"
          v-model="email4"
          :fetch-suggestions="querySearch"
          placeholder="请输入内容"
          @select="handleSelect"
          :trigger-on-focus="false"
        ></el-autocomplete>
        <span class="quantity"> @ele.me</span>
      </el-form-item>
      <el-form-item label="可送货城市" label-width='123px' class="is-required city  w475">
        <span class="city-name">城市</span>
        <span class="city-money">采购单最低金额</span>
        <span class="city-day">在途天数</span>
      </el-form-item>
      <div v-for="(city, index) in cityList" :key="index">
        <el-form-item label="" label-width='130px' class="child-inline"
          style="margin-right:0;display: inline-block;width: 100px;">
          <el-checkbox v-model="checkout[index]" class="cityWidth">{{city.city_name}}</el-checkbox>
        </el-form-item>
        <el-form-item style="margin-right:0;display: inline-block;">
          <el-input :disabled="!checkout[index]" v-model="cityList[index].city_buy_min_limit"
                    placeholder="请输入数字" class="w120"></el-input>
          <span class="from4-unit"> 元</span>
        </el-form-item>
        <el-form-item style="margin-right:0;display: inline-block;margin-left: 0px;padding-left:0;"
                class="city-input-right">
          <el-input :disabled="!checkout[index]" v-model="cityList[index].city_delivery_max_days"
                    placeholder="请输入数字" class="w120"></el-input>
          <span class="from4-unit"> 天</span>
        </el-form-item>
      </div>
      <el-form-item>
        <el-button type="primary" @click="onSubmit('form')" :disabled="btn.createorupdatevendor">保存</el-button>
      </el-form-item>
    </el-form>
  </section>
</template>

<script>
import qs from 'qs'
import util from '@/common/js/util'
import {getcitylist, getBanklist, createVendor, getCityWarehouse, getEleAccountList} from '@/api/api'
export default {
  data () {
    let validatePass = (rule, value, callback) => {
      if (!this.emailFlag || this.email4 === '') {
        callback(new Error('请输入正确的邮箱账户'))
      } else {
        callback()
      }
    }
    let checkYear = (rule, value, callback) => {
      let val = new Date(value).getFullYear()
      let use = new Date().getFullYear()
      if (value === '') {
        callback(new Error('请选择年份'))
      } else if (val > use) {
        callback(new Error('请选择正确年份'))
      } else {
        callback()
      }
    }
    return {
      btn: {
        createorupdatevendor: !util.jdugeShow('vendor-main-createorupdatevendor')
      },
      activeName: '1',
      activeIndex: '1',
      labelPosition: 'left',
      checkout: [],
      befoeCityList: '',
      cityList: '',
            // 结算人邮箱验证
      emailFlag: true,
      buyer_account: '',
      buyer_uname: '',
      email4: '',
      vendorId: this.$route.query.create,
      form: {
        id: 2222,
        name: '',
        radio1: 1,
        radio2: 1,
        radio3: 1,
        radio4: 1,
        capital: '',
        date1: '',
        grade: '1',
        name2: '',
        tel2: '',
        desc2: '',
        email2: '',
        serial: '',
        calculation: 1,
        deadline: '',
        name3: '',
        tel3: '',
        email3: '',
        bank: '',
        bankList: [],
        bankName: '',
        openName: '',
        num: '',
        account: '',
        name4: '',
        num1: '',
        num2: '',
        day1: '',
        day2: '',
        info: [],
        cityList: []
      },
      rules: {
        name: [
          {required: true, message: '请输入供货商名称', trigger: 'blur'},
          {max: 20, message: '长度最多20个字符'}
        ],
        capital: [
          {
            required: true,
            validator: 'regexp',
            pattern: /^\d+$|(^$)/,
            message: '请输入正整数',
            trigger: 'change,blur'
          }
        ],
        num: [
        {validator: 'regexp', pattern: /^\d+$|(^$)/, message: '请输入数字', trigger: 'change,blur'}
        ],
        date1: [
          {validator: checkYear, trigger: 'change'}
        ],
        name2: [
          {required: true, message: '请输入姓名', trigger: 'blur'},
          {max: 10, message: '长度最多10个字符'}
        ],
        name3: [
          {required: true, message: '请输入姓名', trigger: 'blur'},
          {max: 10, message: '长度最多10个字符'}
        ],
        desc2: [
          {required: true, message: '请输入地址', trigger: 'blur'},
          {max: 40, message: '长度最多40个字符'}
        ],
        tel2: [
          {required: true, validator: 'regexp', pattern: /^[0-9/-]+$/, message: '仅支持"数字"、"/"、"-"', trigger: 'blur'}
        ],
        email2: [
          {type: 'email', required: true, message: '请输入邮箱', trigger: 'blur'}
        ],
        deadline: [
          {
            required: true,
            validator: 'regexp',
            pattern: /^[1-9]([0-9]*)$/,
            message: '请输入正整数',
            trigger: 'change,blur'
          }
        ],
        tel3: [
          {
            required: true,
            validator: 'regexp',
            pattern: /^[0-9/-]+$/,
            message: '仅支持"数字"、"/"、"-"',
            trigger: 'change,blur'
          }
        ],
        email3: [
          {type: 'email', required: true, message: '请输入邮箱', trigger: 'blur'}
        ],
        bank: [
          {required: true, type: 'number', message: '请选择银行', trigger: 'change'}
        ],
        bankName: [
          {required: true, message: '请输入开户行名称', trigger: 'blur'}
        ],
        openName: [
          {required: true, message: '请输入开户名', trigger: 'blur'}
        ],
        serial: [
          {type: 'integer', message: '请输入整数', trigger: 'change,blur'}
        ],
        account: [
          {
            required: true,
            validator: 'regexp',
            pattern: /^\d+$/,
            message: '请输入银行账号',
            trigger: 'change,blur'
          }
        ],
        email4: [
          {validator: validatePass, trigger: 'blur,change'}
        ],
        num1: [
          {required: true, message: '请输入数字', trigger: 'blur'}
        ],
        num2: [
          {required: true, message: '请输入数字', trigger: 'blur'}
        ],
        day1: [
          {required: true, message: '请输入数字', trigger: 'blur'}
        ],
        day2: [
          {required: true, message: '请输入数字', trigger: 'blur'}
        ],
        checkDay: [{
          value: ''
        }]
      }
    }
  },
  created () {
    this.getBanklist()
    this.getcitylist()
    this.getInfo()
        // this.getList()
  },
  computed: {},
  methods: {
    getInfo () {
      if (this.$route.query.create) {
        let params = {}
        params.vendor_id = this.vendorId
        getCityWarehouse(params).then((res) => {
          if (res.data.error_no === 0) {
            let obj = res.data.result
            this.form.name = obj.vendor_name
            this.form.name2 = obj.vendor_contacter
            this.form.desc2 = obj.vendor_address
            this.form.tel2 = obj.vendor_mobile.toString()
            this.form.email2 = obj.vendor_email
            this.form.radio1 = obj.vendor_scale
            this.form.capital = obj.vendor_register_money
            let date = new Date()
            date.setFullYear(obj.vendor_register_year, 0, 1)
            this.form.date1 = date
            this.form.radio2 = obj.vendor_level
            this.email4 = obj.vendor_buyer_uaccount
            this.form.radio3 = obj.vendor_nature
            this.form.radio4 = obj.vendor_tax_level
            this.form.calculation = obj.vendor_settlement_cycle
            this.form.deadline = obj.vendor_settlement_keep_days
            this.form.name3 = obj.vendor_settlement_name
            this.form.tel3 = obj.vendor_settlement_mobile
            this.form.email3 = obj.vendor_settlement_email
            this.form.bank = obj.vendor_settlement_bank
            this.form.num = obj.vendor_settlement_bank_no
            this.form.bankName = obj.vendor_settlement_bank_start
            this.form.openName = obj.vendor_settlement_bank_holder
            this.form.account = obj.vendor_settlement_bank_account
                        // 供货商账号绑定
            this.buyer_account = obj.vendor_buyer_account
            this.buyer_uname = obj.vendor_buyer_uname

            this.cityArr = obj.vendor_cities
            this.selectCity = []
            this.cityArr.concat().forEach((item) => {
                            // return item.city_id;
              this.selectCity.push(item.city_id)
                            // console.log(item.city_id);
            })
            this.cityInfo()
          }
        })
      }
    },
    handleClick (tab, event) {
      console.log(tab, event)
    },
        // 表单提交
    onSubmit (formName) {
            // console.log(formName);
      this.$refs[formName].validate((valid) => {
        if (valid) {
          if (this.checkCity()) {
            this.createVendor()
          } else {
            this.$message({message: '采购金额最多保留两位小数，在途天数为整数', type: 'error'})
          }
        } else {
          this.$message({message: '输入信息有误', type: 'error'})
          return false
        }
      })
    },
        // 获取银行列表
    getBanklist () {
      getBanklist().then((res) => {
                // 接口请求成功，且返回数据不为空
        if (res.data.error_no === 0 && res.data.result.bank_list && res.data.result.bank_list.length !== 0) {
          this.form.bankList = res.data.result.bank_list
        }
      })
    },
        // 获取城市列表
    getcitylist () {
      getcitylist().then((res) => {
        if (res.data.error_no === 0 && res.data.result.city_list && res.data.result.city_list.length !== 0) {
          this.befoeCityList = res.data.result.city_list
          this.cityIdList = this.befoeCityList.map((item) => {
            return item.city_id
          })
          this.cityInfo()
        }
                // this.pageData.total = res.data.result.total
      })
    },
        // 表单提交函数
    createVendor () {
            // console.log(this.form.date1.length);
      let cityArr = []
      this.cityList.map((item, index, arr) => {
        if (this.checkout[index]) {
          cityArr.push(item)
        }
      })
      let params = {
        vendor_name: this.form.name,
        vendor_contacter: this.form.name2,
        vendor_address: this.form.desc2,
        vendor_mobile: this.form.tel2,
        vendor_email: this.form.email2,
        vendor_scale: this.form.radio1,
        vendor_register_money: this.form.capital,
        vendor_register_year: this.form.date1.length === 4 ? this.form.date1 : new Date(this.form.date1).getFullYear(),
        vendor_level: this.form.radio2,
        vendor_buyer_uname: this.email4,
        vendor_nature: this.form.radio3,
        vendor_tax_level: this.form.radio4,
        vendor_settlement_cycle: this.form.calculation,
        vendor_settlement_keep_days: this.form.deadline,
        vendor_settlement_name: this.form.name3,
        vendor_settlement_mobile: this.form.tel3,
        vendor_settlement_email: this.form.email3,
        vendor_settlement_bank: this.form.bank,
        vendor_settlement_bank_no: this.form.num,
        vendor_settlement_bank_start: this.form.bankName,
        vendor_settlement_bank_holder: this.form.openName,
        vendor_settlement_bank_account: this.form.account,
        vendor_settlement_cities: JSON.stringify(cityArr)
      }
      if (this.$route.query.create) {
        params.vendor_id = this.vendorId
      }
      createVendor(qs.stringify(params)).then((res) => {
        if (res.data.error_no === 0) {
          this.$message({message: '保存成功', type: 'success'})
          this.$router.push({path: '/getvendorlist'})
        } else {
          this.$message({message: res.data.error_msg, type: 'error'})
        }
      })
    },
    changeCity (i) {
      console.log(i, this.checkout[i])
    },
        // 城市信息
    cityInfo () {
      if (this.cityIdList && this.selectCity) {
        this.checkout = this.cityIdList.map((item) => {
          if (this.selectCity.indexOf(item) !== -1) {
            return true
          }
        })
        this.cityList = this.befoeCityList
        // console.log(this.cityList)
        this.cityIdList.forEach((item, index) => {
          let num = this.selectCity.indexOf(item)
          if (num !== -1) {
            this.cityList[index].city_buy_min_limit = this.cityArr[num].city_buy_min_limit
            this.cityList[index].city_delivery_max_days = this.cityArr[num].city_delivery_max_days
          } else {
            this.cityList[index].city_buy_min_limit = ''
            this.cityList[index].city_delivery_max_days = ''
          }
        })
      } else {
        this.cityList = this.befoeCityList
      }
    },
        // 可选城市验证
    checkCity () {
      let flag = true
      if (this.checkout.indexOf(true) === -1) {
        flag = false
      }
      this.cityList.forEach((item, index) => {
        if (this.checkout[index]) {
          if (item.city_delivery_max_days === undefined) {
            item.city_delivery_max_days = ''
          }
          if (item.city_buy_min_limit === undefined) {
            item.city_buy_min_limit = ''
          }
          if (!/(^$)|(^\d+$)/.test(item.city_delivery_max_days) || !/(^$)|(^\d+(\.\d{1,2})?$)/.test(item.city_buy_min_limit)) {
            flag = false
          }
        }
      })
      return flag
    },
    querySearch (queryString, cb) {
      if (queryString) {
        let params = {
          ele_account: queryString,
          page_size: 50
        }
        getEleAccountList(params).then((res) => {
          if (res.data.error_no === 0) {
            if (res.data.result.ele_account_list && res.data.result.ele_account_list.length !== 0) {
              let testList = res.data.result.ele_account_list.map((item) => {
                item.value = item.email.replace(/@ele.me/, '')
                return item
              })
              if (res.data.result.length !== 0 && !!cb) {
                cb(testList)
              }
                            // 判断用户最后的输入书否与查询到的一直
              if (testList.length === 1 && testList[0].value === queryString) {
                this.emailFlag = true
              } else {
                this.emailFlag = false
              }
            }
          }
        })
      }
    },
    handleSelect (item) {
      this.emailFlag = true
    }
  },
  watch: {
    checkout: function () {
      this.checkout.forEach((item, index) => {
        if (!item) {
          this.cityList[index].city_buy_min_limit = ''
          this.cityList[index].city_delivery_max_days = ''
        }
      })
    }
  }
}
</script>

<style scoped lang="scss">
    section {
      background-color: #fff;
      padding-left: 20px;
      .el-menu-item {
        padding: 0;
        margin: 0 20px;
        &:nth-child(1) {
          margin-left: 0;
        }
        a {
          font-size: 14px;
          text-decoration: none;
          font-weight: 500;
