<template>
  <section>
    <el-form :inline='true' :model='filter' class='options' label-position='left'>
      <el-form-item label-width='120px' label='商品编码'>
          <el-input v-model='filter.id' placeholder='请输入内容'></el-input>
      </el-form-item>
      <el-form-item label-width='120px' label='商品自带条形码'>
          <el-input v-model='filter.barId' placeholder='请输入内容'></el-input>
      </el-form-item>
      <el-form-item label-width='120px' label='供货商名称'>
          <el-input v-model='filter.supplyName' placeholder='请输入内容'></el-input>
      </el-form-item>
      <el-form-item label-width='120px' label='商品名称'>
          <el-input v-model='filter.name' placeholder='请输入内容'></el-input>
      </el-form-item>
      <el-form-item class='btn'>
          <el-button type="primary" @click="getList">查询</el-button>
      </el-form-item> 
    </el-form> 
    <template>
      <el-table
        class="chapter3-work-price"
        :data="tableData"
        v-loading="loading"
        style="width: 100%">
        <el-table-column
          prop="sku_id"
          label="商品编码">
        </el-table-column>
        <el-table-column
          prop="sku_name"
          label="商品名称">
        </el-table-column>
        <el-table-column
          prop="default_upc_id"
          label="商品自带条形码">
        </el-table-column>
        <el-table-column
          label="默认含税报价">
          <template slot-scope='scope'>
            <span>￥ {{ (scope.row.quotation_sku_price / 100).toFixed(2) }}</span>
          </template>
        </el-table-column>
        <el-table-column
          prop="upc_unit_text"
          label="包装单位">
        </el-table-column>
        <el-table-column
          prop="upc_unit_num_text"
          label="规格">
        </el-table-column>
        <el-table-column
          prop="upc_unit_price"
          :render-header='upcUnitPriceLabel'
          width="100">
        </el-table-column>
        <el-table-column
          prop="quotation_sku_min_order"
          label="最小起订包装数">
        </el-table-column>
        <el-table-column
          prop="quotation_id"
          label="来源报价单号">
        </el-table-column>
        <el-table-column
          prop="vendor_name"
          label="供货商名称">
        </el-table-column>
        <el-table-column
          prop="quotation_start_time"
          label="开始日期">
        </el-table-column>
        <el-table-column
          prop="quotation_end_time"
          label="结束日期">
        </el-table-column>
      </el-table>
      <div class="page">
        <el-pagination
            @current-change="changePage"
            @size-change="changeSize"
            :current-page="pageData.page"
            :page-size="pageData.count"
            :page-sizes="[10, 20, 50, 100]"
            layout="total, sizes, prev, pager, next, jumper"
            :total="pageData.total">
        </el-pagination>
      </div>
      <div class='popDiv' :style='{position: "fixed", left: mousePosX + "px", top: mousePosY + "px"}' v-if="showPop">
      {{ showText }}
      </div>
    </template>
  </section>
</template>

<script>
import { getQuotationSkuPrice } from '../../api/api'
export default {
  data () {
    return {
      filter: {
        id: '',
        barId: '',
        name: '',
        supplyName: ''
      },
      pageData: {
        page: 1,
        total: 0,
        count: 20,
        page_num: 0
      },
      tableData: [],
      loading: false,
      showPop: false,
      mousePosX: '',
      mousePosY: '',
      showText: ''
    }
  },
  created () {
    this.getList()
  },
  methods: {
    upcUnitPriceLabel (creatElement) {
      return creatElement('span', {
        class: 'real-price-label',
        style: {
          position: 'relative'
        }
      }, [
        creatElement('span', '规格报价'),
        creatElement('i', {
          class: 'el-icon-question',
          on: {
            mouseover: event => {
              this.showPop = true
              let e = event || window.event
              this.mousePosX = e.clientX + 5
              this.mousePosY = e.clientY + 5
              this.showText = '规格报价=默认含税报价*规格'
            },
            mouseout: () => {
              this.showPop = false
            }
          }
        })
      ])
    },
    getList () {
      let params = {
        page_size: this.pageData.count,
        page_num: this.pageData.page,
        sku_id: this.filter.id,
        upc_id: this.filter.barId,
        sku_name: this.filter.name,
        vendor_name: this.filter.supplyName
      }
      this.loading = true
      getQuotationSkuPrice(params).then((res) => {
        this.tableData = res.data.result.list
        this.pageData.total = res.data.result.total
        this.loading = false
      })
    },
    /**
     * @function
     * @description 翻页触发事件
     */
    changePage (val) {
      this.pageData.page = val
      this.getList()
    },
    /**
     * @function
     * @description 改变翻页size触发事件
     */
    changeSize (val) {
      this.pageData.count = val
      this.getList()
    }
  }
}
</script>

<style lang="scss" scoped>
  section{
    .options{
      padding: 20px;
      background: #fff;
      .btn{
        display: block;
        margin-left: 10%;
      }
    }
  }
  .red {
    color: #FF2D4B;
  }
  .popDiv {
    width: 100px;
    z-index: 1;
    background: #fff;
    color: #5a5e66;
    padding: 20px;
    border-radius: 4px;
    border: 1px solid #e6ebf5;
    box-shadow: 0 2px 12px 0 rgba(0,0,0,.1);
  }
  .chapter3-work-price{
    margin-top: 20px;
  }
</style>



