import { getArrivalInfo } from '../../../../api/api'
import util from '../../../../common/js/util'
export default {
  data () {
    return {
      arrival_id: this.$route.query.arrival_id,
      printStatus: this.$route.query.status,
      isSupplier: util.isSupplier(),
      // 到货单基本信息
      arrival_order_info: {},
      // 到货单列表展示
      arrivalTabel: [],
      // 列表表头解释信息展示状态
      showPop: false,
      mousePosX: '',
      mousePosY: '',
      showText: ''
    }
  },
  created () {
    this.getArrivalList()
  },
  methods: {
    totalAmountLabel (creatElement) {
      // 由于ele的table组件不支持直接在表头添加元素，因此采用手动添加元素的方式
      // todo: 暂时没有其他方法代替
      return creatElement('span', {
        class: 'real-price-label',
        style: {
          position: 'relative'
        }
      }, [
        creatElement('span', '入库单品数'),
        creatElement('i', {
          class: 'el-icon-question printNone',
          on: {
            mouseover: event => {
              this.showPop = true
              let e = event || window.event
              this.mousePosX = e.clientX + 5
              this.mousePosY = e.clientY + 5
              this.showText = '入库单品数=入库包装数*规格'
            },
            mouseout: () => {
              this.showPop = false
            }
          }
        })
      ])
    },
    realPriceTaxLabel (creatElement) {
      return creatElement('span', {
        class: 'real-price-tax-label',
        style: {
          position: 'relative'
        }
      }, [
        creatElement('span', '含税金额'),
        creatElement('i', {
          class: 'el-icon-question printNone',
          on: {
            mouseenter: (event) => {
              this.showPop = true
              let e = event || window.event
              this.mousePosX = e.clientX + 5
              this.mousePosY = e.clientY + 5
              this.showText = '含税金额=含税单价*订货单品数'
            },
            mouseleave: () => {
              this.showPop = false
            }
          }
        })
      ])
    },
    realPriceLabel (creatElement) {
      return creatElement('span', {
        class: 'real-price-label',
        style: {
          position: 'relative'
        }

      }, [
        creatElement('span', '不含税金额'),
        creatElement('i', {
          class: 'el-icon-question',
          on: {
            mouseover: event => {
              this.showPop = true
              let e = event || window.event
              this.mousePosX = e.clientX + 5
              this.mousePosY = e.clientY + 5
              this.showText = '不含税金额=不含税单价*入库单品数=[含税金额/(1+税率)]*入库单品数'
            },
            mouseout: () => {
              this.showPop = false
            }
          }
        })
      ])
    },
    getArrivalList () {
      let params = {}
      params.arrival_id = this.arrival_id
      getArrivalInfo(params).then((res) => {
        if (res.data.error_no === 0) {
          this.arrival_order_info = res.data.result && res.data.result.arrival_order_info
          this.arrivalTabel = res.data.result && res.data.result.arrival_order_details
        } else {
          this.$message.error(res.data.error_msg || '获取数据失败，请稍后重试')
        }
      })
    },
    jumpToPurchase (poId) {
      this.$router.push({ path: '/purchaseinfo', query: {po_id: poId} })
    }
  }
}



