<template>
  <div class="list-wrap">
    <el-tabs v-model="activeTab">
      <el-tab-pane
        class="user-management"
        label="用户管理"
        name="userList">
        <user-list></user-list>
      </el-tab-pane>
      <el-tab-pane
        label="角色管理"
        name="roleList">
        <role-list></role-list>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import userList from './userList'
import roleList from './roleList'
import register from './register'

export default {
  data () {
    return {
      // 启用的tab
      activeTab: this.$route.query.tab ? this.$route.query.tab : 'userList'
    }
  },
  components: {
    'user-list': userList,
    'role-list': roleList
  }
}
</script>

<style lang="scss">
  .list-wrap{

    .el-tabs__header{
      padding: 9px 20px 20px;
      background: #fff;
    }

    .el-tab-pane{
      padding: 20px;
      background: #fff;
    }

    .el-pagination{
      text-align: right;
      margin-top: 10px;
    }

    .el-table{
      margin-top: 20px;

      thead th.is-leaf{
        background-color: #f5f7fa;
        border: none;
      }
    }
  }
</style>



