<template>
  <!--数据权限-->
  <div class="data-permission">
    <el-row class="email">
      <el-col :span="2">
        <label>用户邮箱</label>
      </el-col>
      <el-col :span="4">
        <el-input
                disabled
                :value="userInfo.email">
        </el-input>
      </el-col>
    </el-row>
    <el-row class="warehouse">
      <el-col :span="12" class="warehouse-list">
        <div class="warehouse-container">
          <div class="title">仓库选择</div>
          <div class="content">
            <el-checkbox
                    :disabled="disabledCheckAllBtn"
                    class="check-all-warehouse"
                    v-model="checkAllWarehouse">
              全国仓
            </el-checkbox>
            <el-tree
                    ref="tree"
                    v-if="warehouseData.length > 0"
                    :check-strictly="true"
                    :props="defaultProps"
                    :data="warehouseData"
                    node-key="key"
                    :highlight-current="highlightCurrent"
                    show-checkbox
                    :default-expanded-keys="defaultExpandedKeys"
                    @check-change="handleCheckChange">
            </el-tree>
          </div>
        </div>
      </el-col>
      <el-col :span="12" class="added-warehouse">
        <div class="warehouse-container">
          <div class="title">
            已选：{{checkedWarehouse.length}}个
          </div>
          <div class="content">
            <div v-show="checkedWarehouse.length === 0">
              未选择仓库
            </div>
            <ul>
              <li v-for="item in checkedWarehouse">
                <span>
                  {{item.name}}&nbsp;&nbsp;&nbsp;&nbsp;
                  <span v-if="item.text">
                    ({{item.text}})
                  </span>
                </span>
                <el-button
                        type="text"
                        @click="deleteWarehouse(item.id, item.key)">
                  删除
                </el-button>
              </li>
            </ul>
          </div>
        </div>
      </el-col>
    </el-row>
    <el-row class="btn-container">
      <el-button type="primary" @click="save">保存</el-button>
    </el-row>
  </div>
</template>
<style lang="scss" scoped>
.data-permission {
  .email {
    margin-bottom: 22px;
    .el-col-2 {
      width: 80px;
      text-align: right;
      padding-right: 12px;
      color: #5a5e66;
      vertical-align: middle;
      line-height: 40px;
    }
  }
  .warehouse {
    overflow: hidden;
    .warehouse-list {
      padding-right: 20px;
    }

    .warehouse-list, .added-warehouse {
      .warehouse-container {
        border: 1px solid #ccc;
        float: left;
        margin-right: 20px;
        width: 99%;

        .title {
          border-bottom: 1px solid #ccc;
          height: 40px;
          line-height: 40px;
          padding-left: 20px;
        }

        .content {
          padding: 20px;
          height: 300px;
          overflow-y: auto;
        }
      }
    }

    .added-warehouse {
      .content {
        max-height: 500px;
        ul {
          padding: 0;
          margin: 0;
          list-style: none;
          li {
            padding-bottom: 12px;
            .el-button {
              float: right;
              padding: 0;
            }
          }
          li:last-child {
            padding: 0;
          }
        }
      }
    }
    .check-all-warehouse {
      width: 100%;
      background: #fff;
      padding-top: 10px;
    }
  }
  .btn-container {
    text-align: right;
    margin-top: 15px;
  }
}
</style>
<style lang="scss">
  .data-permission {
    .check-all-warehouse {
      .el-checkbox__input {
        padding-left: 10px;
      }
    }
  }
</style>
<script>
  import { getAllWarehouse, modifyUserWarehouses } from '../api/api'
  export default {
    data () {
      return {
        defaultProps: {
          children: 'children',
          label: 'label'
        },
        // 仓库所在城市
        city: '',
        // 已添加的仓库
        addedWarehouse: [],
        // 高亮当前选中节点
        highlightCurrent: true,
        // 所有的仓库
        warehouseData: [],
        // 是否点击全国仓
        checkAllWarehouse: false,
        // 用户信息
        userInfo: {},
        // 全国的城市id
        countryId: '',
        granted: [],
        defaultExpandedKeys: []
      }
    },
    mounted () {
      this.getAllWarehouse()
    },
    computed: {
      checkedWarehouse () {
        let result = []
        this.addedWarehouse.forEach(item => {
          let obj = {
            id: item.id,
            name: item.label,
            key: item.key,
            type: item.type
          }
          if (!item.children) {
            obj.text = item.text
          }
          result.push(obj)
        })
        return result
      },
      disabledCheckAllBtn () {
        return this.warehouseData.length > 0 ? false : true
      }
    },
    methods: {
      /**
       * 获取仓库数据
       */
      getAllWarehouse () {
        let params = {
          user_id: this.$route.query.user_id
        }
        getAllWarehouse(params).then((response) => {
          let res = response && response.data
          if (res && res.error_no === 0) {
            let result = res.result || {}
            let warehouse_ids = result.warehouse_ids || {}
            let allWarehouseData = warehouse_ids.all || {}
            let districts = allWarehouseData.districts || []
            let granted = warehouse_ids.granted.cities.concat(warehouse_ids.granted.warehouses)
            this.userInfo = warehouse_ids.user_info || {}
            this.countryId = allWarehouseData.city_id || ''
            this.handleWarehouseData(districts[0], granted)
            this.$nextTick(() => {
              if (this.granted.length > 0) {
                this.$refs.tree.setCheckedKeys(this.granted)
              }
              if (granted.indexOf(this.countryId) >= 0) {
                this.checkAllWarehouse = true
              }
            })
          } else {
            this.$message.error(res.error_msg)
          }
        })
          .catch((error) => {
            this.$message.error('服务器傲娇了，请刷新重试')
          })
      },
      /**
       * 处理数据为el-tree需要的数据
       * @param {Array} warehouses 仓库数据
       * @param {Object} granted 权限
       */
      handleWarehouseData (warehouses, granted) {
        if (!Array.isArray(warehouses)) {
          return
        }
        // 省
        warehouses.forEach((item, index) => {
          let provinceId = this.warehouseData.push({
            label: item.city_fullname,
            id: item.city_id,
            children: [],
            disabled: false,
            type: 'province',
            key: index.toString()
          })
          if (granted.indexOf(item.city_id) >= 0) {
            this.granted.push(`${index}`)
            this.defaultExpandedKeys.push(`${index}`)
          }
          // 市
          item.district.forEach((city, cityIndex) => {
            let cityId = this.warehouseData[provinceId - 1].children.push({
              label: city.city_fullname,
              id: city.city_id,
              children: [],
              disabled: false,
              type: 'city',
              key: `${index}-${cityIndex}`,
              parentId: city.parent_id
            })
            if (granted.indexOf(city.city_id) >= 0) {
              this.granted.push(`${index}-${cityIndex}`)
              this.defaultExpandedKeys.push(`${index}-${cityIndex}`)
            }
            if (granted.indexOf(item.city_id) >= 0) {
              this.defaultExpandedKeys.push(`${index}-${cityIndex}`)
            }
            // 仓库
            city.warehouse.forEach((warehouse, warehouseIndex) => {
              this.warehouseData[provinceId - 1].children[cityId - 1].children.push({
                label: warehouse.warehouse_name,
                id: warehouse.warehouse_id,
                disabled: false,
                type: 'warehouse',
                key: `${index}-${cityIndex}-${warehouseIndex}`,
                text: `${item.city_fullname} ${city.city_fullname}`,
                parentId: warehouse.city_id
              })
              if (granted.indexOf(warehouse.warehouse_id) >= 0) {
                this.granted.push(`${index}-${cityIndex}-${warehouseIndex}`)
                this.defaultExpandedKeys.push(`${index}`, `${index}-${cityIndex}`)
              }
            })
          })
        })
      },
      /**
       * 节点选中状态发生变化时的回调
       * @param {Array} Data  传递给 data 属性的数组中该节点所对应的对象
       * @param {Boolean} checked 节点是否被选中
       * @param {Boolean} indeterminate 节点的子树中是否有被选中的节点
       */
      handleCheckChange(data, checked) {
        if (data.children) {
          data.children.forEach(item => {
            this.$refs.tree.setChecked(item.key, checked)
            if (item.children) {
              item.children.forEach(warehouse => {
                this.$refs.tree.setChecked(warehouse.key, checked)
              })
            }
          })
        }
        let provinceIndex = parseInt(data.key.split('-')[0])
        if (data.type === 'province') {
          this.warehouseData[provinceIndex].children.forEach((item) => {
            item.disabled = checked
            item.children.forEach(warehouse => {
              warehouse.disabled = checked
            })
          })
        } else if (data.type === 'city') {
          let cityIndex = parseInt(data.key.split('-')[1])
          let warehouses = this.warehouseData[provinceIndex].children[cityIndex].children
          warehouses.forEach(warehouse => {
            warehouse.disabled = checked
          })
        }
        this.getCheckedData()
      },
      /**
       * 获取当前选中的权限的数据
       */
      getCheckedData () {
        if (this.checkAllWarehouse) {
          this.addedWarehouse.splice(0)
          this.addedWarehouse.push({
            id: 'allWarehouse',
            key: 'allWarehouse',
            label: '全国仓',
            type: 'all'
          })
          return
        }
        let allCheckedNodes = this.$refs.tree.getCheckedNodes()
        let allCheckedNodes1 = allCheckedNodes.slice()
        allCheckedNodes.forEach(item => {
          if (item.type === 'province') {
            item.children.forEach(city => {
              for (let i = 0, len = allCheckedNodes1.length; i < len; i++) {
                if (allCheckedNodes1[i].key === city.key) {
                  allCheckedNodes1.splice(i, 1)
                  break
                }
              }
              city.children.forEach(warehouse => {
                for (let i = 0, len = allCheckedNodes1.length; i < len; i++) {
                  if (allCheckedNodes1[i].key === warehouse.key) {
                    allCheckedNodes1.splice(i, 1)
                    break
                  }
                }
              })
            })
          } else if (item.type === 'city') {
            item.children.forEach(warehouse => {
              for (let i = 0, len = allCheckedNodes1.length; i < len; i++) {
                if (allCheckedNodes1[i].key === warehouse.key) {
                  allCheckedNodes1.splice(i, 1)
                  break
                }
              }
            })
          }
        })
        this.addedWarehouse = allCheckedNodes1
      },
      /**
       * 删除某个选中的仓库
       * @param {String} id 仓库或者城市id
       * @param {String} key el-tree的唯一key
       */
      deleteWarehouse (id, key) {
        // 表示全国仓库权限
        if (id === 'allWarehouse') {
          this.checkAllWarehouse = false
          return
        }
        let allCheckedKeys = this.$refs.tree.getCheckedKeys()
        allCheckedKeys.splice(allCheckedKeys.indexOf(key), 1)
        this.$refs.tree.setCheckedKeys(allCheckedKeys)
      },
      /**
       * 保存
       */
      save () {
        let allCheckedNodes = this.addedWarehouse
        let cityIds = []
        let warehouseIds = []
        if (this.checkAllWarehouse) {
          cityIds.push(this.countryId)
        } else {
          allCheckedNodes.forEach(item => {
            if (item.type === 'province' || item.type === 'city') {
              cityIds.push(item.id)
            } else {
              warehouseIds.push(item.id)
            }
          })
        }
          let params = {
            user_id: this.$route.query.user_id,
            warehouse_ids: warehouseIds.join(','),
            city_ids: cityIds.join(',')
          }
          console.log(params, 'params')
          modifyUserWarehouses(params).then(response => {
            if (response.data.error_no === 0) {
              this.$message.success('保存成功')
              this.$router.push({ path: '/privilegeManage'})
            } else {
              this.$message.error(response.data.error_msg)
            }
          })
            .catch((error) => {
              this.$message.error(error)
            })
        }
    },
    watch: {
      // 是否选择全国仓
      checkAllWarehouse (newVal) {
        let checkedNode = []
        // 省
        this.warehouseData.forEach((province, provinceIndex) => {
          province.disabled = newVal
          if (newVal) {
            checkedNode.push(province.key)
          }
          // 市
          let cityData = this.warehouseData[provinceIndex].children
          cityData.forEach((item, index) => {
            item.disabled = newVal
            if (newVal) {
              checkedNode.push(item.key)
            }
            // 仓库
            cityData[index].children.forEach((warehouse) => {
              warehouse.disabled = newVal
              if (newVal) {
                checkedNode.push(warehouse.key)
              }
            })
          })
        })
        this.$refs.tree.setCheckedKeys(checkedNode)
      }
    }
  }
</script>


