<template>
  <!--数据字段-->
  <div class="role-manage">
    <el-button
            type="primary"
            @click="showDialog('create')"
            icon="el-icon-plus">
      新建页面元素
    </el-button>
    <el-table
            :data="tableData"
            style="width: 100%">
      <el-table-column
              prop="page_id"
              label="NO.">
      </el-table-column>
      <el-table-column
              prop="page_name"
              label="元素名称">
      </el-table-column>
      <el-table-column
              prop="page_code"
              label="元素编码">
      </el-table-column>
      <el-table-column
              prop="content"
              label="描述">
      </el-table-column>
      <el-table-column
              label="操作">
        <template slot-scope="scope">
          <el-button
                  @click.native.prevent="showDialog('edit', scope.row)"
                  type="text"
                  size="small">
            编辑
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
            @size-change="handleSizeChange"
            @current-change="changePage"
            :current-page.sync="page"
            :page-sizes="[20, 50, 100, 200]"
            :page-size="pageSize"
            layout="total, sizes, prev, pager, next"
            prev-text="上一页"
            next-text="下一页"
            :total="totalCount">
    </el-pagination>
    <el-dialog
            :title="dialogTitle"
            :visible.sync="show"
            width="30%">
      <el-form
              :model="formParams"
              :rules="rules"
              ref="pageItem"
              label-width="100px">
        <el-form-item
                prop="pageItemName"
                class="is-required"
                label="元素名称">
          <el-input v-model="formParams.pageItemName"></el-input>
        </el-form-item>
        <el-form-item
                prop="pageItemCode"
                class="is-required"
                label="元素编码">
          <el-input v-model="formParams.pageItemCode"></el-input>
        </el-form-item>
        <el-form-item
                label="描述">
          <el-input v-model="formParams.pageItemContent"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="show = false">取 消</el-button>
        <el-button
                v-show="dialogTitle === '新建页面元素'"
                type="primary"
                @click="save('create')">
          确 定
        </el-button>
        <el-button
                v-show="dialogTitle === '编辑页面元素'"
                type="primary"
                @click="save('edit')">
          确 定
        </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
  import { getPageItemList, createPageItem, updatePageItem } from '../../api/api'
  const APP_ID = 1

  export default {
    data () {
      return {
        // table所有数据
        tableData: [],
        // 当前翻页数
        page: 1,
        // 总页数
        totalPage: 3,
        // table总条数
        totalCount: 1,
        // 每页显示多少条
        pageSize: 20,
        show: false,
        dialogTitle: '',
        formParams: {
          // 元素id
          pageItemId: '',
          // 元素名称
          pageItemName: '',
          // 元素编码
          pageItemCode: '',
          // 描述
          pageItemContent: '',
        },
        rules: {
          pageItemName: [{
            required: true, message: '元素名称不能为空'
          }],
          pageItemCode: [{
            required: true, message: '元素编码不能为空'
          }]
        }
      }
    },
    mounted () {
      this.getPageItemList(1, 20)
    },
    methods: {
      /**
       * 获取页面元素
       * @param {Number} page 页码
       * @param {Number} pageSize 每页显示的条数
       */
      getPageItemList (page, pageSize) {
        this.page = page
        this.pageSize = pageSize
        let params = {
          page_size: pageSize,
          page_num: page
        }
        getPageItemList(params).then(res => {
          if (res.data.error_no === 0) {
            this.tableData = res.data.result.page_items
            this.totalCount = parseInt(res.data.result.total_count)
          } else {
            this.$message.error(res.data.error_msg)
          }
        })
          .catch(error => {
            this.$message.error('服务器傲娇了，请刷新重试')
          })
      },
      changePage (val) {
        this.getPageItemList(val, this.pageSize)
      },
      handleSizeChange (val) {
        this.getPageItemList(1, val)
      },
      /**
       * 新建、编辑页面元素
       * @param {String} operation 操作（编辑 or 删除）
       * @param {Object} data
       */
      showDialog (operation, data = {}) {
        this.show = true
        if (operation ===  'create') {
          this.$nextTick(() => {
            this.$refs['pageItem'].resetFields()
            this.formParams.pageItemName = ''
            this.formParams.pageItemCode = ''
            this.formParams.pageItemId = ''
            this.formParams.pageItemContent = ''
          })
          this.dialogTitle = '新建页面元素'
        } else {
          this.formParams.pageItemName = data.page_name || ''
          this.formParams.pageItemCode = data.page_code || ''
          this.formParams.pageItemId = data.page_id || ''
          this.formParams.pageItemContent = data.content || ''
          this.dialogTitle = '编辑页面元素'
        }
      },
      /**
       * 保存
       * @param {String} operation 操作（编辑 or 删除）
       */
      save (operation) {
        this.formParams.pageItemName = this.formParams.pageItemName.trim()
        this.formParams.pageItemCode = this.formParams.pageItemCode.trim()
        this.$refs['pageItem'].validate((valid) => {
          if (valid) {
            let params = {
              page_name: this.formParams.pageItemName,
              content: this.formParams.pageItemContent,
              page_code: this.formParams.pageItemCode
            }
            if (operation === 'create') {
              createPageItem(params).then(res => {
                if (res.data.error_no === 0) {
                  this.$message.success('保存成功')
                  this.getPageItemList(1, this.pageSize)
                  this.show = false
                } else {
                  this.$message.error(res.data.error_msg)
                }
              })
                .catch(error => {
                  this.$message.error('服务器傲娇了，请刷新重试')
                })
            } else {
              params.page_id = this.formParams.pageItemId
              updatePageItem(params).then(res => {
                if (res.data.error_no === 0) {
                  this.$message.success('保存成功')
                  this.getPageItemList(this.page, this.pageSize)
                  this.show = false
                } else {
                  this.$message.error(res.data.error_msg)
                }
              })
                .catch((error) => {
                  this.$message.error('服务器傲娇了，请刷新重试')
                })
            }
          }
        })
      }
    }
  }
</script>



