<template>
  <section>
    <el-form :inline='true' :model='filter' class='options' label-position='left'>
      <el-form-item label-width='80px' label='商品编码'>
          <el-input v-model='filter.id' placeholder='请输入内容' style="width: 194px"></el-input>
      </el-form-item>
      <el-form-item label-width='80px' label='条形码'>
          <el-input v-model='filter.cardId' placeholder='请输入内容'></el-input>
      </el-form-item>
      <el-form-item label-width='80px' label='商品名称'>
          <el-input v-model='filter.proName' placeholder='请输入内容'></el-input>
      </el-form-item>
      <el-form-item label-width='80px' label='状态'>
        <el-select v-model="filter.region" clearable placeholder="请选择活动区域">
          <el-option label="已启用" value="1"></el-option>
          <el-option label="已禁用" value="2"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label-width='80px' label='后台类目' class="category">
        <el-select v-model="filter.value1" placeholder="一级目录" clearable @change='handleSelect'>
          <el-option
            v-for="item in options1"
            :key="item.cat_id"
            :label="item.cat_name"
            :value="item.cat_id">
          </el-option>
        </el-select>
        <el-select v-model="filter.value2" placeholder="二级目录" clearable @change='handleSelectTwo'>
          <el-option
            v-for="item in options2"
            :key="item.cat_id"
            :label="item.cat_name"
            :value="item.cat_id">
          </el-option>
        </el-select>
        <el-select v-model="filter.value3" placeholder="三级目录" clearable @change='handleSelectThree'>
          <el-option
            v-for="item in options3"
            :key="item.cat_id"
            :label="item.cat_name"
            :value="item.cat_id">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item class='btn'>
          <el-button type="primary" @click="onGetList">搜索</el-button>
      </el-form-item>
    </el-form>
    <section class="table-area">
      <router-link v-if="btn.createsku" :to="{ path: '/createproduct'}" class="create" target="_self">新建商品</router-link>
      <template>
        <el-table
          :data="tableData"
          v-loading="loading"
          :fit='true'
          style="width: 100%;">
          <el-table-column
            prop="sku_id"
            label="商品编码">
          </el-table-column>
          <el-table-column
            prop="sku_image"
            label="图片">
            <template slot-scope="scope">
              <img :src="scope.row.sku_image" width='80' height='80'/>
            </template>
          </el-table-column>
          <el-table-column
            prop="sku_name"
            label="商品名称">
          </el-table-column>
          <el-table-column
            prop="sku_category_text"
            :formatter="formatCategoryText"
            label="后台类目">
          </el-table-column>
          <el-table-column
            prop="sku_tax_rate"
            label="税率">
          </el-table-column>
          <el-table-column
            label="条形码">
            <template slot-scope="scope">
              <div class="content-rowspan">
                <div v-for='(item,index) in scope.row.upcs' :key='index'>{{ item.upc_id_autoload === 2 ? item.upc_id : '&nbsp;' }}</div>
              </div>
            </template>
          </el-table-column>
          <el-table-column
            label="包装单位">
            <template slot-scope="scope">
              <div class="content-rowspan">
                <div v-for='(item,index) in scope.row.upcs' :key='index'>{{ item.upc_unit_text }}</div>
              </div>
            </template>
          </el-table-column>
          <el-table-column
            label="规格">
            <template slot-scope="scope">
              <div class="content-rowspan">
                <div v-for='(item,index) in scope.row.upcs' :key='index'>1* {{ item.upc_unit_num }}</div>
              </div>
            </template>
          </el-table-column>
           <el-table-column
            label="是否为默认规格">
            <template slot-scope="scope">
              <div class="content-rowspan">
                <div v-for='(item,index) in scope.row.upcs' :key='index'>{{ item.is_default===1?'是':'否' }}</div>
              </div>
            </template>
          </el-table-column>
          <el-table-column
            prop="is_active"
            label="状态">
            <template slot-scope="scope">
              <span v-if="scope.row.is_active===1">已启用</span>
              <span v-if="scope.row.is_active===2">已禁用</span>
            </template>
          </el-table-column>
          <el-table-column
            label="操作">
            <template slot-scope="scope">
              <el-button v-if="scope.row.is_active===2 && btn.updateskustatus" @click="handleState(scope.row.sku_id, 1)" type="text" size="small">启用</el-button>
              <el-button v-if="scope.row.is_active===1 && btn.updateskustatus" @click="handleState(scope.row.sku_id, 2)" type="text" size="small">禁用</el-button>
              <el-button v-if="btn.getskuinfo" type="text" size="small" @click="goToEdit(scope.row.sku_id)">查看</el-button>
              <router-link :to='{ path: "/updateSupplyInfo", query: {sku_id: scope.row.sku_id}}' target='_blank' class="update-info">维护配货信息</router-link>
            </template>
          </el-table-column>
        </el-table>
        <div class="page">
          <el-pagination
              @current-change="changePage"
              @size-change="changeSize"
              :current-page="pageData.page"
              :page-size="pageData.count"
              :page-sizes="[10, 20, 50, 100]"
              layout="total, sizes, prev, pager, next, jumper"
              :total="pageData.total">
          </el-pagination>
        </div>
      </template>
    </section>
    <el-dialog title="提示" :visible.sync="dialogVisible" width="30%">
      <span>是否确认修改该工单的状态？</span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="confirm">确 定</el-button>
      </span>
    </el-dialog>
  </section>
</template>

<script>
import qs from 'qs'
import { getskulist, getUpcCategory, updateSkuStatus } from '@/api/api'
import util from '../../common/js/util'
export default {
  data () {
    return {
      btn: {
        createsku: util.jdugeShow('vendor-sku-createsku'),
        getskuinfo: util.jdugeShow('vendor-sku-getskuinfo'),
        updateskustatus: util.jdugeShow('vendor-sku-updateskustatus')
      },
      options1: [],
      options2: [],
      options3: [],
      filter: {
        id: '',
        cardId: '',
        proName: '',
        region: '',
        value1: '',
        value2: '',
        value3: ''
      },
      pageData: {
        page: 1,
        total: 0,
        count: 20,
        page_num: 0
      },
      tableData: [],
      loading: false,
      dialogVisible: false,
      cur_upc_id: '',
      cur_is_active: ''

    }
  },
  created () {
    this.getList()
    this.getUpcCategory(1)
  },
  methods: {
    formatCategoryText (row, column) {
      return (row.sku_category_text + '').replace(/,/g, ' > ')
    },
    handleSelectThree (category) {
      // this.filter.value1 = category.value1
      // this.filter.value2 = category.value2
      // this.filter.value3 = category.value3
    },
    onGetList () {
      this.getList()
    },
    // 获取列表
    getList () {
      let params = {
        page_size: this.pageData.count,
        page_num: this.pageData.page,
        sku_id: this.filter.id,
        upc_id: this.filter.cardId,
        sku_name: this.filter.proName,
        sku_is_active: this.filter.region,
        sku_category_1: this.filter.value1,
        sku_category_2: this.filter.value2,
        sku_category_3: this.filter.value3
      }
      this.loading = true
      getskulist(params).then((res) => {
        this.tableData = res.data.result.skus
        this.pageData.total = res.data.result.total
        this.loading = false
      })
    },
    goToEdit (sku_id) {
      this.$router.push({path: `/createproduct?sku_id=${sku_id}`})
    },
    updateInfo (skuId) {
      this.$router.push({path: `/updateSupplyInfo?sku_id=${skuId}`})
    },
    /**
     * @function
     * @description 禁用状态
     */
    handleState (id, is_active) {
      this.cur_upc_id = id
      this.cur_is_active = is_active
      this.dialogVisible = true
    },
    confirm () {
      let params = {
        sku_id: this.cur_upc_id,
        is_active: this.cur_is_active
      }
      updateSkuStatus(qs.stringify(params)).then((res) => {
        let result = res.data
        if (result.error_no === 0) {
          this.$message({
            message: result.error_msg,
            type: 'success'
          })
          this.dialogVisible = false
          this.getList()
        } else {
          this.$message({
            message: result.error_msg,
            type: 'error'
          })
          this.dialogVisible = false
        }
      })
    },
    /**
     * @function
     * @description 翻页触发事件
     */
    changePage (val) {
      this.pageData.page = val
      this.getList()
    },
    /**
     * @function
     * @description 改变翻页size触发事件
     */
    changeSize (val) {
      this.pageData.count = val
      this.getList()
    },
    getUpcCategory (deepth, parentId) {
      let params = {
        depth: deepth,
        parent_id: parentId || ''
      }
      getUpcCategory(params).then((res) => {
        let _options = `options${deepth}`
        if (res.data.result) {
          this[_options] = res.data.result
        }
      })
    },
    handleSelect (val) {
      this.filter.value2 = ''
      this.filter.value3 = ''
      this.getUpcCategory(2, val)
    },
    handleSelectTwo (val) {
      this.filter.value3 = ''
      this.getUpcCategory(3, val)
    }
  }
}
</script>

<style scoped lang="scss">
  section{
    .options{
      padding: 20px 0;
      background: #fff;
      .el-form-item{
        margin-left: 30px;
      }
      .btn{
        display: block;
        margin-left: 9.2%;
      }
    }
    .create{
      margin: 10px 0 20px 30px;
    }
    .table-area{
      padding: 0 20px;
      margin-top: 20px;
      padding-top: 20px;
      background: #fff;
    }
    .createNew{
      margin: 20px;
    }
    .content-rowspan>div{
      padding: 10px 0;
      border-bottom: 1px solid #e6ebf5;
    }
    .content-rowspan>div:last-child{
      border-bottom:none;
    }
    .update-info{
      border: none;
      color: #409EFF;
      background: 0 0;
      padding: 9px 0 15px 0;
      font-size: 12px;
      border-radius: 3px;
      text-decoration: none;
      font-weight: 500;
      display: inline-block;
      line-height: 1;
      &:hover{
        color: #66b1ff
      }
    }
  }

</style>



