<template>
  <section>
    <el-menu 
      text-color="#333"
      :default-active="activeIndex" 
      active-text-color="#39F"
      class="el-menu-demo" mode="horizontal">
      <el-menu-item index="1"><router-link :to="{ path: '/createBasicInfo',query:{create: vendorId, name: this.$route.query.name}}" >供货商基础信息</router-link></el-menu-item>
      <el-menu-item index="2"><router-link :to="{ path: '/createSupplyAccount',query:{create: vendorId, name: this.$route.query.name}}" v-if="!!vendorId">供货商后台帐号</router-link></el-menu-item>
      <el-menu-item index="3"><router-link :to="{ path: '/createPond',query:{create: vendorId, name: this.$route.query.name}}" v-if="!!vendorId">商品池</router-link></el-menu-item>
    </el-menu>
    <el-form :inline='true' :model='filter' class='options'>
        <h2 class="top-tit">添加商品到商品池</h2>
      <el-form-item label-width='70px' label='商品编码'>
        <el-input v-model='filter.supplyID' placeholder='请输入内容'></el-input>
      </el-form-item>
      <el-form-item label-width='60px' label='条形码'>
        <el-input v-model='filter.code' placeholder='请输入内容'></el-input>
      </el-form-item>
      <el-form-item label-width='70px' label='商品名称'>
        <el-input v-model='filter.name' placeholder='从首字开始匹配'></el-input>
      </el-form-item>
      <el-form-item class='btn'>
        <el-button type="primary" @click="getProductSku" style="padding:12px 30px;margin-left: 70px;">预览</el-button>
      </el-form-item> 
    </el-form> 
    <!-- 查询有效商品 -->
    <template v-if="productNum != -1">
        <h2 class="top-tit">预览结果</h2>
      <el-table
        :data="tableData2"
        v-loading="loading"
        style="width: 100%">
        <el-table-column
          prop="sku_id"
          label="商品编码">
        </el-table-column>
        <el-table-column
          prop="sku_name"
          label="商品名称">
        </el-table-column>
        <el-table-column
          prop="display_upc_id"
          label="条形码">
        </el-table-column>
        <el-table-column
          prop="text"
          label="包装单位">
        </el-table-column>
        <el-table-column
          prop="num"
          label="默认规格">
        </el-table-column>
        <el-table-column
        prop="state"
          label="操作"
          >
          <template slot-scope="scope">
            <el-button @click="createVendorUpc(scope.row.sku_id,scope.row.upc_id)" type="text" size="small">加入商品池</el-button>
          </template>
        </el-table-column>
      </el-table>
    </template>
    <template>
      <el-alert
        title="成功添加"
        type="success"
        show-icon v-if="alert == 1">
      </el-alert>

      <el-alert
        title="商品池中已有该商品"
        type="warning"
        show-icon v-if="alert == 2">
      </el-alert>
      <el-alert
        title="没有符合查询条件的商品"
        type="error"
        show-icon v-if="alert == 3">
      </el-alert>
    </template>

<!-- 商品池商品 -->
    <template>
      <h2 class="top-tit">供货商商品池</h2>
    <el-table
      :data="tableData"
      v-loading="loading"
      style="width: 100%">
      <el-table-column
        prop="sku_id"
        label="商品编码"
        width="90">
      </el-table-column>
      <el-table-column
        prop="pic"
        label="图片"
        width="90">
        <template slot-scope="scope">
          <img :src="scope.row.sku_image" width='80' height='80'/>
        </template>
      </el-table-column>
      <el-table-column
        prop="sku_name"
        label="商品名称"
        width="90">
      </el-table-column>
      <el-table-column
        prop="sku_category_text"
        label="后台分类"
        width="90">
      </el-table-column>
      <el-table-column
        prop="sku"
        label="税率"
        width="90">
      </el-table-column>
      <el-table-column
        prop="display_upc_id"
        label="条形码"
        width="90">
      </el-table-column>
      <el-table-column
        prop="upc"
        label="包装单位"
        width="90">
      </el-table-column>
      <el-table-column
        prop="num"
        label="规格"
        width="90">
      </el-table-column>
      <el-table-column
        prop="state"
        label="状态"
        width="90">
      </el-table-column>
      <el-table-column
      prop="state"
        label="操作"
        width="180">
        <template slot-scope="scope">
          <el-button @click="changeStatus(scope.row.upc_id,scope.row.is_active)" type="text" size="small" v-text='btnText[scope.row.is_active -1]'></el-button>
          <el-button type="text" size="small" @click="getUpcsbySkuId(scope.row.sku_id,scope.row.upc_id)">修改默认采购规格</el-button>
        </template>
      </el-table-column>
    </el-table>
    </template>
    <el-dialog title="提示" :visible.sync="dialogVisible" width="30%">
      <span>是否确认修改该商品的状态？</span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="confirm">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 修改规格 -->
    <el-dialog
      title=""
      :visible.sync="dialogSku"
      width="38%">
      <el-table
      :data="tableData3"
      v-loading="loading"
      align="center"
      style="width: 100%">
        <el-table-column
          prop="state"
          label="默认采购规格"
          align="center"
          >
          <template slot-scope="scope">
            <el-radio 
              v-model="radio" 
              :label="scope.row.upc_id" 
              class="upcRadio">
            </el-radio>
          </template>
        </el-table-column>
        <el-table-column
          prop="display_upc_id"
          label="条形码"
          align="center"
          >
        </el-table-column>
        <el-table-column
          prop="upc"
          label="包装单位"
          align="center"
          >
        </el-table-column>
        <el-table-column
          prop="num"
          label="规格"
          align="center"
          >
        </el-table-column>
      </el-table>
      <span slot="footer" class="dialog-footer" v-if="tableData3.length != 1">
        <el-button @click="dialogSku = false">取 消</el-button>
        <el-button type="primary" @click="changeUpc">确 定</el-button>
      </span>
    </el-dialog>
  </section>
</template>
<script>

import qs from 'qs'
import commonData from '@/common/js/commonData'
import { getVendorUpcList, getskulist, updateVendorupcStatus, createVendorUpc, getUpcsbySkuId } from '@/api/api'
export default {
  data () {
    return {
      activeIndex: '3',
      isActive: '',
      productNum: -1,
      dialogVisible: false,
      dialogSku: false,
      vendorId: this.$route.query.create,
      alert: 0,
      radio: 1,
      upcRadio: 1,
      upcCode: [],
      // 改变格式的sku
      changeSku: '',
      uoc: '',
      active: '',
      filter: {
        supplyID: '',
        code: '',
        name: ''
      },
      btnText: [
        '禁用',
        '开启'
      ],
      tableData: [],
      tableData2: [],
      tableData3: [],
      loading: false,
      pageData: {
        page: 1,
        total: 0,
        count: 100,
          // 需要后期修改，去掉分页必选项
        page_num: 0
      }
    }
  },
  created () {
    this.getvendorupclist()
  },
  methods: {
    getvendorupclist () {
      let params = {
        page_size: this.pageData.count,
        // page_num: this.pageData.page,
        vendor_id: this.vendorId
        // is_active: this.isActive
      }
      getVendorUpcList(params).then((res) => {
        // console.log(res.data.result.upcs);
        if (res.data.error_no === 0 && res.data.result.upcs && res.data.result.upcs.length !== 0) {
          this.tableData = this.regulateList(res.data.result.upcs)
        }
        // this.pageData.total = res.data.result.total
      })
    },
    // 查询商品池
    getProductSku () {
      if (!this.filter.name && !this.filter.supplyID && !this.filter.code) {
        this.$message({message: '请输入查询条件', type: 'swarning'})
        this.tableData2 = []
        this.productNum = -1
      } else {
        let params = {
          sku_name: this.filter.name,
          sku_id: this.filter.supplyID,
          upc_id: this.filter.code,
          page_size: 20
        }
        // console.log(params)
        getskulist(params).then((res) => {
          // console.log(res);
          if (res.data.error_no === 0) {
            if (res.data.result.skus.length === 0) {
              this.tableData2 = []
              this.productNum = -1
              this.$message({message: '没有符合查询条件的商品', type: 'error'})
            } else if ((res.data.result.skus.length === 1) && res.data.result.skus[0].is_active === 2) {
              this.tableData2 = []
              this.productNum = -1
              this.$message({message: '该商品已被禁用', type: 'error'})
            } else {
              res.data.result.skus.forEach((item, index) => {
                item.upcs.forEach((li, index) => {
                  if (li.is_default === 1) {
                    item.upc_id = li.upc_id
                    item.display_upc_id = li.display_upc_id
                    item.text = li.upc_unit_text
                    item.num = '1*' + li.upc_unit_num
                  }
                })
              })
              this.tableData2 = this.regulateList(res.data.result.skus)
              this.productNum = 1
            }
          } else {
            this.$message({message: res.data.error_msg, type: 'error'})
          }
        })
      }
    },
    // 确定更改规格
    changeUpc () {
      this.createVendorUpc(this.changeSku, this.radio)
    },
// 向商品池增加商品
    createVendorUpc (sku, upc) {
      // console.log(upc);
      let params = {
        vendor_id: this.vendorId,
        sku_id: sku || this.changeSku,
        upc_id: upc
      }
      createVendorUpc(qs.stringify(params)).then((res) => {
        const status = res.data.error_no
        if (status === 0) {
          if (this.dialogSku) {
            this.$message({message: '修改成功', type: 'success'})
            this.getvendorupclist()
            this.dialogSku = false
          } else {
            this.$message({message: '成功添加', type: 'success'})
            this.getvendorupclist()
          }
        } else {
          this.productNum = -1
          this.$message({message: res.data.error_msg, type: 'swarning'})
        }
      })
    },
    regulateList (arr) {
      arr.forEach((item, index, array) => {
        if (item.sku_category_text_1) {
          item.sku_category_text = item.sku_category_text_1 + '>' + item.sku_category_text_2 + '>' + item.sku_category_text_3
        }
        if (item.sku_category_text) {
          item.sku_category_text = item.sku_category_text.replace(/,/g, ' > ')
        }
        if (item.upc_unit_num) {
          item.num = '1*' + item.upc_unit_num
        }
        // 转换商品包装名称
        // console.log(commonData)
        item.upc = commonData[item.upc_unit]
        if (item.sku_tax_rate) {
          switch (item.sku_tax_rate) {
            case 1 : item.sku = '17%'; break
            case 2 : item.sku = '11%'; break
            case 3 : item.sku = '6%'; break
            case 4 : item.sku = 0; break
          }
        }
        if (item.is_active) {
          item.state = item.is_active === 1 ? '正常采购' : '暂停采购'
        }
      })
      return arr
    },
    // 更改状态
    changeStatus (upc, active) {
      this.upc = upc
      this.active = active
      this.dialogVisible = true
    },
// 更改状态
    confirm () {
      let params = {
        vendor_id: this.vendorId,
        upc_id: this.upc,
        is_active: this.active === 1 ? 2 : 1
      }
      // console.log(params);
      updateVendorupcStatus(qs.stringify(params)).then((res) => {
        if (res.data.error_no === 0) {
          this.$message({message: '修改成功', type: 'success'})
          this.getvendorupclist()
        } else {
          this.$message({message: res.data.error_msg, type: 'swarning'})
        }
        this.dialogVisible = false
      })
    },
    // 查询规格
    getUpcsbySkuId (id, td) {
      this.changeSku = id
      this.radio = td
      let params = {
        sku_id: id
      }
      getUpcsbySkuId(params).then((res) => {
        if (res.data.error_no === 0) {
          // 判断是否查询到规格列表是否为0
          if (Array.isArray(res.data.result)) {
            this.tableData3 = this.regulateList(res.data.result)
            this.upcCode = this.tableData3
            this.dialogSku = true
          } else {
            this.$message({message: '没有信息', type: 'swarning'})
          }
        } else {
          this.$message({message: res.data.error_msg, type: 'swarning'})
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
  section{
    background-color: #fff;
    padding-left: 20px;
    .el-menu-item{
      padding: 0;
      margin: 0 20px;
      &:nth-child(1){
        margin-left: 0;
      }
      a{
        font-size: 14px;
        text-decoration: none;
        font-weight: 500;
        width: 100%;
        height: 100%;
        display: block;
      }
    }
    .top-tit{
      padding-left: 10px;
      font-size: 16px;
      color: #999;
    }
    .el-form .top-tit{
      padding-left: 0;
    }
    .options{
      padding-top: 20px;
      .btn{
        display: block;
        margin-left: 10%;
      }
    }
    .createNew{
      margin-bottom: 20px;
    }
    .el-pagination{
      text-align: right;
      padding: 16px 7px;
    }
    .el-form{
      padding-left: 10px;
    }
    .el-form-item{
      padding-left: 10px;
      margin-right: 10px;
    }
    .upcRadio{
      color: #fff;
    }
  }
</style>



