<template>
  <!--数据字段-->
  <div class="role-manage">
    <el-button
            type="primary"
            @click.native.prevent="showDialog('create')"
            icon="el-icon-plus">
      新建资源
    </el-button>
    <el-table
            :data="tableData"
            style="width: 100%">
      <el-table-column
              prop="data_id"
              label="NO.">
      </el-table-column>
      <el-table-column
              prop="data_name"
              label="字段名称">
      </el-table-column>
      <el-table-column
              prop="data_code"
              label="字段编码">
      </el-table-column>
      <el-table-column
              prop="router"
              label="路由">
      </el-table-column>
      <el-table-column
              prop="content"
              label="描述">
      </el-table-column>
      <el-table-column
              label="操作">
        <template slot-scope="scope">
          <el-button
                  @click.native.prevent="showDialog('edit', scope.row)"
                  type="text"
                  size="small">
            编辑
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
            @size-change="handleSizeChange"
            @current-change="changePage"
            :current-page.sync="page"
            :page-sizes="[20, 50, 100, 200]"
            :page-size="pageSize"
            layout="total, sizes, prev, pager, next"
            prev-text="上一页"
            next-text="下一页"
            :total="totalCount">
    </el-pagination>
    <el-dialog
            :title="dialogTitle"
            :visible.sync="show"
            width="30%">
      <el-form
              :rules="rules"
              ref="dataField"
              :model="formParams"
              label-width="100px">
        <el-form-item
                prop="dataFieldName"
                class="is-required"
                label="字段名称">
          <el-input v-model="formParams.dataFieldName"></el-input>
        </el-form-item>
        <el-form-item
                prop="dataFieldCode"
                class="is-required"
                label="字段编码">
          <el-input v-model="formParams.dataFieldCode"></el-input>
        </el-form-item>
        <el-form-item
                prop="route"
                class="is-required"
                label="路由">
          <el-input v-model="formParams.route"></el-input>
        </el-form-item>
        <el-form-item label="描述">
          <el-input v-model="formParams.dataFieldContent"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="show = false">取 消</el-button>
        <el-button
                v-show="dialogTitle === '新建数据字段'"
                type="primary"
                @click="save('create')">
          确 定
        </el-button>
        <el-button
                v-show="dialogTitle === '编辑数据字段'"
                type="primary"
                @click="save('edit')">
          确 定
        </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
  import { getDataFieldList, createDataField, updateDataField } from '../../api/api'

  export default {
    data () {
      return {
        // table所有数据
        tableData: [],
        // 当前翻页数
        page: 1,
        // 总页数
        totalPage: 3,
        // table总条数
        totalCount: 1,
        // 每页显示条数
        pageSize: 20,
        show: false,
        dialogTitle: '',
        formParams: {
          // 字段id
          dataFieldId: '',
          // 字段名称
          dataFieldName: '',
          // 字段编码
          dataFieldCode: '',
          // 描述
          dataFieldContent: '',
          // 路由
          route: ''
        },
        rules: {
          dataFieldName: [
            { required: true, message: '字段名称不能为空' }
          ],
          // 字段编码
          dataFieldCode: [
            { required: true, message: '字段编码不能为空' }
          ],
          route: [
            { required: true, message: '路由不能为空' }
          ]
        }
      }
    },
    mounted () {
      this.getDataFieldList(1, 20)
    },
    methods: {
      /**
       * 获取权限列表
       * @param {Number} page 页码
       * @param {Number} pageSize 每页显示条数
       */
      getDataFieldList (page, pageSize) {
        this.page = page
        this.pageSize = pageSize
        let params = {
          page_size: pageSize,
          page_num: page
        }
        getDataFieldList(params).then(res => {
          if (res.data.error_no === 0) {
            this.tableData = res.data.result.data_fields
            this.totalCount = parseInt(res.data.result.total_count)
          } else {
            this.$message.error(res.data.error_msg)
          }
        })
          .catch(error => {
            this.$message.error('服务器傲娇了，请刷新重试')
          })
      },
      changePage (val) {
        this.getDataFieldList(val, this.pageSize)
      },
      handleSizeChange (val) {
        this.getDataFieldList(1, val)
      },
      /**
       * 新建、编辑数据字段
       * @param {String} operation 操作（编辑 or 删除）
       * @param {Object} data
       */
      showDialog (operation, data = {}) {
        this.show = true
        if (operation ===  'create') {
          this.$nextTick(() => {
            this.$refs['dataField'].resetFields()
            this.formParams.dataFieldName = ''
            this.formParams.dataFieldContent = ''
            this.formParams.dataFieldId = ''
            this.formParams.dataFieldCode = ''
            this.formParams.route = ''
          })
          this.dialogTitle = '新建数据字段'
        } else {
          this.formParams.dataFieldName = data.data_name || ''
          this.formParams.dataFieldContent = data.content || ''
          this.formParams.dataFieldId = data.data_id || ''
          this.formParams.dataFieldCode = data.data_code || ''
          this.formParams.route = data.router || ''
          this.dialogTitle = '编辑数据字段'
        }
      },
      /**
       * 保存
       * @param {String} operation 操作（编辑 or 删除）
       */
      save (operation) {
        this.formParams.dataFieldName = this.formParams.dataFieldName.trim()
        this.formParams.dataFieldCode = this.formParams.dataFieldCode.trim()
        this.$refs['dataField'].validate((valid) => {
          if (valid) {
            let params = {
              data_name: this.formParams.dataFieldName,
              content: this.formParams.dataFieldContent,
              data_code: this.formParams.dataFieldCode,
              router: this.formParams.route
            }
            if (operation === 'create') {
              createDataField(params).then(res => {
                if (res.data.error_no === 0) {
                  this.$message.success('保存成功')
                  this.getDataFieldList(1, this.pageSize)
                  this.show = false
                } else {
                  this.$message.error(res.data.error_msg)
                }
              })
                .catch(error => {
                  this.$message.error('服务器傲娇了，请刷新重试')
                })
            } else {
              params.data_id = this.formParams.dataFieldId
              updateDataField(params).then(res => {
                if (res.data.error_no === 0) {
                  this.$message.success('保存成功')
                  this.getDataFieldList(this.page, this.pageSize)
                  this.show = false
                } else {
                  this.$message.error(res.data.error_msg)
                }
              })
                .catch((error) => {
                  this.$message.error('服务器傲娇了，请刷新重试')
                })
            }
          }
        })
      }
    }
  }
</script>



