<template>
  <div class="register">
    <el-form ref="form" :model="formParams" :rules="rules" label-width="80px">
      <el-form-item
        label="用户名"
        prop="userName"
        class="is-required">
        <el-input
          placeholder="请输入6位至20位字母、数字或字符组合"
          v-model="formParams.userName">
        </el-input>
      </el-form-item>
      <!--<el-form-item-->
        <!--label="姓名"-->
        <!--prop="name"-->
        <!--class="is-required">-->
        <!--<el-input-->
          <!--placeholder="请输入姓名"-->
          <!--v-model="formParams.name">-->
        <!--</el-input>-->
      <!--</el-form-item>-->
      <el-form-item
        label="手机号"
        prop="phoneNumber"
        class="is-required">
        <el-input
          placeholder="请输入11位手机号"
          v-model="formParams.phoneNumber">
        </el-input>
      </el-form-item>
      <el-form-item>
        <el-button
          @click.native="onSubmit"
          type="primary">
          注册
        </el-button>
        <span class="tips">注册成功后，登陆密码以短信形式下发至手机号中</span>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  import { register } from '../api/api'

  export default {
    data () {
      const validatePhoneNumber = (rule, value, callback) => {
        value = value.trim()
        if (!value) {
          return callback(new Error('请输入手机号码'))
        }
        let pattern = /^(0|86|17951)?(13[0-9]|15[0-9]|17[0-9]|18[0-9]|14[57])[0-9]{8}$/
        if (!pattern.test(value)) {
          callback(new Error('请输入正确的手机号'))
        } else {
          callback()
        }
      }
      return {
        formParams: {
          // 用户名
          userName: '',
          // 姓名
          // name: '',
          phoneNumber: ''
        },
        rules: {
          userName: [
            {required: true, message: '请输入用户名', trigger: 'blur'}
          ],
          phoneNumber: [
            { validator: validatePhoneNumber, trigger: 'blur' }
          ]
          // name: [
          //   {required: true, message: '请输入姓名', trigger: 'blur'}
          // ]
        }
      }
    },
    created () {
    },
    methods: {
      onSubmit () {
        this.formParams.userName = this.formParams.userName.trim()
        this.$refs['form'].validate(valid => {
          if (valid) {
            let params = {
              phone: this.formParams.phoneNumber,
              user_name: this.formParams.userName
              // nickname: this.formParams.name
            }
            register(params).then(response => {
              let res = response.data
              if (res && res.error_no === 0) {
                this.$message.success('注册成功')
                this.$refs['form'].resetFields()
              } else {
                this.$message.error(res.error_msg)
              }
            }).catch(error => {
              this.$message.error('服务器傲娇了，请刷新重试')
            })
          }
        })
      }
    }
  }
</script>

<style lang="scss">
  .register{

    .el-form{
      background-color: #fff;
      padding: 20px;

      .el-form-item__content{
        width: auto;

        .el-input{
          width: 380px;
          margin-right: 15px;
        }

        .permission-has{
          color: #5a5e66;
          margin: 0;
        }
      }
      .tips {
        padding-left: 10px;
      }
    }
  }
</style>



