import { getPurchaseOrderInfo, wfauditpass, wfauditreject } from '../../../../api/api'
import util from '../../../../common/js/util'
export default {
  data () {
    return {
      btn: {
        orderpurchasecheck: util.jdugeShow('order-purchase-check'),
        orderpurchasecheck2: util.jdugeShow('order-purchase-check2')
      },
      po_id: this.$route.query.po_id,
      status: this.$route.query.status,
      isSupplier: util.isSupplier(),
      // 审核通过的状态址
      passStatus: [50, 60, 94, 95],
      purchase_info: {
        po_status: '',
        inbound_id: '',
        arrival_id: '',
        vendor_id: '',
        vendor_name: '',
        po_contact_id: '',
        po_contact_name: '',
        po_contact_phone: '',
        wf_finish_time: '',
        warehouse_id: '',
        warehouse_name: '',
        warehouse_address: '',
        warehouse_contact_id: '',
        warehouse_contact_name: '',
        warehouse_contact_phone: '',
        total_price_tax: '',
        total_price: '',
        expect_time: '',
        appoint_time: '',
        real_price: '',
        real_price_tax: '',
        actual_time: '',
        wf_info: ''
      },
      // 采购单列表展示
      purchaseTabel: [],
      rejectionVisible: false,
      passVisible: false,
      rejectionVisible2: false,
      passVisible2: false,
      showPop: false,
      rejectText1: '',
      rejectErr1: false,
      rejectText2: '',
      rejectErr2: false,
      mousePosX: '',
      mousePosY: '',
      showText: '',
      printStatus: false
    }
  },
  created () {
    if (this.po_id) {
      let params = {}
      params.po_id = this.po_id
      getPurchaseOrderInfo(params).then((res) => {
        if (res.data.error_no === 0) {
          this.purchase_info = res.data.result
          this.purchase_info.expect_time = util.formatTime(this.purchase_info.expect_time)
          this.purchase_info.appoint_time = util.formatTime(this.purchase_info.appoint_time)
          this.purchase_info.actual_time = util.formatTime(this.purchase_info.actual_time)
          this.purchase_info.wf_finish_time = util.formatTime(this.purchase_info.wf_finish_time)
          this.purchaseTabel = res.data.result.detail
        } else {
          this.$message.error(res.data.error_msg || '获取详情失败')
        }
      })
    }
    if (this.status) {
      this.printStatus = true
    }
  },
  methods: {
    totalAmountLabel (creatElement) {
      return creatElement('span', {
        class: 'real-price-label',
        style: {
          position: 'relative'
        }
      }, [
        creatElement('span', '订货单品数'),
        creatElement('i', {
          class: 'el-icon-question printNone',
          on: {
            mouseover: event => {
              this.showPop = true
              let e = event || window.event
              this.mousePosX = e.clientX + 5
              this.mousePosY = e.clientY + 5
              this.showText = '订货单品数=订货包装数*规格'
            },
            mouseout: () => {
              this.showPop = false
            }
          }
        })
      ])
    },
    realPriceTaxLabel (creatElement) {
      return creatElement('span', {
        class: 'real-price-tax-label',
        style: {
          position: 'relative'
        }
      }, [
        creatElement('span', '含税金额'),
        creatElement('i', {
          class: 'el-icon-question printNone',
          on: {
            mouseenter: (event) => {
              this.showPop = true
              let e = event || window.event
              this.mousePosX = e.clientX + 5
              this.mousePosY = e.clientY + 5
              this.showText = '含税金额=含税单价*订货单品数'
            },
            mouseleave: () => {
              this.showPop = false
            }
          }
        })
      ])
    },
    realPriceLabel (creatElement) {
      return creatElement('span', {
        class: 'real-price-label',
        style: {
          position: 'relative'
        }
      }, [
        creatElement('span', '不含税金额'),
        creatElement('i', {
          class: 'el-icon-question',
          on: {
            mouseover: event => {
              this.showPop = true
              let e = event || window.event
              this.mousePosX = e.clientX + 5
              this.mousePosY = e.clientY + 5
              this.showText = '不含税金额=不含税单价*入库单品数=[含税金额/(1+税率)]*入库单品数'
            },
            mouseout: () => {
              this.showPop = false
            }
          }
        })
      ])
    },
    passPurchase (level) {
      let params = {}
      params.po_id = this.po_id // 依赖po_id
      params.wf_level = level
      wfauditpass(params).then((res) => {
        if (res.data.error_no === 0) {
          this.$message.success('审核通过')
          this.$router.push({ path: '/purchaselist' })
        } else {
          this.$message.error(res.data.error_msg || '审批失败，请稍后重试')
        }
      })
    },
    doReject (level) {
      if (!this['rejectText' + level]) {
        this['rejectErr' + level] = true
      } else {
        let params = {}
        params.po_id = this.po_id
        params.wf_level = level
        params.audit_comment = this['rejectText' + level]
        wfauditreject(params).then((res) => {
          if (res.data.error_no === 0) {
            this.$message.success('驳回成功')
            this.$router.push({ path: '/purchaselist' })
          } else {
            this.$message.error(res.data.error_msg || '审批失败，请稍后重试')
          }
          this['rejectionVisible' + level] = false
        })
      }
    },
    passPurchase1 () {
      this.passPurchase(1)
    },
    passPurchase2 () {
      this.passPurchase(2)
    },
    doReject1 () {
      this.doReject(1)
    },
    doReject2 () {
      this.doReject(2)
    }
  }
}



