/**
 * 商品条码映射
 * @2018-02-05
 */
import {
  getskuupccitylist,
  getskucityupclist,
  getupcsbyskuid,
  getskuinfobyupcid,
  createorupdateskucitiesupc
} from '../../../../api/api'
export default {
  data () {
    return {
      // 查询条件
      filter: {
        city_ids: [],
        sku_id: '',
        sku_name: '',
        upc_id: '',
        upc_id_is_empty: 0
      },
      // 下拉菜单选项
      cityList: [],
      upcListForSku: [],
      emptyStatus: [
        { label: '全部', value: 0 },
        { label: '是', value: 1 },
        { label: '否', value: 2 }
      ],

      // 校验规则
      rules: {},

      // 列表展示
      list: {
        data: [],
        currentPage: 1,
        currentPageSize: 20,
        total: 0
      },

      // 当前选择的行编辑信息
      editCellId: -1,
      currUpcModel: '',
      // 新增映射弹框
      newMapping: {
        dialogVisible: false,
        selectAll: false,
        form: {
          city_ids: [],
          upc_id: '',
          sku_id: '',
          sku_name: ''
        },
        rules: {
          city_ids: {
            required: true,
            validator (rule, value, callback) {
              if (!value || value.length < 1) {
                callback(new Error('请选择城市'))
                return
              }
              callback()
            }
          },
          upc_id: {
            required: true,
            validator: (rule, value, callback) => {
              if (!value) {
                callback(new Error('请输入条形码'))
                return
              }
              this.getSkuByUpcId(value).then((msg) => {
                if (msg) {
                  callback(new Error(msg || '条形码在彩云不存在'))
                  return
                }
                callback()
              })
            },
            trigger: 'blur'
          }
        }
      },
      // 状态控制
      loading: {
        table: false,
        modify: false,
        searchUpc: false,
        add: false
      }
    }
  },
  created () {
    this.getCitylist()
    this.getUpcMappingList()
  },
  methods: {
    /**
     * 获取商品条码映射的城市
     * @return {Promise}
     */
    getCitylist () {
      return getskuupccitylist().then((res = {}) => {
        if (res.error_no !== 0) {
          this.$message.error(res.error_msg || '获取数据失败，请稍后重试')
          return
        }
        this.cityList = (res.result && res.result.city_list) || []
      })
      .catch((e) => { this.$message.error('获取数据失败，请稍后重试') })
    },
    /**
     * 获取upc列表
     * @return {Promise}
     */
    getUpcMappingList () {
      let list = this.list
      let params = {
        page_num: list.currentPage,
        page_size: list.currentPageSize,
        ...this.filter,
        city_ids: this.filter.city_ids.join(',')
      }
      this.loading.table = true
      return getskucityupclist(params).then((res = {}) => {
        if (res.error_no !== 0) {
          this.$message.error(res.error_msg || '获取数据失败，请稍后重试')
          return
        }
        this.list.data = this.dealMappingList(res.result && res.result.list)
        this.list.total = (res.result && res.result.total) || 0
      })
      .catch((e) => { this.$message.error('获取数据失败，请稍后重试') })
      .then(() => {
        this.loading.table = false
      })
    },
    /**
     * 根据Upcid验证skuid是否存在，并填写表单中
     * @param  {} id upcId
     */
    getSkuByUpcId (id) {
      this.loading.searchUpc = true
      return getskuinfobyupcid({upc_id: id}).then((res = {}) => {
        if (res.error_no !== 0) {
          return res.error_msg
        }
        this.newMapping.form.sku_id = res.result && res.result.sku_id
        this.newMapping.form.sku_name = res.result && res.result.sku_name
      })
      .catch(e => '获取数据失败，请稍后重试')
      .then((msg) => {
        this.loading.searchUpc = false
        if (msg) {
          this.newMapping.form.sku_id = ''
          this.newMapping.form.sku_name = ''
        }
        return msg
      })
    },
    /**
     * 将sku转换为upc形式的数组
     * @param  {Array} list 后台返回的列表
     * @return {Array} 根据upc级别的数组
     */
    dealMappingList (list) {
      list || (list = [])
      let result = []
      let uid = 0
      list.forEach((item, skuIndex) => {
        let upcs = item.upc_list || []
        let rowInfo = {
          rowspan: upcs.length || 1,
          colspan: 1
        }
        upcs.forEach((upc, index) => {
          let upcInfo = {
            ...item,
            ...upc,
            // 每行的uid，用于后续控制显示按钮
            cellUid: ++uid,
            skuIndex,
            upc_list: undefined,
            // 第一行设置合并，其他设置为0
            colInfo: index > 0 ? {
              rowspan: 0,
              colspan: 0
            } : rowInfo
          }
          result.push(upcInfo)
        })
      })
      return result
    },
    /**
     * [search 查询函数]
     */
    search () {
      this.$refs.searchForm.validate((valid) => {
        if (!valid) {
          return
        }
        this.list.currentPage = 1
        this.getUpcMappingList()
      })
    },
    /**
     * 合并单元格函数
     * @param  {Object}
     */
    mergeSpan ({row, column, columnIndex, rowIndex}) {
      // 商品编码和商品名称合并
      if (columnIndex === 0 || columnIndex === 1) {
        return row.colInfo
      }
    },
    /**
     * 设置表格每行的style, 达到stripe的效果
     * @param  {Object}
     */
    stripeRow ({ row }) {
      const stripeStyle = {
        'background-color': '#F0F8FF'
      }
      return (row.skuIndex % 2 === 0) ? {} : stripeStyle
    },
    /**
     * 全选
     * @param  {Boolean} value
     */
    selectAllCity (value) {
      if (value) {
        this.newMapping.form.city_ids = this.cityList.map(item => item.city_name)
      } else {
        this.newMapping.form.city_ids = []
      }
    },
    /**
     * 打开新增映射对话框
     */
    showAddMapping () {
      this.clearNewMapping()
      this.newMapping.dialogVisible = true
    },
    /**
     * 点击行中的修改按钮
     * @param  {} row element中行信息
     */
    btnModify (row) {
      getupcsbyskuid({sku_id: row.sku_id}).then((res = {}) => {
        if (res.error_no !== 0) {
          this.$message.error(res.error_msg || '获取数据失败，请稍后重试')
          return
        }
        this.upcListForSku = res.result || []
        this.currUpcModel = this.upcListForSku.length === 1 ? this.upcListForSku[0].upc_id : ''
        this.editCellId = row.cellUid
      })
      .catch((e) => { this.$message.error('获取数据失败，请稍后重试') })
    },

    /**
     * 修改后确认按钮
     * @param  {} row element中行信息
     */
    btnCommit (row) {
      if (this.loading.modify) return
      if (!this.currUpcModel) {
        this.$message.error('请选择条形码')
        return
      }

      let params = {
        city_ids: row.city_id,
        sku_id: row.sku_id,
        sku_name: row.sku_name,
        upc_id: this.currUpcModel
      }
      this.loading.modify = true
      createorupdateskucitiesupc(params).then((res = {}) => {
        if (res.error_no !== 0) {
          this.$message.error(res.error_msg || '提交失败，请稍后重试')
          return
        }
        this.btnCancel()
        this.getUpcMappingList()
      })
      .catch((e) => { this.$message.error('提交失败，请稍后重试') })
      .then(() => {
        this.loading.modify = false
      })
    },
    /**
     * 表格中取消按钮
     */
    btnCancel () {
      this.editCellId = -1
    },

    delOption () {
    },
    /**
     * 新增映射保存
     */
    addNewMapping () {
      if (this.loading.add) return
      this.$refs.newMapping.validate((valid) => {
        if (!valid) {
          return
        }
        this.loading.add = true
        let form = this.newMapping.form
        // 由于checkbox只能取到label的名字，所以要将名字从数组中查出，返回id
        let cityIds = form.city_ids || []
        let cityArr = this.cityList.filter(item => cityIds.indexOf(item.city_name) > -1)
        let params = {
          city_ids: cityArr.map(item => item.city_id).join(','),
          upc_id: form.upc_id,
          sku_id: form.sku_id,
          sku_name: form.sku_name
        }
        createorupdateskucitiesupc(params).then((res = {}) => {
          if (res.error_no !== 0) {
            this.$message.error(res.error_msg || '提交失败，请稍后重试')
            return
          }

          if (res.result && res.result.city_name) {
            this.$message.success('提交成功, 已自动覆盖城市：' + res.result.city_name)
          }

          this.newMapping.dialogVisible = false
          this.getUpcMappingList()
        })
        .catch((e) => { this.$message.error('提交失败，请稍后重试') })
        .then(() => {
          this.loading.add = false
        })
      })
    },
    clearNewMapping () {
      this.newMapping.selectAll = false
      this.newMapping.form.city_ids = []
      this.newMapping.form.upc_id = ''
      this.newMapping.form.sku_id = ''
      this.newMapping.form.sku_name = ''
    },
    handleCurrentChange (val) {
      this.list.currentPage = val
      this.getUpcMappingList()
    },
    handleSizeChange (val) {
      this.list.currentPageSize = val
      this.getUpcMappingList()
    }
  }
}



