import { confirmPurchaseOrder, getPurchaseOrderInfo } from '../../../../api/api'
import util from '../../../../common/js/util'
export default {
  data () {
    return {
      po_id: this.$route.query.po_id,
      appoint_time: '',
      appoint_time_err: false,
      purchase_info: {},
      // 采购单列表展示
      purchaseTabel: [],
      po_status: {
        10: '新建待提交',
        20: '审核中',
        21: '待一审',
        22: '待二审',
        30: '审核通过',
        40: '审核驳回',
        41: '一审驳回',
        42: '二审驳回',
        50: '供货商确认',
        60: '已入库',
        90: '订单作废',
        91: '人工作废',
        92: '超时作废'
      },
      // 列表表头解释信息展示状态
      showPop: false,
      mousePosX: '',
      mousePosY: '',
      showText: ''
    }
  },
  mounted () {
    if (this.po_id) {
      this.getPurchaseInfo()
    }
  },
  methods: {
    totalAmountLabel (creatElement) {
      // 由于ele的table组件不支持直接在表头添加元素，因此采用手动添加元素的方式
      // todo: 暂时没有其他方法代替
      return creatElement('span', {
        class: 'real-price-label',
        style: {
          position: 'relative'
        }
      }, [
        creatElement('span', '订货单品数'),
        creatElement('i', {
          class: 'el-icon-question printNone',
          on: {
            mouseover: event => {
              this.showPop = true
              let e = event || window.event
              this.mousePosX = e.clientX + 5
              this.mousePosY = e.clientY + 5
              this.showText = '订货单品数=订货包装数*规格'
            },
            mouseout: () => {
              this.showPop = false
            }
          }
        })
      ])
    },
    realPriceTaxLabel (creatElement) {
      return creatElement('span', {
        class: 'real-price-tax-label',
        style: {
          position: 'relative'
        }
      }, [
        creatElement('span', '含税金额'),
        creatElement('i', {
          class: 'el-icon-question printNone',
          on: {
            mouseenter: (event) => {
              this.showPop = true
              let e = event || window.event
              this.mousePosX = e.clientX + 5
              this.mousePosY = e.clientY + 5
              this.showText = '含税金额=含税单价*订货单品数'
            },
            mouseleave: () => {
              this.showPop = false
            }
          }
        })
      ])
    },
    realPriceLabel (creatElement) {
      return creatElement('span', {
        class: 'real-price-label',
        style: {
          position: 'relative'
        }

      }, [
        creatElement('span', '不含税金额'),
        creatElement('i', {
          class: 'el-icon-question',
          on: {
            mouseover: event => {
              this.showPop = true
              let e = event || window.event
              this.mousePosX = e.clientX + 5
              this.mousePosY = e.clientY + 5
              this.showText = '不含税金额=不含税单价*入库单品数=[含税金额/(1+税率)]*入库单品数'
            },
            mouseout: () => {
              this.showPop = false
            }
          }
        })
      ])
    },
    getPurchaseInfo () {
      let params = {}
      params.po_id = this.po_id
      getPurchaseOrderInfo(params).then((res) => {
        if (res.data.error_no === 0) {
          this.purchaseTabel = res.data.result && res.data.result.detail
          this.purchase_info = res.data.result && res.data.result
          this.purchase_info.expect_time = util.timeStampToDate(this.purchase_info.expect_time * 1000)
          this.purchase_info.wf_finish_time = util.timeStampToDate(this.purchase_info.wf_finish_time * 1000)
        } else {
          this.$message.error(res.data.error_msg || '数据获取失败，请稍后重试')
        }
      })
    },
    selectTime () {
      this.appoint_time_err = false
    },
    confirm () {
      if (this.validata()) {
        let params = {}
        params.po_id = this.po_id
        params.appoint_time = util.getTimeStamp(this.appoint_time)
        confirmPurchaseOrder(params).then((res) => {
          if (res.data.error_no === 0) {
            this.$router.push({ path: '/purchaselist' })
          } else {
            this.$message.error(res.data.error_msg || '操作失败，请稍后重试')
          }
        })
      }
    },
    validata () {
      let diffHours = util.getTimeDiff(this.purchase_info.expect_time, this.appoint_time, 'minutes')
      if (!this.appoint_time) {
        this.appoint_time_err = true
      } else if (diffHours < 0 || diffHours > (48 * 3600 * 1000)) {
        this.appoint_time_err = true
      } else {
        return true
      }
    }
  }
}



