// 接口
import {
  createPurchase,
  searchVendor,
  getCityWarehouse,
  getSkuInfoList,
  getPurchaseOrderInfo,
  updatePurchaseOrder,
  getvendorwarehouse
} from '../../../../api/api'
import util from '../../../../common/js/util'
export default {
  data () {
    return {
      po_id: this.$route.query.po_id,
      vendor_name: '',
      old_vendor_name: '',
      vendor_id: '',
      citys: [],
      city_warehouse: [],
      city_id: '',
      city_name: '',
      warehouses: [],
      warehouse_id: '',
      warehouse_name: '',
      warehouse_info: {},
      city_delivery_max_days: '',
      city_buy_min_limit: '',
      expect_time: '',
      old_expect_time: '',
      batchText: '',
      batchErr: false,
      batchErrArr: [],
      batchErrArrText: '',
      purchaseTabel: [],
      po_contact_id: '',
      po_contact_name: '',
      po_contact_phone: '',
      // 统计是否首次渲染编辑页面
      renderCount: 0,
      priceIndex: [],
      specIndex: [],
      priceErrType: {},
      specErrType: {},
      showPop: false,
      mousePosX: '',
      mousePosY: '',
      showText: '',
      saveDisabled: false, // 防止重复保存
      addPurchaseDisabled: false// 防止重复新建
    }
  },
  created () {
    // 编辑页面请求信息
    this.po_id && this.getPurchaseInfo()
  },
  methods: {
    unitPriceTaxLabel (creatElement) {
      return creatElement('span', {}, [
        creatElement('span', {
          style: {
            color: 'red'
          }
        }, '*'), creatElement('span', '本次含税单价')
      ])
    },
    specUnitLabel (creatElement) {
      return creatElement('span', {}, [
        creatElement('span', {
          style: {
            color: 'red'
          }
        }, '*'), creatElement('span', '订货包装数')
      ])
    },
    totalAmountLabel (creatElement) {
      return creatElement('span', {
        class: 'real-price-label',
        style: {
          position: 'relative'
        }
      }, [
        creatElement('span', '订货单品数'),
        creatElement('i', {
          class: 'el-icon-question',
          on: {
            mouseover: event => {
              this.showPop = true
              let e = event || window.event
              this.mousePosX = e.clientX + 5
              this.mousePosY = e.clientY + 5
              this.showText = '订货单品数=订货包装数*规格'
            },
            mouseout: () => {
              this.showPop = false
            }
          }
        })
      ])
    },
    realPriceTaxLabel (creatElement) {
      return creatElement('span', {
        class: 'real-price-tax-label',
        style: {
          position: 'relative'
        }
      }, [
        creatElement('span', '含税金额'),
        creatElement('i', {
          class: 'el-icon-question',
          on: {
            mouseenter: (event) => {
              this.showPop = true
              let e = event || window.event
              this.mousePosX = e.clientX + 5
              this.mousePosY = e.clientY + 5
              this.showText = '含税金额=含税单价*订货单品数'
            },
            mouseleave: () => {
              this.showPop = false
            }
          }
        })
      ])
    },
    realPriceLabel (creatElement) {
      return creatElement('span', {
        class: 'real-price-label',
        style: {
          position: 'relative'
        }

      }, [
        creatElement('span', '不含税金额'),
        creatElement('i', {
          class: 'el-icon-question',
          on: {
            mouseover: event => {
              this.showPop = true
              let e = event || window.event
              this.mousePosX = e.clientX + 5
              this.mousePosY = e.clientY + 5
              this.showText = '不含税金额=不含税单价*入库单品数=[含税金额/(1+税率)]*入库单品数'
            },
            mouseout: () => {
              this.showPop = false
            }
          }
        })
      ])
    },
    countTotal (value) {
      let hasTaxTotal = 0
      let noTaxTotal = 0
      value.data.map((item, index, list) => {
        hasTaxTotal += (item.real_price_tax * 1.00)
        noTaxTotal += (item.real_price * 1.00)
      })
      return ['合计', undefined, undefined, undefined, undefined, undefined, undefined, undefined, '￥ ' + hasTaxTotal.toFixed(2), '￥ ' + noTaxTotal.toFixed(2)]
    },
    getPurchaseInfo () {
      let params = {}
      params.po_id = this.po_id
      getPurchaseOrderInfo(params).then((res) => {
        let data = res.data
        if (data.error_no === 0) {
          let result = data.result
          this.editDataFormat(result)
        } else {
          this.$message.error(res.data.error_msg || '请求数据出错，试试刷新页面')
        }
      })
    },
    editDataFormat (result) {
      // 对获取的采购单详情数据进行处理（包括已购商品列表中单价的处理）
      this.vendor_name = result.vendor_name
      this.old_vendor_name = this.vendor_name
      this.vendor_id = result.vendor_id
      this.city_id = result.city_id
      this.city_name = result.city_name
      this.warehouse_id = result.warehouse_id
      this.warehouse_name = result.warehouse_name
      // 前端展示时间计算使用的是毫秒数，后端录入的是时间戳
      this.expect_time = new Date(result.expect_time * 1000)
      this.old_expect_time = this.expect_time
      // 逻辑：可能出现生效报价单修改过后与原采购单存储数据不同的情况(最小采购包装数，默认报价)，因此需要额外请求一次报价单
      this.warehouse_info = {
        warehouse_address: result.warehouse_address,
        warehouse_contact_id: result.warehouse_contact_id,
        warehouse_contactor_name: result.warehouse_contact_name,
        warehouse_contactor_mobile: result.warehouse_contact_phone
      }
      this.po_contact_id = result.po_contact_id
      this.po_contact_name = result.po_contact_name
      this.po_contact_phone = result.po_contact_phone
      this.editGetSkuInfo(result.detail)
      // 这里的total是用作整表金额统计的（和purchaseTable中的不一样）
      this.total_price = result.total_price
      this.total_price_tax = result.total_price_tax
      this.getCity_warehouse(this.vendor_id)
    },
    editGetSkuInfo (list) {
      let skuArr = []
      let newList = {}
      list.forEach((val, index, arr) => {
        skuArr.push(arr[index].sku_id)
        newList[arr[index].sku_id] = arr[index]
      })
      let params = {}
      params.vendor_id = this.vendor_id
      params.sku_upc_ids = skuArr
      getSkuInfoList(params).then((res) => {
        if (res.data.error_no === 0) {
          // 编辑状态再次校验默认报价和最小订单数
          let result = res.data.result
          result.forEach((val, index, arr) => {
            newList[arr[index].sku_id].default_unit_price_tax = arr[index].default_unit_price_tax
            newList[arr[index].sku_id].quotaion_sku_min_order = arr[index].quotaion_sku_min_order
          })
          let tableArr = []
          for (let i in newList) {
            tableArr.push(newList[i])
          }
          this.tableFormat(tableArr)
        } else {

        }
      })
    },
    tableFormat (data) {
      // 对purchaseTable数据格式进行处理，列表有一些数据需要手动填写
      this.purchaseTabel = data
      this.purchaseTabel.forEach((val, index, arr) => {
        this.$set(arr[index], 'real_unit_price_tax', (arr[index].real_unit_price_tax / 100).toFixed(2))
        // 映射处理, 后端总计金额和某种商品多数量金额相同字段, 前端将后者处理为real_格式，以便区分
        // 后端数据全部处理为'分'
        this.$set(arr[index], 'real_price', (arr[index].total_price / 100).toFixed(2))
        this.$set(arr[index], 'real_price_tax', (arr[index].total_price_tax / 100).toFixed(2))
      })
    },
    querySupplier (queryString, callback) {
      // 供货商sug查询tableFormat, 详见elementui, http://element-cn.eleme.io/2.0/#/zh-CN/
      let params = {}
      params.vendor_name = queryString
      searchVendor(params).then((res) => {
        let venderArr = []
        let result = (Array.isArray(res.data.result) && res.data.result) || []
        result.forEach((val, index, arr) => {
          venderArr[index] = {}
          venderArr[index].value = val.vendor_name
          venderArr[index].vendor_id = val.vendor_id
        })
        callback(venderArr)
      })
    },
    // todo: 优化
    computePrice (row) {
      if (row.total_amount_spec) {
        let amount = Math.round(row.total_amount_spec)
        row.total_amount = parseInt(parseInt(row.spec) * amount)
      }
      if (row.real_unit_price_tax) {
        row.real_unit_price_tax = parseFloat(row.real_unit_price_tax).toFixed(2)
      }
      if (row.total_amount_spec && row.real_unit_price_tax) {
        row.real_price_tax = (row.spec * row.total_amount_spec * row.real_unit_price_tax).toFixed(2)
        let realPrice = Math.round((Math.round((row.real_unit_price_tax / (1 + row.tax / 100)) * 100000) / 100000) * row.spec * row.total_amount_spec * 100) / 100
        row.real_price = realPrice
      }
    },
    selectSupplier (item) {
      if (this.purchaseTabel.length !== 0 || this.city_name !== '' || this.warehouse_name !== '' || this.expect_time !== '') {
        this.$confirm('供货商名称一旦修改，下方的所有信息将被清空，确认此操作吗?', '', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.vendor_id = item.vendor_id
          this.getCity_warehouse(item.vendor_id)
          this.city_name = ''
          this.city_id = ''
          this.warehouse_name = ''
          this.warehouse_id = ''
          this.city_delivery_max_days = ''
          this.expect_time = ''
          this.old_expect_time = ''
          this.purchaseTabel = []
          this.old_vendor_name = this.vendor_name
          this.batchText = ''
        }).catch(() => {
          this.vendor_name = this.old_vendor_name
        })
      } else {
        this.vendor_id = item.vendor_id
        this.old_vendor_name = this.vendor_name
        // 供应商填写完成后查询城市&仓库
        this.getCity_warehouse(item.vendor_id)
      }
    },
    getCity_warehouse (vendorId) {
      let params = {}
      params.vendor_id = vendorId
      params.has_warehouses = 1
      getCityWarehouse(params).then((res) => {
        if (res.data.error_no === 0) {
          let result = res.data.result || {}
          let cityWarehouse = (result && result.vendor_warehouses) || []
          this.city_warehouse = cityWarehouse
          let citys = (result && result.vendor_cities) || []
          this.citys = citys
          this.po_contact_id = result.vendor_buyer_uid
          this.po_contact_name = result.vendor_buyer_uname
          this.po_contact_phone = result.vendor_buyer_mobile
          // todo: 不合理，getCity_warehouse加then
          if (this.po_id) {
            this.cityChange()
          }
        } else {
          this.$message.error('获取城市仓库数据失败')
        }
      })
    },
    cityChange (cityId) {
      let isCityChange = true
      if (cityId === this.city_id) {
        isCityChange = false
      }
      this.citys.forEach((val, index, arr) => {
        if (cityId === val.city_id) {
          this.city_id = cityId
          this.city_name = val.city_name
          this.city_delivery_max_days = val.city_delivery_max_days
          this.city_buy_min_limit = val.city_buy_min_limit
        }
      })
      this.city_warehouse.forEach((val, index, arr) => {
        if (isCityChange) {
          if (this.city_id === val.id) {
            if (this.renderCount) {
              this.warehouse_name = ''
              this.warehouse_id = ''
            }
            this.renderCount = 1
            this.warehouses = val.warehouses
          }
        }
      })
    },
    wareHouseChange (warehouseId) {
      this.warehouses.forEach((val, index, arr) => {
        if (warehouseId === val.id) {
          this.warehouse_id = warehouseId
          this.warehouse_name = val.name
          this.getWareHouseInfo()
        }
      })
    },
    getWareHouseInfo () {
      let params = {}
      params.warehouse_id = this.warehouse_id
      getvendorwarehouse(params).then((res) => {
        this.warehouse_info = res.data.result.warehouse_info
      })
    },
    addPurchase () {
      // 替换掉多余的换行符
      this.batchText = this.batchText.replace(/\n[\n]+/g, '\n')
      let textArr = this.batchText.split('\n')
      let skuUpcArr = []
      let countArr = {}
      let checkFlag = true
      // 查重用，重复sku或者upc存放对象
      let arrCheck = {}
      textArr.forEach((val, index, arr) => {
        // 取出数组单挑数据，减少计算量
        // 间隔可能是空格或者制表符
        let singleStr = arr[index].split(/\s/g)
        skuUpcArr[index] = singleStr[0].toString()
        // arr[a] = [1,2,3];
        if (singleStr[1]) {
          countArr[singleStr[0]] = singleStr[1].toString()
        } else {
          countArr[singleStr[0]] = singleStr[1]
        }
      })

      skuUpcArr.forEach((item, index) => {
        // sku出现的第一个index
        const li = skuUpcArr.indexOf(item)
        if (li !== index) {
          // sku查重处理
          checkFlag = false
          if (arrCheck[item]) {
            arrCheck[item].push(index + 1)
          } else {
            // 重复行数存储
            arrCheck[item] = []
            arrCheck[item].push(li + 1)
            arrCheck[item].push(index + 1)
          }
        }
      })
      // 待报错重复sku存放的数组
      let checkErrArr = []
      if (!checkFlag) {
        // arrCheck中的重复sku
        let repeatIds = Object.keys(arrCheck)
        repeatIds.forEach((item, index) => {
          // 合成查重报错语句
          checkErrArr[index] = '第' + arrCheck[item].toString() + '行'
        })
        let strMsg = checkErrArr.toString() + '商品编码重复'
        this.$message({message: strMsg, type: 'error'})
      } else {
        if (!skuUpcArr[skuUpcArr.length - 1]) {
          skuUpcArr.pop()
        }
        if (!skuUpcArr[0]) {
          skuUpcArr.shift()
        }
        // 暂存已添加的sku,做查重处理
        let purchaseSkuArr = []
        let purchaseUpcArr = []
        this.purchaseTabel.forEach((val, index, arr) => {
          purchaseSkuArr.push((arr[index].sku_id).toString())
          purchaseUpcArr.push((arr[index].upc).toString())
        })
        if (this.validataAddPurchase(skuUpcArr, purchaseSkuArr, purchaseUpcArr)) {
          this.addPurchaseDisabled = true
          let params = {}
          params.vendor_id = this.vendor_id
          params.sku_upc_ids = skuUpcArr
          getSkuInfoList(params).then((res) => {
            if (res.data.error_no === 0) {
              let len = this
              .purchaseTabel.length + res.data.result.length
              if (len > 50) {
                this.$message.error('录入之后商品已超50条，请移除一部分')
              }
              this.purchaseTabel = this.purchaseTabel.concat(res.data.result)
              this.purchaseTabel.forEach((value, index, array) => {
                this.$set(array[index], 'real_unit_price_tax', (array[index].default_unit_price_tax / 100).toFixed(2))
                if (!array[index].total_amount_spec) {
                  this.$set(array[index], 'total_amount_spec', countArr[array[index].sku_id] || countArr[array[index].upc])
                }
                this.$set(array[index], 'total_amount', array[index].spec * (array[index].total_amount_spec || 0))
                this.$set(array[index], 'real_price_tax', (array[index].total_amount * array[index].real_unit_price_tax).toFixed(2))
                let realPrice = Math.round((Math.round(value.real_unit_price_tax / (1 + value.tax / 100) * 100000) / 100000) * value.total_amount * 100) / 100
                this.$set(array[index], 'real_price', realPrice)
              })
            } else {
              this.batchErrArrText = res.data.error_msg || '数据获取失败，请稍后重试'
              this.batchErr = true
            }
            this.addPurchaseDisabled = false
          })
        }
      }
    },
    validataAddPurchase (skuUpcArr, purchaseSkuArr, purchaseUpcArr) {
      let errNum = 0
      this.batchErr = false
      this.batchErrArr = []
      if (skuUpcArr.length > 50) {
        this.$message.error('批量录入数据超过50条')
        return
      }
      skuUpcArr.forEach((val, index, arr) => {
        if (purchaseSkuArr.indexOf(arr[index]) !== -1) {
          this.batchErrArr.push(arr[index])
          errNum++
        }
        if (purchaseUpcArr.indexOf(arr[index]) !== -1) {
          this.batchErrArr.push(arr[index])
          errNum++
        }
      })
      if (errNum !== 0) {
        this.batchErr = true
        this.batchErrArrText = `以下商品已添加过：${this.batchErrArr.join(',')}`
        return
      }
      return true
    },
    deleteRow (index, rows) {
      this.$confirm('此操作将移除该商品, 是否继续?', '', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        rows.splice(index, 1)
      }).catch(() => {
      })
    },
    getFormatTime () {
      let time = new Date(this.expect_time).getTime()
      return time
    },
    savePruchase () {
      let params = {}
      params = this.getCommitData()
      if (this.validata()) {
        this.saveDisabled = true
        if (params.total_price_tax < parseInt(this.city_buy_min_limit * 100)) {
          this.$confirm('订单含税总金额<采购单最低金额，是否继续保存', '', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
            if (!this.po_id) {
              createPurchase(params).then((res) => {
                if (res.data.error_no === 0) {
                  this.$message.success('保存成功')
                  this.$router.push({ path: '/purchaselist' })
                } else {
                  this.$message.error(res.data.error_msg || '保存失败，请稍后重试')
                }
                this.saveDisabled = false
              })
            } else {
              params.po_id = this.po_id
              updatePurchaseOrder(params).then((res) => {
                if (res.data.error_no === 0) {
                  this.$message.success('保存成功')
                  this.$router.push({ path: '/purchaselist' })
                } else {
                  this.$message.error(res.data.error_msg || '保存失败，请稍后重试')
                }
                this.saveDisabled = false
              })
            }
          }).catch(() => {
            this.saveDisabled = false
          })
        } else {
          if (!this.po_id) {
            createPurchase(params).then((res) => {
              if (res.data.error_no === 0) {
                this.$message.success('保存成功')
                this.$router.push({ path: '/purchaselist' })
              } else {
                this.$message.error(res.data.error_msg || '保存失败，请稍后重试')
              }
              this.saveDisabled = false
            })
          } else {
            params.po_id = this.po_id
            updatePurchaseOrder(params).then((res) => {
              if (res.data.error_no === 0) {
                this.$message.success('保存成功')
                this.$router.push({ path: '/purchaselist' })
              } else {
                this.$message.error(res.data.error_msg || '保存失败，请稍后重试')
              }
              this.saveDisabled = false
            })
          }
        }
      }
    },
    validata () {
      if (!this.vendor_id) {
        this.$message.error('请填写供应商')
        return
      }
      if (!this.expect_time) {
        this.$message.error('请填写期望到仓时间')
        return
      } else {
        let now = new Date()
        if (util.getTimeDiff(this.expect_time, now, 'minutes') < 0) {
          this.$message.error('期望到仓时间不能小于当前时间')
          return
        }
      }
      let priceErr = 0
      let amountErr = 0
      if (this.purchaseTabel.length > 50) {
        this.$message.error('商品数量超过50条，请移除一部分商品')
        return
      }
      this.purchaseTabel.forEach((val, index, arr) => {
        if (!val.real_unit_price_tax) {
          this.priceIndex.push(index)
          this.priceErrType[index] = 'none'
          priceErr++
        } else {
          if (arr[index].default_unit_price_tax < parseInt(arr[index].real_unit_price_tax * 100)) {
            this.priceIndex.push(index)
            this.priceErrType[index] = 'errnum'
            priceErr++
          }
        }
        if (!val.total_amount_spec) {
          this.specIndex.push(index)
          this.specErrType[index] = 'none'
          amountErr++
        } else {
          if (arr[index].total_amount_spec < arr[index].quotation_sku_min_order) {
            this.specIndex.push(index)
            this.specErrType[index] = 'errnum'
            amountErr++
          }
          if (parseInt(arr[index].total_amount_spec) !== parseFloat(arr[index].total_amount_spec)) {
            this.specIndex.push(index)
            this.specErrType[index] = 'notInt'
            amountErr++
          }
        }
      })
      if (priceErr) {
        return
      }
      if (amountErr) {
        return
      }
      return true
    },
    compareTime () {
      let expectDate = new Date(this.expect_time)
      let nowDate = new Date()
      let date = expectDate.getTime() - nowDate.getTime()
      let days = Math.floor(date / (24 * 3600 * 1000))
      if (days < this.city_delivery_max_days && this.expect_time) {
        this.$confirm(`留给供应商的送货天数小于在途天数${this.city_delivery_max_days}天，是否依然保存?`, '', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.old_expect_time = this.expect_time
        }).catch(() => {
          this.expect_time = this.old_expect_time
        })
      } else {
        this.old_expect_time = this.expect_time
      }
    },
    getCommitData () {
      let detail = []
      let totalPriceTax = 0
      let totalPrice = 0
      this.purchaseTabel.forEach((value, index, array) => {
        detail[index] = {
          'sku_id': array[index].sku_id,
          'sku_name': array[index].sku_name,
          'upc': array[index].upc,
          'default_unit_price_tax': array[index].default_unit_price_tax,
          'last_unit_price_tax': array[index].last_unit_price_tax || 0,
          'real_unit_price_tax': (array[index].real_unit_price_tax * 100).toFixed(0),
          'total_amount': array[index].total_amount,
          'tax': array[index].tax,
          'unit': array[index].unit,
          'spec_unit': array[index].spec_unit,
          'spec': array[index].spec,
          'total_amount_spec': array[index].total_amount_spec,
          'total_price': (array[index].real_price * 100).toFixed(0),
          'total_price_tax': (array[index].real_price_tax * 100).toFixed(0)
        }
        totalPriceTax += Math.round(parseFloat(value.real_price_tax) * 100)
        totalPrice += Math.round(value.real_price * 100)
      })
      let params = {
        'vendor_id': this.vendor_id,
        'vendor_name': this.vendor_name,
        'city_id': this.city_id,
        'city_name': this.city_name,
        'warehouse_id': this.warehouse_id,
        'warehouse_name': this.warehouse_name,
        'warehouse_address': this.warehouse_info.warehouse_address,
        'warehouse_contact_name': this.warehouse_info.warehouse_contactor_name,
        'warehouse_contact_phone': this.warehouse_info.warehouse_contactor_mobile,
        'total_price_tax': totalPriceTax,
        'total_price': totalPrice,
        'po_contact_id': this.po_contact_id,
        'po_contact_name': this.po_contact_name,
        'po_contact_phone': this.po_contact_phone,
        'expect_time': this.getFormatTime() / 1000,
        'detail': detail
      }
      return params
    }
  }
}



