<template>
<section>
<el-form ref="form" label-width="119px" :inline="true"  :model="form" class="demo-form-inline width-auto"> 
    <h2 class="top-tit">商品信息</h2>
  <p class="product-info">商品编码:<span>{{productSku}}</span></p>
  <p class="product-info">商品名称:<span>{{productNmane}}</span></p>
    <h2 class="top-tit">配货信息</h2>
  <section
  v-for="(item, index) in form.dataArr"
  :key="item.key"
  >
    <el-form-item size="small"  style="width:80px;text-align:left;color:#5a5e66;padding-left:20px;font-weight:600;">{{listName[item.key]}}</el-form-item>
    <el-form-item label="默认配货规格数" size="small"  class="upcList is-required" 
      :prop="'dataArr.' + index + '.radio'"
      :rules="{
        required: true,  validator: 'regexp', pattern: /^\d+$/, message: '请选择', trigger: 'blur,change' 
      }">
      <el-select v-model="item.radio" placeholder="请选择" style="width:100px;">
        <el-option
          v-for="item2 in form.upcList"
          :key="item2"
          :label="'1*'+item2"
          :value="item2">
        </el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="配货价的计算方式" label-width="150px" size="small"  class="tab2 is-required" style="margin-right:25px;"
      :prop="'dataArr.' + index + '.productRadio'"
      :rules="{
        required: true,  validator: 'regexp', pattern: /^\d+$/, message: '请选择', trigger: 'blur,change' 
      }">
      <el-select v-model="item.productRadio" placeholder="请选择" style="width:130px;" @change='handleSelect(index)'>
        <el-option
          v-for="(item3, index) in formulaMode"
          :key="index"
          :label="item3"
          :value="index">
        </el-option>
      </el-select>
    </el-form-item>   
    <el-form-item size="small"  label="1+" label-width="32px" v-if="form.dataArr[index].productRadio ===0" 
      :prop="'dataArr.' + index + '.value'"
      :rules="{
        required: true,  validator: 'regexp', pattern: /(^[1-9]{1}\d*(\.\d{1,2})?$)|(^[0]{1}\.\d{1,2})$/, message: '只支持正数且最多两位小数', trigger: 'blur,change' 
      }"
    >
      <el-input v-model="item.value" style="width:118px;"  class="float-text" placeholder="最多两位小数" ></el-input>
    </el-form-item>
    <el-form-item size="small"  label="¥" label-width="32px" v-if="form.dataArr[index].productRadio ===1" 
      :prop="'dataArr.' + index + '.value'"
      :rules="{
        required: true, validator: 'regexp', pattern: /(^[1-9]{1}\d*(\.\d{1,2})?$)|(^[0]{1}\.\d{1,2})$/, message: '只支持正数且最多两位小数', trigger: 'blur' 
      }"
    >
      <el-input v-model="item.value" style="width:118px;"  class="float-text" placeholder="最多两位小数" ></el-input>
    </el-form-item>
    <el-form-item label="%" label-width="30px"  size="small"  class="item-texm" v-if="form.dataArr[index].productRadio ===0"></el-form-item>
    <br>
    
  </section>
  <el-form-item>
    <el-button type="primary" @click="submitForm('form')">保存</el-button>
  </el-form-item>
</el-form>
</section>
</template>
<script>
import qs from 'qs'
import { updateSkuBusinessForm, getSkuInfo } from '@/api/api'
export default {
  data () {
    return {
      productSku: this.$route.query.sku_id,
      productNmane: '',
      options: '',
      value: '',
      formulaMode: '',
      skuForm: [],
      form: {
        name: '',
        upcList: '',
        float: [],
        dataArr: []
      },
      rules: {
        float: [
          { required: true, validator: 'regexp', pattern: /^\d+(\.\d{1,2})?$/, message: '请输入正确的数字', trigger: 'change,blur' }
        ]
      }
    }
  },
  created () {
    // this.init()
    this.getVendorBySku()
    this.init()
  },
  methods: {
    init () {
      this.formulaMode = [
        '成本价加点',
        '固定金额数',
        '平进平出'
      ]
      this.listName = [
        '',
        '货架：',
        '前置仓：',
        '门店：'
      ]
    },
    getVendorBySku () {
      let params = {
        sku_id: this.$route.query.sku_id
      }
      getSkuInfo(params).then((res) => {
        // console.log(res.data.result.upcs)
        let result = res.data.result
        if (res.data.error_no !== 0) {
          this.$message({message: res.data.error_msg, type: 'error'})
          return
        } else if (!result.sku_business_form || result.sku_business_form.length === 0) {
          this.$message({message: '没有配货信息', type: 'warning'})
          return
        }
        result.sku_business_form.forEach((item, index) => {
          let detailItem = result.sku_business_form_detail[index]
          let obj = {
            key: item
          }
          if (detailItem) {
            // 填充默认金额
            if (detailItem.sku_price_value !== undefined) {
              obj.value = detailItem.sku_price_value
            } else {
              obj.value = ''
            }
            // 填充默认计算方式
            if (detailItem.sku_price_type !== undefined) {
              obj.productRadio = Number(detailItem.sku_price_type) - 1
            } else {
              obj.productRadio = ''
            }
            // 匹配默认规格
            obj.radio = Number(detailItem.default_upc_num) || ''
          } else {
            // 如果没有这个选项，全部填为空
            obj.value = ''
            obj.productRadio = ''
            obj.radio = ''
          }
          this.form.dataArr.push(obj)
        })
        // 规格列表(去重)
        this.form.upcList = []
        result.upcs.forEach((item) => {
          if (this.form.upcList.indexOf(item.upc_unit_num) === -1) {
            this.form.upcList.push(item.upc_unit_num)
          }
        })
        // 商品名称
        this.productNmane = result.sku_name
        // console.log(this.form.dataArr)
      })
    },
    updateSku () {
      let skuForm = new Array(this.form.dataArr.length).fill({})
      this.form.dataArr.forEach((item, index) => {
        let skuItem = {
          type: item.key,
          default_upc_num: item.radio,
          sku_price_type: item.productRadio + 1,
          sku_price_value: item.value
        }
        skuForm[index] = skuItem
      })
      let params = {
        sku_id: this.productSku,
        sku_business_form_detail: JSON.stringify(skuForm)
      }
      // console.log(skuForm)
      updateSkuBusinessForm(qs.stringify(params)).then((res) => {
        if (res.data.error_no === 0) {
          this.$message({message: res.data.error_msg, type: 'success'})
        } else {
          this.$message({message: res.data.error_msg, type: 'error'})
        }
      })
    },
    submitForm (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.updateSku()
        } else {
          this.$message({message: '请填写正确信息', type: 'error'})
          return false
        }
      })
    },
    /**
     * @function
     * @handleSelect 选择计价方式
     */
    handleSelect (index) {
      if (this.form.dataArr[index].productRadio === 2) {
        this.form.dataArr[index].value = ''
      }
    }
  }
}
</script>

<style scoped lang="scss">
section{
    background-color: #fff;
    padding-left: 20px;
    .product-info{
      padding-left: 40px;
      color: #5a5e66;
      font-weight: 600;
      span{
        font-weight: initial;
        margin-left: 30px;
      }
    }
    .item-texm{
      margin-right: 0;
    }
    .float-text{
      width: 90px;
      font-size: 14px;
    }
    .top-tit{
      color: #878d99;
    }
    .el-form{
      overflow: hidden;
    }
}
</style>



